import api from 'Api';
import socketAndHttp from '../../../services/socketAndHttp';
/**
 * Gets a list of members of the specified projectId project and refreshes local data
 */
async function refreshContributorList(context, projectId) {
  await this.dispatch('projectCollection/getProjectById', projectId);
  let response = await api.get(`/projects/${projectId}/contributors`);
  let data = {
    projectId,
    contributors: response.data
  };

  this.commit('projectDetail/refreshContributorList', data);
  return data;
}

/**
 * Replace and update the list of members for the specified projectId project, and refresh the list of members
 * Mainly used for batch update operation
 */
async function putContributorList(context, payload) {
  await socketAndHttp(
    `/projects/${payload.projectId}/contributors`,
    'put',
    payload.contributorList
  );
  let contributors = await this.dispatch(
    'projectDetail/refreshContributorList',
    payload.projectId
  );
  return {
    projectId: payload.projectId,
    contributors
  };
}

/**
 * Add a member to the list of members of the specified projectId project and refresh the list of members
 */
async function inviteOneContributor(context, payload) {
  await api.patch(
    `/projects/${payload.projectId}/contributors/${payload.contributorId}`
  );
  let contributors = await this.dispatch(
    'projectDetail/refreshContributorList',
    payload.projectId
  );
  return contributors;
}

/**
 * Remove contributorId-specified members from the list of project members specified by projectId
 */
async function removeOneContributor(context, payload) {
  await socketAndHttp(
    `/projects/${payload.projectId}/contributors`,
    'put',
    payload.contributorList
  );
  this.commit('projectDetail/removeOneContributor', payload);
  return payload;
}

/**
 * Find members by username (supporting regular matching) and return a list of eligible members
 */
async function searchContributorsByName(context, payload) {
  let data = {
    params: {
      username: payload.username
    }
  };
  let response = await api.get(
    `/projects/${payload.projectId}/contributors/search`,
    data
  );
  return response.data;
}

/**
 * Accurate access to a list of member information for a project specified by projectId through idList
 */
async function getContributorsByIdList(context, payload) {
  let data = {
    params: {
      ids: payload.idList || []
    }
  };
  let response = await api.get(
    `/projects/${payload.projectId}/contributors/search`,
    data
  );
  return response.data;
}

export default {
  refreshContributorList,
  putContributorList,
  inviteOneContributor,
  removeOneContributor,
  searchContributorsByName,
  getContributorsByIdList
};
