import NProgress from 'nprogress';
import api from 'Api';
import { getPbkdf2HashForPassword } from '../../../helpers/helpers';

// Send mail with reset password
function sendResetPasswordEmail(context, email) {
  NProgress.start();
  let data = {
    email
  };

  return new Promise((resolve, rejiect) => {
    api
      .post('/emails/send-for-reset-password', data)
      .then(response => {
        context.commit('sendResetPasswordEmailSuccess');
        resolve(response);
      })
      .catch(error => {
        context.commit('sendResetPasswordEmailFailure', error);
        rejiect(error);
      })
      .finally(() => NProgress.done());
  });
}

// reset password
function resetPasswordWithoutOriginalPassword(context, payload) {
  NProgress.start();
  let data = {
    password: getPbkdf2HashForPassword(payload.newPassword)
  };

  return new Promise((resolve, rejiect) => {
    api
      .post(`/emails/${payload.emailId}/reset-password`, data)
      .then(response => {
        context.commit('resetPasswordWithoutOriginalPasswordSuccess');
        resolve(response);
      })
      .catch(error => {
        context.commit('resetPasswordWithoutOriginalPasswordFailure', error);
        rejiect(error);
      })
      .finally(() => NProgress.done());
  });
}

