<template>
  <div class="user-account-box">
    <div class="user-account">
      <div class="account-avatararea">
        <div class="account-avatar" @click.stop="imgCropperDialogOpen">
          <user-avata-circle :user-avata="userAvatar"></user-avata-circle>
          <div class="avatar-edit">Edit</div>
        </div>
      </div>
      <div class="account-infoform">
        <div class="account-field-box">
          <div class="account-field-content">
            <div class="account-value">
              <div class="input-title">{{ trans.OTOrganization }}</div>
              <input
                v-model="organization"
                class="settings-input"
                name="organization"
                :placeholder="trans.OTOrganizationText"
              />
            </div>
            <div class="account-value">
              <div class="input-title">{{ trans.OTUserName }}</div>
              <input
                v-model="userNamedefault"
                class="settings-input"
                name="userName"
                :placeholder="trans.OTEnterName"
              />
            </div>
            <div class="account-value">
              <div class="input-title">
                <div class="input-text">{{ trans.OTUserEmail }}</div>
                <div class="edit-email" v-if="!userEmail.verified">
                  {{ trans.OTVerificationEmailSent }}
                  <span @click="resend">{{ trans.OTResend }}</span>
                </div>
              </div>
              <div class="input-content-box">
                <input
                  ref="accountEmailInput"
                  v-model="emaildefaultText"
                  class="settings-input"
                  name="email"
                  :placeholder="trans.OTEnterEmail"
                />
                <div v-if="!userEmail.verified" class="unverified">
                  <v-chip>{{ trans.OTUnverified }}</v-chip>
                </div>
                <div v-else class="verified">
                  <v-chip color="green" text-color="white">{{
                    trans.OTVerified
                  }}</v-chip>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="setting-error">
        {{ settingsErrorText }}
      </div>
      <v-btn
        round
        :disabled="disabled"
        color="orange"
        class="settings-button mt-3"
        @click.stop="accountSave"
        v-text="trans.AccountSave"
      ></v-btn>
    </div>

    <v-dialog
      v-model="avatarCropperDialogIsOpen"
      persistent
      content-class="img-cropper-dialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-card-text>
          <v-btn
            slot="activator"
            icon
            large
            class="img-cropper-dialog-close"
            style="font-size: 2rem"
            @click.stop="imgCropperDialogClose"
          >
            <i class="mdi mdi-plus mdi-rotate-45"></i>
          </v-btn>

          <div class="vc-parent">
            <div class="vc-child">
              <div
                style="padding: 40px 20px 80px; position: relative; text-align: center; white-space: nowrap;"
              >
                <div class="setting-avatar-leftarea">
                  <div class="setting-avatar-processarea">
                    <div class="vc-parent">
                      <div class="vc-progress-new" v-show="imgLoadingShow">
                        <v-progress-circular
                          color="orange"
                          indeterminate
                        ></v-progress-circular>
                      </div>
                      <div class="vc-child">
                        <div class="imagecrop-cropwrapper">
                          <croppa
                            v-model="imgCroppa"
                            :width="200"
                            :height="200"
                            :initial-image="userAvatar"
                            placeholder="Click here"
                            placeholder-color="#000"
                            :placeholder-font-size="16"
                            canvas-color="#f3f3f3"
                            :show-remove-button="true"
                            remove-button-color="red"
                            :remove-button-size="40"
                            :show-loading="true"
                            prevent-white-space
                            :loading-size="50"
                            @init="onInit"
                            @new-image-drawn="handleNewImage"
                            @image-remove="handleNewImage"
                            @move="handleNewImage"
                            @zoom="handleNewImage"
                          ></croppa>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div class="setting-avatar-btnbar" v-if="!imgLoadingShow">
                    <v-btn
                      outline
                      round
                      color="indigo1"
                      @click.stop="rotate"
                      v-text="trans.RotateImg"
                    ></v-btn>
                    <v-btn
                      round
                      class="save-img-button"
                      @click.stop="saveImg('user_avatar')"
                      v-text="trans.ConfirmToUpload"
                    ></v-btn>
                  </div>
                </div>
                <div class="setting-avatar-rightarea">
                  <div class="setting-avatar-preview setting-avatar-preview1">
                    <img :src="imgDataUrl" width="100%" />
                  </div>
                  <div class="setting-avatar-preview setting-avatar-preview2">
                    <img :src="imgDataUrl" width="100%" />
                  </div>
                  <div class="setting-avatar-preview setting-avatar-preview3">
                    <img :src="imgDataUrl" width="100%" />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
  import Api from '../../api';
  import { mapGetters } from 'vuex';
  import userAvataCircle from '../../components/userAvataCircle';
  import ossUploadFiles from '../../oss';

  export default {
    components: {
      userAvataCircle
    },
    data() {
      return {
        emaildefaultText: '',
        userNamedefault: '',
        settingsErrorText: '',
        organization: '',
        imgCroppa: {},
        imgDataUrlVersion: 1,
        imgDataUrl: null,
        trans: {
          ErrorUpload: this.$i18n.t('StackEdit.Layout.ErrorUpload'),
          FNCity: this.$i18n.t('Session.Account.FNCity'),
          FNCommpany: this.$i18n.t('Session.Account.FNCommpany'),
          FNSchool: this.$i18n.t('Session.Account.FNSchool'),
          OTCityPlaceholder: this.$i18n.t('Session.Account.OTCityPlaceholder'),
          OTCommpanyPlaceholder: this.$i18n.t(
            'Session.Account.OTCommpanyPlaceholder'
          ),
          OTSchoolPlaceholder: this.$i18n.t(
            'Session.Account.OTSchoolPlaceholder'
          ),
          Email: this.$i18n.t('Session.Account.Email'),
          AccountSave: this.$i18n.t('Session.Account.AccountSave'),
          OTEmailRules: this.$i18n.t('Session.Account.OTEmailRules'),
          OTUserNameRules: this.$i18n.t('Session.Account.OTUserNameRules'),
          RotateImg: this.$i18n.t('Session.Account.RotateImg'),
          ConfirmToUpload: this.$i18n.t('Session.Account.ConfirmToUpload'),
          NoImageToUpload: this.$i18n.t('Session.Account.NoImageToUpload'),
          EmailHaveExist: this.$i18n.t('Session.Account.EmailHaveExist'),
          SaveSuccessfully: this.$i18n.t('Session.Account.SaveSuccessfully'),
          OTVerified: this.$i18n.t('UserSetting.OTVerified'),
          OTUnverified: this.$i18n.t('UserSetting.OTUnverified'),
          OTVerificationEmailSent: this.$i18n.t(
            'UserSetting.OTVerificationEmailSent'
          ),
          OTResend: this.$i18n.t('UserSetting.OTResend'),
          OTUserName: this.$i18n.t('Session.Account.OTUserName'),
          OTUserEmail: this.$i18n.t('Session.Account.OTUserEmail'),
          OTEnterName: this.$i18n.t('Session.Account.OTEnterName'),
          OTEnterEmail: this.$i18n.t('Session.Account.OTEnterEmail'),
          OTOrganization: this.$i18n.t('Session.SignUp.OTOrganization'),
          OTOrganizationText: this.$i18n.t('Session.SignUp.OTOrganizationText')
        },
        imgLoadingShow: false
      };
    },
    created() {
      let v = this.$validator;
      v.attach({
        name: 'userName',
        alias: this.trans.userName,
        getter: () => this.userNamedefault,
        rules: {
          required: true
        }
      });
      v.attach({
        name: 'email',
        alias: this.trans.email,
        getter: () => this.emaildefaultText,
        rules: {
          required: true,
          email: true,
          regex: /^[^\u4e00-\u9fa5]+$/
        }
      });
    },
    computed: {
      ...mapGetters({
        userAvatar: 'session/userAvatar',
        userName: 'session/userName',
        userEmail: 'session/userEmail',
        user: 'session/user',
        avatarCropperDialogIsOpen: 'user/avatarCropperDialogIsOpen'
      }),
      disabled() {
        if (
          this.userNamedefault === this.userName &&
          this.emaildefaultText === this.userEmail.email &&
          this.organization === this.user.profile.organization
        ) {
          return true;
        } else {
          return false;
        }
      }
    },
    mounted() {
      this.emaildefaultText = _.cloneDeep(this.userEmail.email);
      this.userNamedefault = _.cloneDeep(this.userName);
      this.organization = _.cloneDeep(this.user.profile.organization);
    },
    watch: {
      imgDataUrlVersion() {
        setTimeout(() => this.recacultImageDataUrl(), 16);
      },
      userAvatar(newValue, oldValue) {
        this.imgCroppa.refresh();
      }
    },
    methods: {
      resend() {
        let self = this;
        if (!self.disabled) {
          self.$store.commit('settings/showGlobalSnackbar', {
            text: self.$i18n.t('Session.Account.OTNoSmitSend')
          });
          return;
        }
        self.$store
          .dispatch('emails/resendForConfirmVerification')
          .then(res => {
            self.$store.commit('settings/showGlobalSnackbar', {
              text: self.$i18n.t('Session.Account.OTSendEmail')
            });
          });
      },
      async accountSave() {
        let v = this.$validator;
        let validateFailed = !(await v.validate());
        let errors = await v.errors.all();
        if (validateFailed) {
          switch (v.errors.items[0].field) {
            case 'email':
              this.settingsErrorText = this.trans.OTEmailRules;
              break;
            case 'userName':
              this.settingsErrorText = this.trans.OTUserNameRules;
              break;
          }
        } else {
          this.settingsErrorText = '';
          let payload = {};
          let emailVL = this.emaildefaultText === this.userEmail.email;
          let usernameVL = this.userNamedefault === this.userName;
          let organization =
            this.organization === this.user.profile.organization;
          if (!emailVL) {
            payload.email = this.emaildefaultText;
          }
          if (!usernameVL) {
            payload.username = this.userNamedefault;
          }
          if (!organization) {
            payload.organization = this.organization;
          }
          if (!emailVL || !usernameVL || !organization) {
            try {
              await this.$store
                .dispatch('user/updateUserAccount', payload)
            } catch (error) {
              if (error.response.status === 422) {
                this.settingsErrorText = this.trans.EmailHaveExist;
              }
            }
          }
        }
      },
      rotate() {
        this.imgCroppa.rotate();
        this.imgDataUrlVersion += 1;
      },
      onInit() {
        this.imgCroppa.addClipPlugin(function(ctx, x, y, w, h) {
          ctx.beginPath();
          ctx.arc(x + w / 2, y + h / 2, w / 2, 0, 2 * Math.PI, true);
          ctx.closePath();
        });
      },
      imgCropperDialogOpen() {
        this.$store.commit('user/openUserAvatarCropperDialog');
      },
      imgCropperDialogClose() {
        this.$store.commit('user/closeUserAvatarCropperDialog');
      },
      async saveImg(category) {
        if (!this.imgCroppa.hasImage()) {
          this.$store.commit('settings/showGlobalSnackbar', {
            text: this.trans.NoImageToUpload
          });
          return;
        }
        this.imgLoadingShow = true;
        let files = await new Promise((resolve, reject) => {
          this.imgCroppa.generateBlob(blob => {
            if (!blob) {
              return reject();
            }
            let file = [
              new File([blob], 'userAvatar.png', {
                type: blob.type,
                lastModified: Date.now()
              })
            ];
            resolve(file);
          });
        });
        let imagesArry = [];
        try {
          imagesArry = await ossUploadFiles(files, category);
          let avatar = imagesArry[0][0].href;
          let payload = {
            avatar: avatar
          };

          await this.$store
            .dispatch('user/updateUserAccount', payload)
            .then(res => {
              this.imgLoadingShow = false;
              this.$store.commit('settings/showGlobalSnackbar', {
                text: this.trans.SaveSuccessfully
              });
            });
        } catch (error) {
          this.$store.commit('settings/showGlobalSnackbar', {
            text: this.trans.ErrorUpload
          });
        }
      },
      handleNewImage() {
        this.imgDataUrlVersion += 1;
      },
      recacultImageDataUrl() {
        this.imgDataUrl =
          (this.imgCroppa.hasOwnProperty('generateDataUrl') &&
            this.imgCroppa.generateDataUrl()) ||
          '';
      },
      accountEditEmail() {
        this.$refs.accountEmailInput.focus();
      }
    }
  };
</script>
<style lang="scss" scoped>
  ::-webkit-input-placeholder {
    /* WebKit browsers */
    color: #ccc;
  }

  :-moz-placeholder {
    /* Mozilla Firefox 4 to 18 */
    color: #ccc;
  }

  ::-moz-placeholder {
    /* Mozilla Firefox 19+ */
    color: #ccc;
  }

  :-ms-input-placeholder {
    /* Internet Explorer 10+ */
    color: #ccc;
  }

  input:-webkit-autofill,
  input:-webkit-autofill:hover,
  input:-webkit-autofill:focus {
