<template>
  <div class="logo-box" style="cursor: pointer;" @click.stop="openRouter('/')">
    <img :src="logo" alt="logo" style="display:block; width: 100%" />
  </div>
</template>

<script>
  export default {
    name: 'Logo',
    data(){
      return{
        logo:`/img/logo.svg`
      }
    },
    methods: {
      openRouter(to) {
        this.$router.push(to);
      }
    }
  };
</script>
