<template>
  <div class="project-manage-layout">
    <div class="header layout-header">
      <v-container text-xs-center class="pa-0" style="max-width: 1200px">
        <v-layout row wrap class="ma-0">
          <v-flex xs4>
            <logo-box></logo-box>
          </v-flex>
          <v-flex xs4>
            <div class="project-category pl-1" :class="currentProjectCategory">
              <button
                class="text-center"
                @click.stop="toggleProjectFilter('all')"
              >
                <span v-text="trans.OPAllProjects"></span>
              </button>
              <button
                class="text-center"
                @click.stop="toggleProjectFilter('owned')"
              >
                <span v-text="trans.OPOwnedProjects"></span>
              </button>
              <button
                v-if="sharesListGetters.length > 0"
                class="text-center"
                @click.stop="toggleProjectFilter('share')"
              >
                <span v-text="trans.OPShareExperment"></span>
              </button>
            </div>
          </v-flex>
          <v-flex xs4>
            <div class="user-info">
              <user-info-card></user-info-card>
            </div>
          </v-flex>
        </v-layout>
      </v-container>
    </div>
    <div v-if="this.userEmail.verified" class="project-list-container">
      <!-- <div class="project-list-container"> -->
      <div v-if="!shareShow" style="height: 100%">
        <div v-if="localProjects.length < 1" class="empty-panel">
          <div class="content-container">
            <div
              class="empty-project-list-hint"
              v-text="trans.OTNoProjectsAreAvailableHint1"
            ></div>
            <div
              class="create-project-tips text--disabled transparent"
              v-text="trans.OTNoProjectsAreAvailableHint2"
            ></div>
            <button class="orange add-null-btn" @click.stop="createProject">
              <span v-text="trans.OPCreateAnEmptyProject"></span>
              <i class="mdi mdi-arrow-right"></i>
            </button>
            <!-- <button
              class="orange add-null-btn"
              @click.stop="newProjectDialogIsOpen = true"
            >
              <span v-text="trans.OPCreateAnEmptyProject"></span>
              <i class="mdi mdi-arrow-right"></i>
            </button> -->
          </div>
        </div>
        <div v-else class="project-list">
          <div class="tool-box pr-4">
            <div class="sort-by">
              <button
                class="by-last-access-time text-center"
                :class="{ 'active-order': !orderByConfig.updatedAt.disabled }"
                @click.stop="toggleSortBy('updatedAt')"
              >
                <i
                  class="mdi"
                  :class="
                    orderByConfig.updatedAt.order === 'asc'
                      ? 'mdi-sort-ascending'
                      : 'mdi-sort-descending'
                  "
                ></i>
                <span v-text="trans.OPOrderByLastAccessTime"></span>
              </button>
              <button
                class="by-create-time text-center"
                :class="{ 'active-order': !orderByConfig.createdAt.disabled }"
                @click.stop="toggleSortBy('createdAt')"
              >
                <i
                  class="mdi"
                  :class="
                    orderByConfig.createdAt.order === 'asc'
                      ? 'mdi-sort-ascending'
                      : 'mdi-sort-descending'
                  "
                ></i>
                <span v-text="trans.OPOrderByCreateTime"></span>
              </button>
            </div>
          </div>
          <div class="content-container">
            <project-card
              ref="projectCard"
              v-for="project in localProjects"
              :key="project.id"
              :project="project"
            >
              <template slot="settings-update-project">
                <button @click="updateProjectConfig(project)">
                  <i class="mdi mdi-settings-outline"></i>
                </button>
              </template>
            </project-card>
          </div>
          <div class="create-project-button">
            <div class="btn-box">
              <div class="n-btn">
                <div class="text">
                  <span class="text1">Create a project</span>
                  <span class="arrow"></span>
                </div>
              </div>
              <v-btn fab large @click.stop="createProject">
                <i class="mdi mdi-plus"></i>
              </v-btn>
            </div>
          </div>
        </div>
        <new-project-dialog
          v-if="newProjectDialogIsOpen"
          v-model="newProjectDialogIsOpen"
        ></new-project-dialog>
      </div>
      <div v-if="shareShow">
        <div class="share-show-container">
          <div class="waterfall">
            <div
              class="share-show-item"
              v-for="item in fromExpermentList"
              :key="item.id"
            >
              <div class="share-experment-card">
                <div class="card-from">
                  <div class="share-name-info">
                    <user-avata-circle
                      class="card-avata"
                      :user-name="item.initiator.name"
                      :user-avata="item.initiator.avatar"
                    ></user-avata-circle>
                    <span
                      >{{ trans.OTShareFrom }}
                      <strong v-text="item.initiator.name"></strong
                    ></span>
                  </div>
                  <div class="share-delete">
                    <button @click="deleteShareCard(item.id)">
                      <i class="mdi mdi-close"></i>
                    </button>
                  </div>
                </div>
                <div class="click-box" @click="shareLink(item.id)">
                  <div
                    class="experment-title"
                    v-text="item.sharedObject.title"
                  ></div>
                  <div class="experment-fotter">
                    <div class="fotter-item">
                      <div class="small-title">share time</div>
                      <div class="small-text" v-text="item.createdAt"></div>
                    </div>
                    <div class="fotter-item">
                      <div class="small-title">status</div>
                      <div class="small-text">
                        <span
                          :class="item.sharedObject.status"
                          v-text="item.sharedObject.status"
                        ></span>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div
      v-if="!this.userEmail.verified"
      class="project-list-container user-email-unverified"
    >
      <div class="empty-panel">
        <div class="content-container">
          <div class="user-email-unverified-icon">
            <img src="/img/email-un.svg" width="200" />
          </div>
          <div class="user-email-unverified-title">
            {{ trans.OTUnverifiedTitle }}
          </div>
          <div class="user-email-unverified-text">
            {{ trans.OTUnverifiedText }}
            <strong v-text="userEmail.email"></strong>。
          </div>
          <div class="user-email-unverified-messages">
            {{ trans.OTUnverifiedTextMessages }}
            <span @click="resend">
              {{ trans.OTUnverifiedTextMessagesResend }}
            </span>
            {{ trans.OTUnverifiedTextMessagesOr }}
            <span @click="goToRouter('/user/account')">{{
              trans.OTUnverifiedTextMessagesChange
            }}</span
            >。
          </div>
        </div>
      </div>
      <div class="empty-footer">
        <div class="publick-footer">
          <div class="wall-endline"></div>
          <div class="footer-content">
            <div class="footer-site">
              www.metaml.io
            </div>
          </div>
        </div>
      </div>
    </div>
    <description-dialog
      ref="descDialogDisplay"
      v-show="descDialogDisplayShowGetters"
      :projectInfoObj="projectInfoContent"
    ></description-dialog>
    <div class="project-manage-layout-contact">
      <span class="mdi mdi-help-circle"></span>
      <contact-us class="back-white bottom-left"></contact-us>
    </div>
  </div>
</template>

<script>
import userAvataCircle from '../../components/userAvataCircle';
import logoBox from '../../components/Widgets/logo';
import projectCard from 'Views/projectManage/projectCard';
import newProjectDialog from 'Views/projectManage/newProjectDialog';
import UserInfoCard from '../../components/Widgets/header/UserInfoCard';
import descriptionDialog from '../projectPage/descriptionDialog';
import contactUs from '../../components/contactUs';
import { mapGetters } from 'vuex';

export default {
  name: 'ProjectManageLayout',
  created() {
    this.$store.commit('projectCollection/descDialogDisplayShowFun', false);
    this.$store.commit('projectCollection/descDialogDisplayShowToIdFun', '');
    this.$store.commit(
      'projectDetail/ideasCreateEditDialogDisplayShowFun',
      false
    );
    this.$store
      .dispatch('projectCollection/refreshProjectList')
      .then(rresult => {
        setTimeout(() => {
          this.$store.commit('settings/closeGlobalLoadingPage');
        }, 800);
      });
  },
  components: {
    logoBox,
    projectCard,
    UserInfoCard,
    newProjectDialog,
    userAvataCircle,
    descriptionDialog,
    contactUs
  },
  computed: {
    ...mapGetters({
      user: 'session/user',
      userAvata: 'session/userAvata',
      userName: 'session/userName',
      userEmail: 'session/userEmail',
      projects: 'projectCollection/projectList',
      descDialogDisplayShowGetters:
        'projectCollection/descDialogDisplayShowGetters',
      sharesListGetters: 'shares/sharesListGetters'
    }),
    localProjects: function() {
      let rst = [];
      let self = this;
      switch (self.currentProjectCategory) {
        case 'all':
          rst = self.projects;
          break;
        case 'owned':
          rst = self.projects.filter(x => x.creator === self.user.id);
          break;
        default:
          rst = self.projects;
          break;
      }
      _.forOwn(self.orderByConfig, function(value, key) {
        if (value.disabled) {
          return;
        }
        rst = _.orderBy(rst, [key], [value.order]);
      });

      return rst;
    },
    fromExpermentList() {
      let fromExpermentList = _.orderBy(
        this.sharesListGetters,
        'createdAt',
        'desc'
      );
      fromExpermentList.forEach(item => {
        item.createdAt = moment(item.createdAt).format('MM-DD HH:mm');
      });
      return fromExpermentList;
    }
  },
  mounted() {
    window.onbeforeunload = null;
    this.updateShareList();
  },
  data() {
    return {
      newProjectDialogIsOpen: false,
      // all | owned
      currentProjectCategory: 'all',
      shareShow: false,
      orderByConfig: {
        createdAt: {
          disabled: true,
          order: 'desc'
        },
        updatedAt: {
          disabled: false,
          order: 'desc'
        }
      },
      trans: {
        OTNoProjectsAreAvailableHint1: this.$i18n.t(
          'ProjectManage.Dashboard.OTNoProjectsAreAvailableHint1'
        ),
        OTNoProjectsAreAvailableHint2: this.$i18n.t(
          'ProjectManage.Dashboard.OTNoProjectsAreAvailableHint2'
        ),
        OPCreateAnEmptyProject: this.$i18n.t(
          'ProjectManage.Dashboard.OPCreateAnEmptyProject'
        ),
        OPAllProjects: this.$i18n.t('ProjectManage.Dashboard.OPAllProjects'),
        OPOwnedProjects: this.$i18n.t(
          'ProjectManage.Dashboard.OPOwnedProjects'
        ),
        OPShareExperment: this.$i18n.t(
          'ProjectManage.Dashboard.OPShareExperment'
        ),
        OPOrderByCreateTime: this.$i18n.t(
          'ProjectManage.Dashboard.OPOrderByCreateTime'
        ),
        OPOrderByLastAccessTime: this.$i18n.t(
          'ProjectManage.Dashboard.OPOrderByLastAccessTime'
        ),
        OTUnverifiedTitle: this.$i18n.t(
          'ProjectManage.Dashboard.OTUnverifiedTitle'
        ),
        OTUnverifiedText: this.$i18n.t(
          'ProjectManage.Dashboard.OTUnverifiedText'
        ),
        OTUnverifiedTextMessages: this.$i18n.t(
          'ProjectManage.Dashboard.OTUnverifiedTextMessages'
        ),
        OTUnverifiedTextMessagesOr: this.$i18n.t(
          'ProjectManage.Dashboard.OTUnverifiedTextMessagesOr'
        ),
        OTUnverifiedTextMessagesResend: this.$i18n.t(
          'ProjectManage.Dashboard.OTUnverifiedTextMessagesResend'
        ),
        OTUnverifiedTextMessagesChange: this.$i18n.t(
          'ProjectManage.Dashboard.OTUnverifiedTextMessagesChange'
        ),
        OTShareFrom: this.$i18n.t('shareText.OTShareFrom')
      },
      projectInfoContent: {}
    };
  },
  methods: {
    async createProject() {
      let projectNameArry = this.projects.map(e => e.title);
      let regPos = /^project[1-9][0-9]*$/;
      let filterName = projectNameArry.filter(e => regPos.test(e));
      let nameNumber = filterName.map(e => parseInt(e.slice(7)));
      let max = Math.max(...nameNumber);
      let title = filterName.length > 0 ? `project${max + 1}` : 'project1';
      let data = {
        title: title,
        abstract: {
          markdown: '',
          editorHtml: '',
          previewHtml: ''
        }
      };
      let project = await this.$store.dispatch(
        'projectCollection/createProject',
        data
      );
      this.$store
        .dispatch('userGraph/getUserSelectedGraph', project.id)
        .then(res => {
          this.$router.push('/projects/' + project.id);
        })
        .catch(err => {
          this.$store.commit('settings/showGlobalSnackbar', {
            timeout: 2000,
            text: '创建项目失败'
          });
          setTimeout(() => {
            this.$router.go(0);
          }, 3000);
        });
    },
    updateShareList() {
      let payload = {
        category: 'favorite',
        types: ['Experiment']
      };
      this.$store.dispatch('shares/getShares', payload);
    },
    verLock() {
      this.$store
        .dispatch('projectCollection/addLock', {
          target: `/projects/${this.projectInfoContent.id}`
        })
        .then(response => {
          this.$store.commit(
            'projectCollection/descDialogDisplayShowToIdFun',
            this.projectInfoContent.id
          );
          this.$store.commit(
            'projectCollection/descDialogDisplayShowFun',
            true
          );
        })
        .catch(error => {
          if (error.response.status === 423) {
            this.$store.commit('settings/showGlobalSnackbar', {
              text: this.$i18n.t('ProjectPage.OTProjectLock')
            });
          }
        });
    },
    async updateProjectConfig(project) {
      let projectMine = {};
      project.contributors.forEach(c => {
        if (c.id === this.user.id) {
          projectMine = c;
        }
      });
      if (projectMine.ability === 'Update') {
        this.projectInfoContent = _.cloneDeep(project);
        this.$store.dispatch(
          'projectCollection/setProjectById',
          this.projectInfoContent.id
        );
        //判断是否被锁定
        this.verLock();
      } else {
        this.$store.commit('settings/showGlobalSnackbar', {
          text: this.$i18n.t('ProjectManage.ProjectCard.OTCreatorUpdate')
        });
      }
    },
    openMessageBox() {
      // TODO:open message box
      console.log('TODO:open message box');
    },
    openUserInfoCard() {
      // TODO:implement open user info card
      console.log('TODO:implement open user info card');
    },
    toggleSortBy(fieldName) {
      // console.log(this.projects)
      let field = this.orderByConfig[fieldName];
      if (!field.disabled) {
        field.order = field.order === 'desc' ? 'asc' : 'desc';
      }
      _.forOwn(this.orderByConfig, function(value, key) {
        value.disabled = key !== fieldName;
      });
    },
    toggleProjectFilter(category) {
      category === 'share' ? (this.shareShow = true) : (this.shareShow = false);
      this.currentProjectCategory = category;
    },
    goToRouter(to) {
      this.$router.push(to);
    },
    resend() {
      let self = this;
      self.$store.dispatch('emails/resendForConfirmVerification').then(res => {
        self.$store.commit('settings/showGlobalSnackbar', {
          timeout: 5000,
          text: self.$i18n.t('Session.Account.OTSendEmail')
        });
      });
    },
    deleteShareCard(id) {
      let payload = {
        shareId: id,
        category: 'favorite'
      };
      this.$store.dispatch('shares/deleteSharedShare', payload).then(res => {
        if (this.sharesListGetters.length === 0) {
          this.toggleProjectFilter('all');
        }
      });
    },
    shareLink(id) {
      let routeUrl = this.$router.resolve('/shares/' + id);
      window.open(routeUrl.href, '_blank');
    }
  }
};
</script>
