import Vue from 'vue';
import Vuex from 'vuex';
import createLogger from 'vuex/dist/logger';

// modules
import session from './modules/session';
import emails from './modules/emails';
import projectCollection from './modules/projectCollection';
import projectDetail from './modules/projectDetail';
import settings from './modules/settings';
import user from './modules/user';
import userGraph from './modules/userGraph';
import oss from './modules/oss';
import shares from './modules/shares';

// StackEdit
import stackEdit from '../components/StackEdit/store';

Vue.use(Vuex);

const store = new Vuex.Store({
  modules: {
    emails,
    projectCollection,
    projectDetail,
    session,
    settings,
    user,
    userGraph,
    stackEdit,
    oss,
    shares
  },
  getters: {
    currentUrl() {
      return window.location.href;
    }
  }
  // plugins: [createLogger()]
});

export default store;
