<template>
  <div class="">
    <v-select
      v-model="permissionText"
      :items="permissionItems"
      class="permission-email"
      :label="
        contributorsPermission.ability === 'Update'
          ? this.$i18n.t('ProjectManage.InviteTeammatesDialog.OTCanEdit')
          : this.$i18n.t('ProjectManage.InviteTeammatesDialog.OTCanView')
      "
      @change="change()"
      solo
    ></v-select>
  </div>
</template>

<script>
export default {
  name: 'teammatesListSelect',
  props: {
    contributorsPermission: {
      type: Object
    }
  },
  data() {
    return {
      permissionText: '',
      permissionItems: [
        this.$i18n.t('ProjectManage.InviteTeammatesDialog.OTCanEdit'),
        this.$i18n.t('ProjectManage.InviteTeammatesDialog.OTCanView')
      ],
      trans: {}
    };
  },
  computed: {},
  methods: {
    change() {
      this.permissionText ===
      this.$i18n.t('ProjectManage.InviteTeammatesDialog.OTCanEdit')
        ? this.$set(this.contributorsPermission, 'ability', 'Update')
        : this.$set(this.contributorsPermission, 'ability', 'Read');
    }
  },
  mounted() {}
};
</script>
