<template>
  <div class="tab-question-box">
    <div class="tab-item-content">
      <v-flex class="public-input-box" style="flex: 0 0 auto;margin-bottom: 0">
        <v-text-field
          ref="ideasTitleDom"
          v-model="ideasContentObj.content.title"
          :label="trans.IdeasTitle"
          outline
          height="40px"
          :rules="rules.ideasTitleRequired"
          validate-on-blur
          color="normal"
          :error-messages="titleErrorMessages"
          @focus="textFocus()"
        ></v-text-field>
      </v-flex>
      <v-flex class="public-input-box stack-edit-box stack-edit-box-height1">
        <stack-edit-editor></stack-edit-editor>
      </v-flex>
    </div>
    <div class="text-center" style="position: relative;">
      <v-btn
        v-if="this.ideasContentObj.action === 'create'"
        color="warning"
        class="orange save-btn mt-3"
        @click="saveideasBtn"
        v-text="trans.IdeasCreateBtn"
      ></v-btn>
    </div>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex';
  import StackEditEditor from './StackEdit';
  import editorSvc from './StackEdit/services/editorSvc';

  export default {
    name: 'ProjectInfoTabIdeas',
    components: {
      StackEditEditor
    },
    props: {
      projectInfoTabIdeasShow: {
        type: Boolean
      },
      ideasContentObj: {
        type: Object
      },
      projectInfo: {
        type: Object
      }
    },
    created() {},
    computed: {},
    data() {
      return {
        stackEdit: null,
        rules: {
          ideasTitleRequired: [
            value =>
              !!value ||
              this.$i18n.t('ProjectPage.ProjectInfoTabIdeas.IdeasTitleEmpty'),
            value =>
              !_.some(
                this.projectInfo.ideas,
                item =>
                  item.id !== this.ideasContentObj.content.id &&
                  item.title === value
              ) ||
              this.$i18n.t('ProjectPage.ProjectInfoTabIdeas.IdeasTitleRepeat')
          ]
        },
        titleErrorMessages: '',
        trans: {
          OTNoSaveTips: this.$i18n.t('ProjectPage.OTNoSaveTips'),
          OTSaveSuccessful: this.$i18n.t('ProjectPage.OTSaveSuccessful'),
          OTAutoSaveSuccessful: this.$i18n.t(
            'ProjectPage.OTAutoSaveSuccessful'
          ),
          OTConfirmProjectSave: this.$i18n.t(
            'ProjectPage.OTConfirmProjectSave'
          ),
          OTConfirmNoSave: this.$i18n.t('ProjectPage.OTConfirmNoSave'),
          OTConfirmSave: this.$i18n.t('ProjectPage.OTConfirmSave'),

          IdeasTitle: this.$i18n.t(
            'ProjectPage.ProjectInfoTabIdeas.IdeasTitle'
          ),
          IdeasContent: this.$i18n.t(
            'ProjectPage.ProjectInfoTabIdeas.IdeasContent'
          ),
          IdeasCreateBtn: this.$i18n.t(
            'ProjectPage.ProjectInfoTabIdeas.IdeasCreateBtn'
          ),
          OTSaveing: this.$i18n.t('StackEdit.Layout.OTSaveing'),
          OTNoContentChanges: this.$i18n.t(
            'StackEdit.Layout.OTNoContentChanges'
          ),
          OTAllChangesSaved: this.$i18n.t('StackEdit.Layout.OTAllChangesSaved')
        }
      };
    },

    methods: {
      returnIdeasList() {
        this.$emit('closeDialog', false);
      },

      textFocus() {
        this.titleErrorMessages = '';
      },

      saveideasBtn() {
        if (!this.$refs.ideasTitleDom.validate(true)) {
          return;
        }
        if (this.ideasContentObj.action === 'create') {
          let payload = {
            projectId: this.projectInfo.id,
            idea: {
              title: this.ideasContentObj.content.title,
              content: {
                editorHtml: editorSvc.editorElt.innerHTML,
                previewHtml: editorSvc.previewElt.innerHTML,
                markdown: this.$store.state.stackEdit.markdown.text
              }
            }
          };
          let self = this;
          this.$store
            .dispatch('projectDetail/createIdea', payload)
            .then(res => {
              self.projectInfo.ideas.push(_.cloneDeep(res.idea));
              self.$store.commit(
                'projectDetail/ideasCreateEditDialogDisplayShowFun',
                false
              );
            })
            .catch(err => {
              self.titleErrorMessages = self.$i18n.t(
                'ProjectPage.ProjectInfoTabIdeas.IdeasTitleRepeat'
              );
            });
          this.returnIdeasList();
        }
      },
      autoSave() {
        if (!this.$refs.ideasTitleDom.validate(true)) {
          return false;
        }
        let payload = {
          projectId: this.projectInfo.id,
          idea: {
            id: this.ideasContentObj.content.id,
            title: this.ideasContentObj.content.title,
            content: {
              editorHtml: editorSvc.editorElt.innerHTML,
              previewHtml: editorSvc.previewElt.innerHTML,
              markdown: this.$store.state.stackEdit.markdown.text
            }
          }
        };
        let dataChange = '';
        let self = this;
        let ideaId = payload.idea.id;
        let keys = Object.keys(payload.idea);
        let originIdea = self.$store.getters[
          'projectDetail/project'
        ].ideas.find(x => x.id === ideaId);
        if (!originIdea || _.isEqual(_.pick(originIdea, keys), payload.idea)) {
          self.$store.commit(
            'projectCollection/autoSaveMessagesFun',
            this.trans.OTNoContentChanges
          );
        } else {
          self.$store.commit(
            'projectCollection/autoSaveMessagesFun',
            this.trans.OTSaveing
          );
          this.$store
            .dispatch('projectDetail/updateIdea', payload)
            .then(res => {
              let data = res.idea;
              this.projectInfo.ideas.forEach(item => {
                if (item.id === data.id) {
                  item.title = data.title;
                  item.content = data.content;
                }
              });
              setTimeout(() => {
                self.$store.commit(
                  'projectCollection/autoSaveMessagesFun',
                  this.trans.OTAllChangesSaved
                );
              }, 1000);
            })
            .catch(err => {
              self.titleErrorMessages = self.$i18n.t(
                'ProjectPage.ProjectInfoTabIdeas.IdeasTitleRepeat'
              );
            });
          this.returnIdeasList();
        }
        return true;
      }
    }
  };
</script>
