<template>
  <v-dialog
    ref="nodeDialog"
    v-model="nodeDialogShow"
    hide-overlay
    persistent
    :content-class="
      'node-dialog-display stack-edit-dialog node-addteam-width right-node-dialog edit-model resize-dialog-width' +
        (selectModelIsOpen ? ' select-model' : ' edit-model') +
        (isWidthActive ? ' active' : '') +
        (nodeDialogShow ? ' right-o' : '')
    "
    :width="resizeDialogWidth"
  >
    <v-card>
      <!--Modify the experiment-->
      <div
        v-if="createNodeShow"
        class="creat-node-edit creat-node-edit-stack"
        style="overflow: hidden;position: relative;"
      >
        <div
          class="resize-width-mousedown"
          @mousedown="resizeWidth($event)"
        ></div>
        <div
          class="mdi mdi-arrow-expand-horizontal ew-risize-dom"
          @mousedown="resizeWidth($event)"
        ></div>

        <v-btn
          slot="activator"
          icon
          class="ma-0 close-btn-creat-node"
          @click.stop="exitClose"
        >
          <i class="mdi mdi-close theme--light grey--text"></i>
        </v-btn>
        <div class="container1 pt-5" style="height:100vh">
          <div class="desc-tab-content">
            <div class="project-tab-item pa-2">
              <div class="node-main-type">
                <div
                  class="tab-item-content creat-experment-stackedit stack-edit-box-graph-width"
                  style="width:100%"
                >
                  <v-layout column>
                    <v-flex
                      class="project-title creat-experment-input"
                      style="flex: initial;"
                    >
                      <v-text-field
                        ref="experimentTitleDom"
                        v-model="nodeInfo.title"
                        :label="trans.ExperimentName"
                        outline
                        height="40px"
                        validate-on-blur
                        color="normal"
                        class="title-input"
                        :rules="rules.titleRequired"
                        :error-messages="errorMessages"
                        @focus="textFocus()"
                      ></v-text-field>
                    </v-flex>
                    <v-flex>
                      <div class="node-progress-box">
                        <div class="node-progress-title">
                          {{ trans.ExperimentProgress }}:
                        </div>
                        <div class="node-progress-content">
                          <v-radio-group v-model="nodeInfo.status" row>
                            <v-radio
                              v-for="(item, i) in experimentProgressType"
                              :key="i"
                              light
                              :value="item.type"
                              :label="item.name"
                              :color="item.type"
                            ></v-radio>
                          </v-radio-group>
                        </div>
                      </div>
                    </v-flex>
                    <v-flex
                      class="project-abstract creat-experment-input stack-edit-box stack-edit-box-graph-height"
                    >
                      <stack-edit-editor
                        class="create-experment-stack-editor create-experment-height"
                        v-if="stackEditOpen"
                      ></stack-edit-editor>
                    </v-flex>
                  </v-layout>
                  <div style="height: 65px;"></div>

                  <div
                    class="creat-experment-btn-box return-list-all-experment"
                  >
                    <v-btn
                      v-if="!returnAllExpermentListBtnShow"
                      flat
                      small
                      class="desc-closed"
                      @click.stop="returnAllExpermentList"
                    >
                      <i class="mdi mdi-menu-left" style="font-size: 18px"></i
                      >{{ trans.OTBackToList }}
                    </v-btn>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- All experiments -->
      <div
        v-if="allExpermentShow"
        class="creat-node-edit import-nodes-list-box all-experment"
      >
        <div class="experment-tab">
          <v-btn
            slot="activator"
            icon
            class="all-experment-close ma-0"
            @click.stop="close"
            style="font-size: 1rem;"
          >
            <i class="mdi mdi-close theme--light grey--text"></i>
          </v-btn>
          <v-tabs
            v-model="expermentTabActive"
            class="experment-tab-content"
            dark
          >
            <v-tabs-slider></v-tabs-slider>

            <v-tab href="#tab-all">
              <span v-text="trans.OTAllExperiment"></span>
            </v-tab>
            <v-tab
              href="#tab-share"
              v-if="getsharedSharesListGetters.length > 0"
            >
              <span v-text="trans.OTShare"></span>
            </v-tab>

            <v-tabs-items>
              <v-tab-item value="tab-all">
                <v-container class="pt-0 new-tab-share-list">
                  <div class="desc-tab-content">
                    <div class="project-tab-item pa-2">
                      <div class="node-main-type">
                        <div class="add-team-list import-nodes-list">
                          <div class="add-team-main">
                            <div class="add-team-seach all-experiment-seach">
                              <v-flex xs12>
                                <v-text-field
                                  v-model="allExpermentSearchText"
                                  label="Solo"
                                  :placeholder="trans.SearchExperiment"
                                  solo
                                  autofocus
                                  prepend-icon="mdi mdi-magnify"
                                ></v-text-field>
                              </v-flex>
                            </div>
                            <div
                              class="add-team-people-list nodes-lists import-nodes-list-edit-box"
                            >
                              <v-list-tile
                                v-for="item in allExpermentSearchList"
                                :key="item.id"
                                avatar
                                @click.stop="importNodeBtn(item)"
                                draggable="true"
                                @dragstart="expermentDragstart($event, item)"
                                @dragend="expermentDragend"
                              >
                                <v-list-tile-avatar style="width: auto">
                                  <div
                                    :class="
                                      'impor-experiment-status ' + item.status
                                    "
                                  >
                                    <div
                                      :class="'ico ' + item.status"
                                      v-text="item.status"
                                    ></div>
                                  </div>
                                </v-list-tile-avatar>
                                <v-list-tile-content>
                                  <v-list-tile-title>
                                    {{ item.title }}
                                  </v-list-tile-title>
                                </v-list-tile-content>
                                <v-list-tile-action>
                                  <v-btn
                                    icon
                                    ripple
                                    @click.stop="updateExperiment(item)"
                                  >
                                    <span
                                      class="mdi mdi-circle-edit-outline"
                                    ></span>
                                  </v-btn>
                                  <v-btn
                                    icon
                                    ripple
                                    @click.stop="deleteExperimentOpen(item)"
                                  >
                                    <span
                                      class="mdi mdi-trash-can-outline"
                                    ></span>
                                  </v-btn>
                                </v-list-tile-action>
                              </v-list-tile>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </v-container>
              </v-tab-item>

              <v-tab-item value="tab-share">
                <div class="share-item-content">
                  <v-container class="pt-0 new-tab-share-list">
                    <div class="desc-tab-content">
                      <div class="project-tab-item pa-2">
                        <div class="node-main-type">
                          <div class="add-team-list import-nodes-list">
                            <div class="add-team-main">
                              <div class="add-team-seach all-experiment-seach">
                                <v-flex xs12>
                                  <v-text-field
                                    v-model="shareSearchText"
                                    label="Solo"
                                    :placeholder="trans.SearchExperiment"
                                    solo
                                    autofocus
                                    prepend-icon="mdi mdi-magnify"
                                  ></v-text-field>
                                </v-flex>
                              </div>
                              <div
                                class="add-team-people-list nodes-lists import-nodes-list-edit-box"
                              >
                                <ul>
                                  <li
                                    v-for="item in shareSearchList"
                                    :key="item.id"
                                  >
                                    <div class="item-share-content">
                                      <div
                                        class="share-left"
                                        @click="shareLink(item.id)"
                                      >
                                        <div class="share-info">
                                          <div class="share-user">
                                            <img :src="item.initiator.avatar" />
                                            <div>
                                              <span
                                                v-text="trans.OTShareFrom"
                                              ></span>
                                              <span
                                                class="share-name"
                                                v-text="item.initiator.name"
                                              ></span>
                                              <span>
                                                (<span
                                                  v-text="item.createdAt"
                                                ></span
                                                >)
                                              </span>
                                            </div>
                                          </div>
                                        </div>
                                        <div class="share-content">
                                          <div
                                            class="share-title"
                                            v-text="item.sharedObject.title"
                                          ></div>
                                        </div>
                                      </div>
                                      <div class="delete-share-button">
                                        <v-btn
                                          icon
                                          ripple
                                          @click.stop="deleteShare(item.id)"
                                        >
                                          <span
                                            class="mdi mdi-trash-can-outline"
                                          ></span>
                                        </v-btn>
                                      </div>
                                    </div>
                                  </li>
                                </ul>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </v-container>
                </div>
              </v-tab-item>
            </v-tabs-items>
          </v-tabs>
        </div>
      </div>
    </v-card>
    <public-prompt-dialog
      v-show="publicPromptDialogShow"
      :public-prompt-dialog-show.sync="publicPromptDialogShow"
      @cancelButton="cancelHandle"
      @deleteButton="deleteExperiment"
    >
      <div slot="title">
        <span v-text="deleteExperimentInfo.title"></span>
      </div>
      <div slot="content" class="public-prompt-dialog-content">
        <div class="text-content" v-html="deleteExperimentInfo.content"></div>
      </div>
    </public-prompt-dialog>
  </v-dialog>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import * as graphCfg from '../../constants/peojectGraphConfig';
import experimentNodeSvc from '../../services/experimentNodeSvc';
import publicPromptDialog from '../../components/publicPromptDialog';
import ideasListContent from '../../components/ideasListContent';
import StackEditEditor from '../../components/StackEdit';
import userAvataCircle from '../../components/userAvataCircle';
import editorSvc from '../../components/StackEdit/services/editorSvc';
export default {
  name: 'CreateExperimentDialog',
  components: {
    userAvataCircle,
    ideasListContent,
    publicPromptDialog,
    StackEditEditor
  },
  props: {
    createText: {
      type: String
    },
    value: {
      type: Boolean,
      required: true
    },
    nodeInfo: {
      type: Object
    },
    stackEditOpen: {
      type: Boolean
    }
  },
  beforeCreate() {
    this.$_modelEvent =
      (this.$options.model && this.$options.model.event) || 'input';
  },
  data() {
    return {
      timer: null,
      lockTimer: null,
      teamListShow: false,
      importNodesShow: false,
      allTeamListShow: true,
      isWidthActive: false,
      importSearchList: [], // List of experiments
      importSearchEorrorShow: false, // Search error
      allExpermentSearchText: '', // All experimental search terms
      allExpermentSearchEorrorShow: false, // Search error
      shareSearchText: '', // share search terms
      contributorsNodeList: [],
      searchContributors: '',
      searchContributorsEorrorText: '',
      nodeDialogShow: this.value,
      errorMessages: '',
      rules: {
        titleRequired: [
          value =>
            !!value ||
            this.$i18n.t(
              'ProjectPage.CreateExperimentDialog.ExperimentNameEmpty'
            ),
          value =>
            !_.some(
              this.experimentsNode,
              item => item.id !== this.nodeInfo.id && item.title === value
            ) ||
            this.$i18n.t(
              'ProjectPage.CreateExperimentDialog.ExperimentNameRepeated'
            )
        ],
        contentRequired: value =>
          !!value ||
          this.$i18n.t(
            'ProjectPage.CreateExperimentDialog.ExperimentContentEmpty'
          )
      },
      trans: {
        OTNoSaveTips: this.$i18n.t('ProjectPage.OTNoSaveTips'),
        OTSaveSuccessful: this.$i18n.t('ProjectPage.OTSaveSuccessful'),
        OTAutoSaveSuccessful: this.$i18n.t('ProjectPage.OTAutoSaveSuccessful'),
        OTConfirmExperimentSave: this.$i18n.t(
          'ProjectPage.OTConfirmExperimentSave'
        ),
        OTConfirmNoSave: this.$i18n.t('ProjectPage.OTConfirmNoSave'),
        OTConfirmSave: this.$i18n.t('ProjectPage.OTConfirmSave'),

        ImportExperiment: this.$i18n.t('ProjectPage.ImportExperiment'),
        CreateExperiment: this.$i18n.t('ProjectPage.CreateExperiment'),
        SearchExperiment: this.$i18n.t(
          'ProjectPage.CreateExperimentDialog.SearchExperiment'
        ),
        SearchExperimentError: this.$i18n.t(
          'ProjectPage.CreateExperimentDialog.SearchExperimentError'
        ),
        EditTitle: this.$i18n.t(
          'ProjectPage.CreateExperimentDialog.ConfigurationExperiment'
        ),
        ExperimentName: this.$i18n.t(
          'ProjectPage.CreateExperimentDialog.ExperimentName'
        ),
        ExperimentContent: this.$i18n.t(
          'ProjectPage.CreateExperimentDialog.ExperimentContent'
        ),
        TeamMembers: this.$i18n.t('ProjectPage.TeamMembers'),
        AllTheMembers: this.$i18n.t(
          'ProjectPage.CreateExperimentDialog.AllTheMembers'
        ),
        MemberSearch: this.$i18n.t(
          'ProjectPage.CreateExperimentDialog.MemberSearch'
        ),
        ExperimentProgress: this.$i18n.t(
          'ProjectPage.CreateExperimentDialog.OTExperimentProgress'
        ),
        OTAllExperiment: this.$i18n.t('ProjectPage.OTAllExperiment'),
        OTBackToList: this.$i18n.t(
          'ProjectPage.CreateExperimentDialog.OTBackToList'
        ),
        OTDeleteExperimentContent: this.$i18n.t(
          'ProjectPage.ProjectGraph.DeleteExperimentContent'
        ),
        OTTips: this.$i18n.t('OTTips'),
        OTNoContentChanges: this.$i18n.t('StackEdit.Layout.OTNoContentChanges'),
        OTSaveing: this.$i18n.t('StackEdit.Layout.OTSaveing'),
        OTAllChangesSaved: this.$i18n.t('StackEdit.Layout.OTAllChangesSaved'),
        OTExperimentLock: this.$i18n.t(
          'ProjectPage.CreateExperimentDialog.OTExperimentLock'
        ),
        OTShare: this.$i18n.t('shareText.OTShare'),
        OTShareFrom: this.$i18n.t('shareText.OTShareFrom')
      },
      experimentProgressType: [
        {
          type: 'New',
          name: 'New'
        },
        {
          type: 'Active',
          name: 'Active'
        },
        {
          type: 'Closed',
          name: 'Closed'
        }
      ],
      publicPromptDialogShow: false,
      returnAllExpermentListBtnShow: true,
      deleteExperimentInfo: {
        action: 'experiment'
      },
      expermentTabActive: 'tab-all'
    };
  },
  mounted() {},
  computed: {
    ...mapGetters({
      projectInfo: 'projectDetail/project',
      userGraphList: 'userGraph/userGraphList',
      selectedGraphNodes: 'projectDetail/selectedGraphNodes',
      resizeDialogWidth: 'projectDetail/resizeDialogWidth',
      getsharedSharesListGetters: 'shares/getsharedSharesListGetters'
    }),
    ...mapGetters('stackEdit/layout', ['styles']),

    stackEditContainerClasses() {
      let classes = [];
      if (this.styles.expanded) {
        classes.push('stack-edit-plus-active');
      }
      return classes;
    },
    experimentsNode() {
      let experimentsList = this.projectInfo.experiments;
      return _.orderBy(experimentsList, ['updatedAt'], ['desc']);
    },
    contributorsNode() {
      return this.projectInfo.contributors;
    },
    contributorsNodeId() {
      return this.nodeInfo.contributors.map(x => x.id);
    },
    // Screening experiments
    diffExperiments() {
      let graph = this.$store.getters['projectDetail/selectedGraph'];
      if (!graph) {
        return [];
      }

      let experimentList = _.differenceBy(
        this.experimentsNode,
        graph.nodes,
        x => x.id
      ).map(exp =>
        Object.assign({}, exp, {
          contributors: exp.contributors.map(c1 =>
            this.contributorsNode.find(c2 => c2.id === c1)
          )
        })
      );

      return experimentList;
    },
    createNodeShow() {
      if (this.nodeInfo.action === 'edit') {
        $('.project-content').removeClass('padding-right-24');
        return true;
      } else {
        return false;
      }
    },
    allExpermentShow() {
      if (this.nodeInfo.action === 'allExperment') {
        return true;
      } else {
        return false;
      }
    },
    allExpermentSearchList() {
      let searchTextVal = this.allExpermentSearchText;
      let searchLists = this.experimentsNode;
      let sortType = 'experment.updatedAt';
      let searchList = this.searchListArr(searchTextVal, searchLists, sortType);
      return searchList;
    },
    shareSearchList() {
      let searchTextVal = this.shareSearchText;
      let searchLists = this.getsharedSharesListGetters;
      let sortType = 'experment.createdAt';
      let shareSearchList = this.searchListArr(
        searchTextVal,
        searchLists,
        sortType
      );
      shareSearchList.forEach(e => {
        e.createdAt = moment(e.createdAt).format('MM-DD HH:mm');
      });
      return shareSearchList;
    },
    searchContributorsEorror() {
      if (this.contributorsNodeList.length === 0) {
        this.searchContributorsEorrorText = this.$i18n.t(
          'ProjectPage.CreateExperimentDialog.NoMember'
        );
        return true;
      } else {
        return false;
      }
    },
    createBtnText() {
      return this.$i18n.t('ProjectPage.CreateExperimentDialog.OTSave');
    },
    selectModelIsOpen() {
      if (this.nodeInfo.action === 'import') {
        this.importNode();
        return false;
      } else {
        return !this.createNodeShow && !this.importNodesShow;
      }
    }
  },
  watch: {
    value(newValue, oldValue) {
      this.nodeDialogShow = newValue;
    },
    nodeDialogShow(Val) {
      this.toggleStatus(Val);
    },
    createNodeShow(newValue, oldValue) {
      // Monitor the change of edit box data and save it automatically
      if (newValue) {
        // Click refresh, modify routing address, close window, prompt whether to reload
        window.onbeforeunload = function(e) {
          e = e || window.event;
          // Compatible with previous versions of IE8 and Firefox 4
          if (e) {
            e.returnValue = '';
          }
          // Chrome, Safari, Firefox 4+, Opera 12+ , IE 9+
          return '';
        };
        this.timer = setInterval(() => {
          this.autoSave();
        }, 5000);
        this.lockTimer = setInterval(() => {
          this.$store.dispatch('projectCollection/addLock', {
            target: `/projects/${this.projectInfo.id}/experiments/${this.nodeInfo.id}`
          });
        }, 60000);
      } else {
        // Cancel the prompt pop-up window and timer when the project edit box closes
        window.onbeforeunload = null;
        clearInterval(this.timer);
        clearInterval(this.lockTimer);
        this.timer = null;
        this.lockTimer = null;
        this.$store.dispatch('projectCollection/unLock', {
          target: `/projects/${this.projectInfo.id}/experiments/${this.nodeInfo.id}`
        });
      }
    }
  },
  // Cancel Timer before Component Destruction
  beforeDestroy() {
    window.onbeforeunload = null;
    clearInterval(this.timer);
    clearInterval(this.lockTimer);
    this.timer = null;
    this.lockTimer = null;
  },
  methods: {
    ...mapActions('stackEdit/data', ['toggleExpanded']),
    dialogWidthNull() {
      let dialogWidth = '';
      this.$store.commit('projectDetail/resizeDialogWidthFun', dialogWidth);
    },
    textFocus() {
      this.errorMessages = '';
    },
    searchListArr(searchTextVal, searchLists, sortType) {
      let searchText = searchTextVal.split('').filter(x => x !== ' ');
      searchText.length === 0 && searchText.push('.*');
      let regString = _.compact(searchText)
        .map(x => `(${x})`)
        .join('.*');
      let searchList = searchLists.map(x => {
        let matches = !x.creator
          ? x.sharedObject.title.match(new RegExp(regString || '.*', 'i'))
          : x.title.match(new RegExp(regString || '.*', 'i'));
        let score = _(matches || [])
          .tail()
          .compact()
          .value()
          .join('').length;
        return {
          experment: x,
          score: score
        };
      });

      searchList = _.orderBy(searchList, ['score', sortType], ['desc', 'desc'])
        .filter(x => x.score > 0)
        .map(x => x.experment);

      return searchList;
    },
    addNodeTeam() {
      this.contributorsNodeList = JSON.parse(
        JSON.stringify(this.contributorsNode)
      );
      this.isWidthActive = true;
      this.teamListShow = true;
    },
    addAllContributors() {
      let newIdlist = this.nodeInfo.contributors.map(x => x.id);
      this.contributorsNodeList.forEach(item => {
        if (!newIdlist.includes(item.id)) {
          this.nodeInfo.contributors.push(item);
        }
      });
      this.contributorsNodeList = [];
    },
    addExperimentContributors(id) {
      this.contributorsNodeList.forEach((item, index) => {
        if (item.id === id) {
          this.nodeInfo.contributors.push(item);
          this.contributorsNodeList.splice(index, 1);
        }
      });
    },
    deleteExperimentContributors(id) {
      let newIdlist = this.contributorsNodeList.map(x => x.id);
      this.nodeInfo.contributors.forEach((item, index) => {
        if (item.id === id) {
          this.nodeInfo.contributors.splice(index, 1);
          if (!newIdlist.includes(id)) {
            this.contributorsNodeList.push(item);
          }
        }
      });
    },
    // getPreviewHtml() {
    //   let html = document.getElementsByClassName('metaml-preview-html')[0].innerHTML;
    //   return html;
    // },
    async exitClose() {
      // Close the experiment editing rectification, check whether the data is changed, if changed, display the inquiry box, whether to save.
      let result = await this.autoSave();
      if (result) {
        this.close();
      }
    },
    close() {
      $('body').removeClass('edit-experment-class-1024');
      this.toggleExpanded(false);
      this.toggleStatus(false); // Bullet window closure
      this.$emit('update:stackEditOpen', false);
      this.returnAllExpermentListBtnShow = true;
      $('.project-content').removeClass('padding-right-24');
      this.dialogWidthNull();
      setTimeout(() => {
        this.nodeInfo.action = 'default';
      }, 550);
    },
    autoSave() {
      let self = this;
      self.teamListShow = false;
      self.isWidthActive = false;
      if (!self.$refs.experimentTitleDom.validate(true)) {
        self.$refs.experimentTitleDom.onBlur();
        return false;
      }

      // 截取超参和结果
      let allPreviewHtml = editorSvc.previewElt.innerHTML;
      let allEditorHtml = editorSvc.editorElt.outerHTML;
      let allMarkDown = this.$store.state.stackEdit.markdown.text;

      // 超参预览html
      let pParameterHtmlList = $('.preview pre.language-parameter').parent(
        '.cl-preview-section'
      );
      let parameterPreviewHtml = '';
      for (var i = 0; i < pParameterHtmlList.length; i++) {
        let pParameterHtmlOuterText = pParameterHtmlList[i].outerText.replace(
          /\s/gm,
          ''
        );
        pParameterHtmlOuterText.replace(/\n/gm, '');
        if (
          pParameterHtmlOuterText !== '' &&
          pParameterHtmlList[i].outerText !== 'parameter'
        ) {
          parameterPreviewHtml += pParameterHtmlList[i].outerHTML;
        }
      }
      // 结果预览html
      let pResultHtmlList = $('.preview pre.language-result').parent(
        '.cl-preview-section'
      );
      let resultPreviewHtml = '';
      for (var i = 0; i < pResultHtmlList.length; i++) {
        let pResultHtmlOuterText = pResultHtmlList[i].outerText.replace(
          /\s/gm,
          ''
        );
        pResultHtmlOuterText.replace(/\n/gm, '');
        if (
          pResultHtmlOuterText !== '' &&
          pResultHtmlList[i].outerText !== 'result'
        ) {
          resultPreviewHtml += pResultHtmlList[i].outerHTML;
        }
      }
      // 超参编辑html
      let eParameterHtmlList = $('.editor span.pre').parent('.cledit-section');
      let regPE = /^```parameter\n[\s\S]*?\n```$/gm;
      let parameterEditorHtmlArr = [];
      for (var i = 0; i < eParameterHtmlList.length; i++) {
        let regEResult = eParameterHtmlList[i].textContent.match(regPE);
        if (regEResult) {
          parameterEditorHtmlArr.push(eParameterHtmlList[i].outerHTML);
        }
      }
      let parameterEditorHtml = parameterEditorHtmlArr.join();

      // 结果编辑html
      let eResultHtmlList = $('.editor span.pre').parent('.cledit-section');
      let regRE = /^```result\n[\s\S]*?\n```$/gm;
      let resultEditorHtmlArr = [];
      for (var i = 0; i < eResultHtmlList.length; i++) {
        let regEResult = eResultHtmlList[i].textContent.match(regRE);
        if (regEResult) {
          resultEditorHtmlArr.push(eResultHtmlList[i].outerHTML);
        }
      }
      let resultEditorHtml = resultEditorHtmlArr.join();

      // 超参markdown文本
      let regPM = /^```parameter\n[\s\S]*?\n```$/gm;
      let regPMResult = allMarkDown.match(regPM);
      let parameterMarkDown = '';
      if (regPMResult) {
        parameterMarkDown = regPMResult.join('\n');
      }
      // 结果markdown文本
      let regRM = /^```result\n[\s\S]*?\n```$/gm;
      let regRMResult = allMarkDown.match(regRM);
      let resultMarkDown = '';
      if (regRMResult) {
        resultMarkDown = regRMResult.join('\n');
      }
      // 截取结果
      let payload = {
        projectId: self.$route.params.projectId,
        experiment: {
          title: self.nodeInfo.title,
          content: {
            markdown: allMarkDown,
            editorHtml: allEditorHtml,
            previewHtml: allPreviewHtml
          },
          goal: {
            markdown: '',
            editorHtml: '',
            previewHtml: ''
          },
          result: {
            markdown: resultMarkDown,
            editorHtml: resultEditorHtml,
            previewHtml: resultPreviewHtml
          },
          settings: {
            markdown: parameterMarkDown,
            editorHtml: parameterEditorHtml,
            previewHtml: parameterPreviewHtml
          },
          status: self.nodeInfo.status
        }
      };
      if (self.nodeInfo.action === 'edit') {
        let dataChange = '';
        payload.experiment.id = self.nodeInfo.id;
        // Compared with local stroe, unchanged and uncommitted
        let originExperiment = self.$store.getters[
          'projectDetail/project'
        ].experiments.find(x => x.id === payload.experiment.id);
        let chooseFields = ['id', 'title', 'content', 'status'];
        let chooseExperiment = _.pick(originExperiment, chooseFields);
        if (
          !chooseExperiment ||
          _.isEqual(chooseExperiment, _.pick(payload.experiment, chooseFields))
        ) {
          self.$store.commit(
            'projectCollection/autoSaveMessagesFun',
            this.trans.OTNoContentChanges
          );
        } else {
          self.$store.commit(
            'projectCollection/autoSaveMessagesFun',
            this.trans.OTSaveing
          );
          self.$store
            .dispatch('projectDetail/updateExperiment', payload)
            .then(res => {
              let selectedGraphNodes = this.$store.getters[
                'projectDetail/selectedGraph'
              ].nodes;
              let node = selectedGraphNodes.find(
                x => x.experimentId === self.nodeInfo.id
              );
              let nodeHeight = $(`[data-node-id=${node.id}]`)
                .find('.v-card')
                .height();
              let payload = {
                nodeId: node.id,
                height: Math.ceil(nodeHeight)
              };
              this.$store.commit('projectDetail/shrinkExperimentNode', payload);
              setTimeout(() => {
                self.$store.commit(
                  'projectCollection/autoSaveMessagesFun',
                  this.trans.OTAllChangesSaved
                );
              }, 1000);
            })
            .catch(err => {
              self.errorMessages = self.$i18n.t(
                'ProjectPage.CreateExperimentDialog.ExperimentNameRepeated'
              );
            });
        }
        return true;
      }
    },
    searchContributorsText() {
      let Contributorslist = [];
      this.contributorsNode.forEach(item => {
        if (
          item.username
            .toLowerCase()
            .search(this.searchContributors.toLowerCase()) !== -1 &&
          !this.contributorsNodeId.includes(item.id)
        ) {
          Contributorslist.push(item);
        }
      });
      this.contributorsNodeList = Contributorslist;
    },
    importNode() {
      this.importSearchList = this.diffExperiments;
      this.importNodesShow = true;
    },
    chooseCreateNode() {
      this.toggleStatus(false);
      experimentNodeSvc.createNodeWithNewExpriment();
    },
    importNodeBtn(exper) {
      let project = this.$store.getters['projectDetail/project'];
      let selectedGraph = this.$store.getters['projectDetail/selectedGraph'];
      let payload = {
        projectId: project.id,
        graphId: selectedGraph.id,
        node: {
          experimentId: exper.id,
          location: experimentNodeSvc.calculateNodeInitialLocation()
        }
      };
      let experimentNodesIdArry = [];
      this.selectedGraphNodes.forEach(e => {
        experimentNodesIdArry.push(e.experimentId);
      });
      if (experimentNodesIdArry.includes(exper.id)) {
        this.$store.commit('settings/showGlobalSnackbar', {
          text: this.$i18n.t(
            'ProjectPage.CreateExperimentDialog.ExperimentsExist'
          )
        });
      } else {
        this.$store.dispatch('projectDetail/importNode', payload);
      }
    },
    // Drag experiment begins
    expermentDragstart(event, item) {
      event.dataTransfer.setData('item', item.id);
    },
    // End of drag experiment
    expermentDragend(event) {
      event.dataTransfer.clearData();
    },
    verLock(operType,experiment){
      this.$store
        .dispatch('projectCollection/addLock', {
          target: `/projects/${this.projectInfo.id}/experiments/${experiment.id}`
        })
        .then(response => {
          if(operType==='updateExperiment'){
            this.$emit('expermentListEdit', experiment);
          }
          else{
            this.publicPromptDialogShow = true;
          }
        })
        .catch(error => {
          if (error.response.status === 423) {
            this.$store.commit('settings/showGlobalSnackbar', {
              text: this.$i18n.t(
                'ProjectPage.CreateExperimentDialog.OTExperimentLock'
              )
            });
          }
        });
    },
    async deleteExperimentOpen(experiment) {
      this.deleteExperimentInfo = {
        action: 'deleteExperiment',
        title: this.$i18n.t('ProjectPage.ProjectGraph.RemoveExperiment'),
        content: this.$i18n.t(
          'ProjectPage.ProjectGraph.DeleteExperimentContent',
          [experiment.title]
        ),
        tips: this.$i18n.t('PublicPromptDialog.OTTips'),
        experimentInfoList: experiment
      };

      //判断是否被锁定
      this.verLock('deleteExperiment',experiment)
    },
    deleExpermentErrorTextFun(error) {
      let graphNamelist = [];
      let response = error.response.data;
      response.data.forEach(id => {
        this.projectInfo.graphs.forEach(g => {
          if (g.id === id) {
            graphNamelist.push(g.title);
          }
        });
      });
      let graphName =
        graphNamelist.length > 1
          ? graphNamelist.join(',')
          : graphNamelist.join(' ');
      if (error.response.status === 422) {
        this.$store.commit('settings/showGlobalSnackbar', {
          timeout: 5000,
          text: this.$i18n.t(response.message, [graphName])
        });
      }
    },
    cancelHandle() {
      if (this.deleteExperimentInfo.action === 'deleteExperiment') {
        this.$store.dispatch('projectCollection/unLock', {
          target: `/projects/${this.projectInfo.id}/experiments/${this.deleteExperimentInfo.experimentInfoList.id}`
        });
      }
    },
    async deleteExperiment() {
      // Delete experimental and experimental node
      let graphId = this.$route.params.selectedGraphId;
      let nodes = [];
      this.projectInfo.graphs.forEach(graph => {
        if (parseInt(graph.id) === graphId) {
          graph.nodes.forEach(node => {
            nodes.push(node.id);
          });
        }
      });
      if (this.deleteExperimentInfo.action === 'deleteExperiment') {
        let payload = {
          projectId: this.$route.params.projectId,
          experimentId: this.deleteExperimentInfo.experimentInfoList.id,
          graphId: this.$route.params.selectedGraphId,
          nodeId: this.deleteExperimentInfo.experimentInfoList.id
        };
        try {
          await this.$store.dispatch('projectDetail/deleteExperiment', payload);
        } catch (error) {
          this.deleExpermentErrorTextFun(error);
        }

        this.publicPromptDialogShow = false;
      }
    },
    toggleStatus(status) {
      this.nodeDialogShow = status;
      this.importNodesShow = false;
      this.teamListShow = false;
      this.isWidthActive = false;
      if (this.value !== status) {
        this.$emit(this.$_modelEvent, this.nodeDialogShow);
      }
    },
    async updateExperiment(experiment) {
      this.returnAllExpermentListBtnShow = false;
      //判断是否被锁定
      this.verLock('updateExperiment',experiment)
    },
    async returnAllExpermentList() {
      // If the experiment edit box exists, verify the experiment and save it automatically
      let createNodeShow = this.createNodeShow;
      if (createNodeShow) {
        let result = await this.autoSave();
        if (result) {
          this.dialogWidthNull();
          this.$parent.importNodeOpen();
        }
      } else {
        this.dialogWidthNull();
        this.$parent.importNodeOpen();
      }
    },
    resizeWidth(e) {
      $('body').addClass('resize-dialog-width-down-active');
      let containerRect = $('.resize-dialog-width')[0].getBoundingClientRect();
      let dialogWidth = parseInt(containerRect.right - containerRect.left);

      let startX = e.clientX;
      let self = this;
      document.onmousemove = function(e) {
        let windowWidth = $(window).width();
        let graphWidth = $('.project-content').width();
        let endX = e.clientX;
        let moveLen = dialogWidth - (endX - startX);
        if (moveLen > graphWidth) moveLen = graphWidth;
        if (moveLen < 455) moveLen = '28.44rem';
        self.$store.commit('projectDetail/resizeDialogWidthFun', moveLen);
      };
      document.onmouseup = function(evt) {
        document.onmousemove = null;
        document.onmouseup = null;
        $('body').removeClass('resize-dialog-width-down-active');
      };
      return false;
    },
    deleteShare(shareId) {
      let payload = {
        category: 'shared',
        shareId: shareId
      };
      this.$store.dispatch('shares/deleteSharedShare', payload).then(res => {
        if (this.getsharedSharesListGetters.length === 0) {
          this.expermentTabActive = 'tab-all';
        }
      });
    },
    shareLink(id) {
      let routeUrl = this.$router.resolve('/shares/' + id);
      window.open(routeUrl.href, '_blank');
    }
  }
};
</script>
