import Vue from 'vue';
/**
 * Update local project experiments
 */
function refreshExperiment(state, payload) {
  let project = this.getters['projectCollection/project'](payload.projectId);
  if (!project) {
    return;
  }
  let itemExperiment = project.experiments.find(
    x => x.id === payload.experiment.id
  );
  if (itemExperiment && itemExperiment.editor) {
    payload.experiment.editor = itemExperiment.editor;
  }
  if (
    itemExperiment &&
    (itemExperiment.settings.isActive || itemExperiment.result.isActive)
  ) {
    payload.experiment.settings.isActive = itemExperiment.settings.isActive;
    payload.experiment.result.isActive = itemExperiment.result.isActive;
  }
  _.vueRemove(project.experiments, x => x.id === payload.experiment.id);
  project.experiments.push(payload.experiment);
}

/**
 * Delete an experimental detail data from the local cache
 */
function removeExperiment(state, payload) {
  let project = this.getters['projectCollection/project'](payload.projectId);
  let selectedGraphId = this.getters['userGraph/userSelectedGraph'];
  let graphList = project.graphs.filter(x => x.id !== selectedGraphId);
  let expIdlist = _(graphList.map(x => x.nodes))
    .flatten()
    .map(x => x.id)
    .uniq()
    .value();
  if (expIdlist.includes(payload.experimentId)) {
    throw new Error(
      `Can not remove the experiment which id is "${payload.experimentId}",there have other graph node referenced it.`
    );
  }
  _.vueRemove(project.experiments, x => x.id === payload.experimentId);
}

/**
 * Update the local project experiment list
 */
function refreshExperimentList(state, payload) {
  let project = this.getters['projectCollection/project'](payload.projectId);
  if (typeof project !== 'undefined') {
    project.experiments.splice(
      0,
      project.experiments.length,
      ...payload.experimentList
    );
  }
}

