<template>
  <div class="text-xs-center">
    <v-dialog
      content-class="public-overlay-box"
      v-model="publicPromptDialogShow"
      persistent
      width="25rem"
    >
      <v-card class="graph-menu-dialog-dom">
        <v-card-title class="headline lighten-2" primary-title>
          <slot name="title"></slot>
          <div class="closed-dialog" @click="cancelBtn">
            <span class="mdi mdi-window-close"></span>
          </div>
        </v-card-title>

        <v-card-text>
          <slot name="content"></slot>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="#9FA2A8"
            flat
            @click="cancelBtn"
            v-text="trans.Cancel"
          ></v-btn>
          <v-btn
            color="orange"
            flat
            @click="deleteBtn"
            v-text="trans.OTDelete"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
  export default {
    name: 'PublicPromptDialog',
    props: {
      publicPromptDialogShow: {
        type: Boolean
      }
    },
    data() {
      return {
        trans: {
          Cancel: this.$i18n.t('ProjectPage.Cancel'),
          OTDelete: this.$i18n.t('PublicPromptDialog.OTDelete')
        }
      };
    },
    methods: {
      cancelBtn() {
        this.$emit('update:publicPromptDialogShow', false);
        this.$emit('cancelButton');
      },
      deleteBtn(event) {
        this.$emit('deleteButton');
      }
    }
  };
</script>
