<template>
  <div class="graph-input-style">
    <v-text-field
      ref="tabInput"
      v-model="defaultText"
      class="tab-input ma-0 text-center"
      :autofocus="!inputReadonly"
      :readonly="inputReadonly"
      solo
      @dblclick.stop="editorTitle($event)"
      @blur="updateValue($event)"
      @keyup.enter="updateValue($event)"
    ></v-text-field>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex';
  export default {
    name: 'GraphTabInput',
    props: {
      title: {
        type: String
      },
      graphId: {
        type: String
      }
    },
    data() {
      return {
        defaultText: this.title,
        inputReadonly: true
      };
    },
    computed: {
      ...mapGetters({
        projectInfo: 'projectDetail/project',
        user: 'session/user'
      }),
      updateShow() {
        let projectMine = {};
        this.projectInfo.contributors.forEach(c => {
          if (c.id === this.user.id) {
            projectMine = c;
          }
        });
        let ability = projectMine.ability === 'Update' ? true : false;
        return ability;
      }
    },
    watch: {
      title(value) {
        this.defaultText = value;
      }
    },
    methods: {
      editorTitle(e) {
        let graph = this.projectInfo.graphs.find(x => x.id === this.graphId);
        this.updateShow
          ? (this.inputReadonly = false)
          : (this.inputReadonly = true);
      },
      updateValue(e) {
        let graphTitle = [];
        this.projectInfo.graphs.forEach(element => {
          graphTitle.push(element.title);
        });
        this.inputReadonly = true;
        if (this.defaultText === '' || graphTitle.includes(this.defaultText)) {
          this.defaultText = this.title;
        } else {
          let data = {
            projectId: this.projectInfo.id,
            title: this.defaultText,
            graphId: this.graphId
          };
          this.$store.dispatch('projectDetail/refreshGraphBasicInfo', data);
        }
      }
    }
  };
</script>
