import api from 'Api';
import socketAndHttp from '../../../services/socketAndHttp';
/**
 * Get the project palette list and refresh local data
 */
async function refreshGraphList(context, projectId) {
  await this.dispatch('projectCollection/getProjectById', projectId);
  let response = await api.get(`/projects/${projectId}/graphs`);
  let data = {
    projectId,
    graphList: response.data
  };
  this.commit('projectDetail/refreshGraphList', data);
  return data;
}

async function resetGraphsOrderIndex(context, payload) {
  this.commit('projectDetail/resetGraphsOrderIndex', payload);
  let graphIdList = payload.sortedGraphIdList;
  api.patch(
    `/projects/${payload.projectId}/graphs?action=resetGraphsOrderIndex`,
    graphIdList
  );
}

/**
 * Get the palette information specified by graphId in the project and cache it
 */
async function refreshGraphById(context, payload) {
  let projectId = payload.projectId;
  let graphId = payload.graphId;
  let response = await api.get(`/projects/${projectId}/graphs/${graphId}`);
  let data = {
    projectId,
    graph: response.data
  };
  this.commit('projectDetail/refreshGraphById', data);
  return data;
}

/**
 * Create a New Sketchpad
 */
async function createGraph(context, payload) {
  let projectId = payload.projectId;
  let graph = payload.graph;
  let response = await socketAndHttp(
    `/projects/${projectId}/graphs`,
    'post',
    graph
  );
  let data = {
    projectId: projectId,
    graph: response.data
  };
  this.commit('projectDetail/refreshGraphById', data);
  return data;
}

/**
 * Update the palette information specified by graphId
 */
async function refreshGraphBasicInfo(context, payload) {
  let graphInfo = {
    title: payload.title
  };
  let response = await socketAndHttp(
    `/projects/${payload.projectId}/graphs/${payload.graphId}`,
    'patch',
    graphInfo
  );
  let data = response.data;
  this.commit('projectDetail/updateGraphBasicInfo', data);
  return data;
}

/**
 * Delete the drawing board with the specified ID
 */
async function deleteGraphById(context, payload) {
  let response = await socketAndHttp(
    `/projects/${payload.projectId}/graphs/${payload.graphId}`,
    'delete'
  );
  this.commit('projectDetail/deleteGraphById', payload);
  return payload;
}

/**
 * User enlargement panel
 */
async function userExpandGraph(context, payload) {
  this.commit('projectDetail/userExpandGraph', payload);
  let response = await api.patch(
    `/users/projects/${payload.projectId}/graphs/${payload.graphId}`,
    payload.transform
  );
  return response;
}

/**
 * Get the item graphs with the specified id, try to get the local cached item, and get it from the server if it does not exist locally
 */
async function getProjectGraphsById(context, projectId) {
  let project = this.getters['projectCollection/project'](projectId);
  return new Promise((resolve, reject) => {
    if (project) {
      if (project.graphs.length > 0) {
        let payload = {
          projectId: projectId,
          graphList: project.graphs
        };
        resolve(payload);
      } else {
        this.dispatch('projectDetail/refreshGraphList', projectId)
          .then(p => resolve(p))
          .catch(e => reject(e));
      }
    } else {
      this.dispatch('projectDetail/refreshGraphList', projectId)
        .then(p => resolve(p))
        .catch(e => reject(e));
    }
  });
}

export default {
  refreshGraphList,
  resetGraphsOrderIndex,
  refreshGraphById,
  createGraph,
  deleteGraphById,
  refreshGraphBasicInfo,
  userExpandGraph,
  getProjectGraphsById
};
