import store from '../store';

function emails(to, from, next) {
  switch (to.name) {
    case 'confirm-email-address':
      let emailId = to.params.emailId;
      store
        .dispatch('emails/getConfirmVerification', emailId)
        .then(res => {
          store.commit('session/logoutSuccess');
        })
        .catch(err => {
          err.response.status === 404 ? next('/404') : '';
        });
      break;
    case 'reset-password':
      break;
    case 'project-inviting':
      let LoggedIn = store.getters['session/isAuthenticated']; // Whether to log in or not
      let invitingEmailId = to.params.emailId;
      store
        .dispatch('emails/getProjectInviting', invitingEmailId)
        .then(res => {
          if (LoggedIn) {
            store.dispatch('user/getAccount');
            next({
              path: `/projects/${res.data}`
            });
          } else {
            next({
              path: '/login',
              query: {
                projectId: res.data
              }
            });
          }
        })
        .catch(err => {
          let type = err.response.data.message;
          if (type === 'UserEmail') {
            // Account not registered
            next({
              path: '/signup',
              query: {
                email: err.response.data.data,
                redirect: to.fullPath
              }
            });
          } else {
            next('/404');
          }
        });
      break;
  }
}

export default {
  emails
};
