import 'babel-polyfill';
import Vue from 'vue';
import Vuex from 'vuex';
import Vuetify from 'vuetify';
import Notifications from 'vue-notification';
import velocity from 'velocity-animate';
import Croppa from 'vue-croppa';
import NProgress from 'nprogress';
import VueI18n from 'vue-i18n';
import vueLogger from 'vue-logger';
import { initialSocket } from './services/socket';
// app.vue
import App from './App';

// router
import router from './router';

// themes
import primaryTheme from './themes/primaryTheme';

// include script file
import './lib/VuelyScript';

// include all css files
import './lib/VuelyCss';

// store
import store from './store';

// emails
import emails from './services/emails';

// messages
import messages from './lang';

import VeeValidate from 'vee-validate';

import en from 'vee-validate/dist/locale/en';
import zh_CN from 'vee-validate/dist/locale/zh_CN';
import * as helpFuncs from './helpers/helpers';

import VueLazyload from 'vue-lazyload'; // Picture Lazy Loading

Vue.config.productionTip = false;

Vue.use(VeeValidate);

_.vueRemove = helpFuncs.vueRemove;

if (!store.getters['settings/selectedLocale']) {
  let language = helpFuncs.getLanguage().substring(0, 2);
  // language = "zh";
  switch (language.toLowerCase()) {
    case 'zh':
      language = store.getters['settings/languages'].find(
        x => x.locale === 'zh_CN'
      );
      break;
    default:
      language = store.getters['settings/languages'].find(
        x => x.locale === 'en'
      );
      break;
  }
  store.dispatch('settings/changeLanguage', language);
}

if (store.getters['settings/selectedLocale'].locale === 'zh_CN') {
  VeeValidate.Validator.localize('zh_CN', zh_CN);
  moment.locale('zh-cn');
} else {
  VeeValidate.Validator.localize('en', en);
  moment.locale('en');
}

// navigation guards before each
router.beforeEach((to, from, next) => {
  NProgress.start();
  if (!to.matched.length) {
    next('/404');
    return;
  }

  if (to.matched.some(record => record.meta.needLoadingPage)) {
    store.commit('settings/openGlobalLoadingPage');
  } else {
    setTimeout(() => store.commit('settings/closeGlobalLoadingPage'), 32);
  }

  emails.emails(to, from, next);

  // this route requires auth, check if logged in
  // if not, redirect to login page.
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (store.getters['session/isAuthenticated']) {
      let projectId = to.params.projectId;
      let toNameArry = ['projectGraph', 'ideasPage', 'experimentDetailPage'];
      if (toNameArry.includes(to.name)) {
        // Jump graph 404
        if (to.name === 'projectGraph') {
          store
            .dispatch('projectDetail/getProjectGraphsById', projectId)
            .then(res => {
              let selectedGraphId = to.params.selectedGraphId;
              let graphList = res.graphList;
              let fIndex = graphList.findIndex(x => x.id === selectedGraphId);
              if (fIndex === -1) {
                next('/404');
              } else {
                next();
              }
            })
            .catch(error => {
              console.log(error)
              if (
                error.message &&
                error.message === 'window.Tracker is not a constructor'
              ) {
                next();
              } else {
                next('/404');
              }
            });
        }
        // Jump idea 404
        if (to.name === 'ideasPage') {
          store
            .dispatch('projectDetail/getProjectIdeasById', projectId)
            .then(res => {
              let ideasId = to.params.ideasId;
              let ideaList = res.ideaList;
              let fIndex = ideaList.findIndex(
                x => parseInt(x.id) === Number(ideasId)
              );
              if (fIndex === -1) {
                next('/404');
              } else {
                next();
              }
            })
            .catch(error => {
              next('/404');
            });
        }
        // Jump experimentDetailPage 404
        if (to.name === 'experimentDetailPage') {
          store
            .dispatch('projectDetail/getProjectExperimentsById', projectId)
            .then(res => {
              let experimentId = to.params.experimentId;
              let experimentList = res.experimentList;
              let fIndex = experimentList.findIndex(
                x => parseInt(x.id) === Number(experimentId)
              );
              if (fIndex === -1) {
                next('/404');
              } else {
                next();
              }
            })
            .catch(error => {
              next('/404');
            });
        }
      } else {
        next();
      }
    } else {
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      });
    }
  } else {
    next();
  }
});

// navigation guard after each
router.afterEach((to, from) => {
  NProgress.done();
  setTimeout(() => {
    $('.v-content__wrap').scrollTop(0, 0);
  }, 200);
});

// ability
import { abilitiesPlugin } from '@casl/vue';
import { defineAbilitiesForProjectSubject } from './services/cals';

Vue.use(
  abilitiesPlugin,
  defineAbilitiesForProjectSubject(store.getters['session/user'])
);

import { Can } from '@casl/vue';

Vue.component('Can', Can);

// plugins
Vue.use(Vuetify, {
  theme: store.getters['settings/selectedTheme'].theme
});
Vue.use(VueI18n);
Vue.use(Notifications, {
  velocity
});

Vue.use(Croppa);

Vue.use(VueLazyload, {
  preLoad: 1.3,
  error: require('../public/img/img-load.png'),
  loading: require('../public/img/img-load.png'),
  attempt: 1
});

let loggerOption = {
  prefix: () => new Date(),
  dev: true,
  shortname: true,
  levels: ['log', 'warn', 'debug', 'error', 'dir'],
  forceLevels: []
};
Vue.use(vueLogger, loggerOption);
// Create VueI18n instance with options
const i18n = new VueI18n({
  locale: store.getters['settings/selectedLocale'].locale, // set locale
  messages // set locale messages
});

// Global error capture
const errorHandler = (error, vm) => {
  // Submit Vue error data to log server
  let payload = {
    type: 'clientError',
    userId: store.getters['session/user'].id,
    url: window.location.href,
    message: error.message,
    stack: error.stack
  };
  setLog(payload);
  // Console Error Reporting
  console.error('error');
  console.error(vm);
  console.error(error);
};

Vue.config.errorHandler = errorHandler;
Vue.prototype.$throw = error => errorHandler(error, this);

/* eslint-disable no-new */

let vueInstance = new Vue({
  store,
  el: '#app',
  i18n,
  router,
  components: {
    App
  },
  methods: {},
  template: '<App/>'
});

initialSocket(vueInstance);

window.metamlVueInstance = vueInstance;

export default vueInstance;
