'use strict';

let Ability = require('@casl/ability').Ability;

let originalCan = Ability.prototype.can;
let originalCannot = Ability.prototype.cannot;
let originalThrowUnlessCan = Ability.prototype.throwUnlessCan;

const PRIVATE_FIELD =
  typeof Symbol !== 'undefined'
    ? Symbol('private')
    : `__customAbility${Date.now()}`;
let relevantRuleFor = function(callBack, action, subjectName, subject, fields) {
  if (typeof subject === 'string' || typeof subject === 'undefined') {
    fields = subject;
    subject = subjectName;
  } else if (typeof subject === 'object') {
    subject[PRIVATE_FIELD] = {
      subjectName
    };
  } else {
    throw new Error('arguments are invalid');
  }

  return callBack.call(this, action, subject, fields);
};

Ability.prototype.can = function(action, subjectName, subject, fields) {
  return relevantRuleFor.call(
    this,
    originalCan,
    action,
    subjectName,
    subject,
    fields
  );
};

Ability.prototype.cannot = function(action, subjectName, subject, fields) {
  return relevantRuleFor.call(
    this,
    originalCannot,
    action,
    subjectName,
    subject,
    fields
  );
};

Ability.prototype.throwUnlessCan = function(
  action,
  subjectName,
  subject,
  fields
) {
  return relevantRuleFor.call(
    this,
    originalThrowUnlessCan,
    action,
    subjectName,
    subject,
    fields
  );
};

let abilityOptions = {
  subjectName(subject) {
    if (!subject) {
      throw new Error('subject is empty');
    }

    if (typeof subject === 'string') {
      return subject;
    }

    if (typeof subject === 'object') {
      let subjectName = subject[PRIVATE_FIELD].subjectName;
      if (typeof subjectName !== 'string') {
        throw new Error('subjectName is invalid');
      }

      return subjectName;
    }

    throw new Error('subject is invalid');
  }
};

/**
 * @param {User} user
 */
function defineAbilitiesForProjectSubject(user) {
  let rules = [
    {
      actions: 'read',
      subject: 'all'
    }
  ];

  if (!user || !user.roles || user.roles.includes('guest')) {
    return new Ability(rules, abilityOptions);
  }

  if (user.roles.includes('admin')) {
    rules.push({
      actions: 'manage',
      subject: 'all'
    });
    return new Ability(rules, abilityOptions);
  }

  // useage: ability.can("create", "project")
  rules.push({
    actions: 'create',
    subject: 'project'
  });

  // Project basicinfo must be maintained by the creator
  // useage: ability.can(actions, "project", project)
  rules.push({
    actions: ['update', 'delete'],
    subject: 'project',
    conditions: {
      creator: user.id
    }
  });

  let ability = new Ability(rules, abilityOptions);
  return ability;
}

export default defineAbilitiesForProjectSubject;
