import actions from './actions';
import mutations from './mutations';

const states = {
  user: JSON.parse(localStorage.getItem('userData') || '{}'),
  sid: localStorage.getItem('sid') || '',
  avarBackgtoundColor: '#000'
};
// getters
const getters = {
  user(state, getters) {
    return state.user;
  },
  connectSid(state, getters) {
    return state.user;
  },
  userAvatar(state, getters) {
    return getters.user.avatar;
  },
  userName(state, getters) {
    return getters.user.username || 'Guest';
  },
  userEmail(state, getters) {
    return {
      email: state.user.email,
      verified: state.user.verified
    };
  },
  isAuthenticated(state, getters) {
    return Object.keys(state.user).length > 0;
  },
  userSid(state, getters) {
    return state.sid;
  },
  getAvarBackgtoundColor(state) {
    return state.avarBackgtoundColor;
  }
};

export default {
  namespaced: true,
  state: states,
  getters,
  actions,
  mutations
};
