<template>
  <v-dialog
    v-model="lazyValue"
    persistent
    content-class="new-project-dialog create-new-project"
  >
    <v-card>
      <v-card-title>
        <span
          v-once
          class="flex project-title-top"
          v-text="trans.OTTitle"
        ></span>
        <v-btn slot="activator" icon @click.stop="toggleStatus(false)">
          <i class="mdi mdi-close theme--light grey--text"></i>
        </v-btn>
      </v-card-title>
      <v-container class="create-new-project-new">
        <v-layout column>
          <v-flex class="project-title">
            <v-text-field
              ref="title"
              v-model="title.value"
              :label="trans.OTTitlePlaceholder"
              height="40px"
              outline
              validate-on-blur
              :rules="title.rules"
              color="normal"
              :error-messages="titleErrorMessages"
              :placeholder="trans.OTEnterProjectName"
              @focus="textFocus()"
            ></v-text-field>
          </v-flex>
          <v-flex class="project-abstract mgt-10">
            <label class="abstract-name">{{
              trans.OTAbstractPlaceholder
            }}</label>
            <div
              class="creat-experment-input stack-edit-box stack-edit-box-graph-height"
            >
              <stack-edit-editor></stack-edit-editor>
            </div>
          </v-flex>
        </v-layout>
      </v-container>
      <v-card-actions class="container">
        <v-btn class="flex submit-button" @click.stop="create" v-if="btnShow">
          <span v-text="trans.OPSubmitButton"></span>
        </v-btn>
        <transition name="fade">
          <v-progress-circular
            v-if="!btnShow"
            style="margin:0 auto 20px"
            indeterminate
            color="orange"
          ></v-progress-circular>
        </transition>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import StackEditEditor from '../../components/StackEdit';
import editorSvc from '../../components/StackEdit/services/editorSvc';
export default {
  name: 'NewProjectDialog',
  components: {
    StackEditEditor
  },
  props: {
    value: {
      type: Boolean,
      required: true
    }
  },
  computed: {},
  data() {
    return {
      title: {
        value: '',
        rules: [
          v =>
            !!v ||
            this.$i18n.t('ProjectManage.NewProjectDialog.VLTitleIsRequired')
        ]
      },
      abstract: {
        value: '',
        rules: [
          // abstract is optional
          v =>
            !!v ||
            true ||
            this.$i18n.t('ProjectManage.NewProjectDialog.VLAbstractIsRequired')
        ]
      },
      titleErrorMessages: '',
      lazyValue: this.value,
      snackbarIsOpen: false,
      trans: {
        OTTitle: this.$i18n.t('ProjectManage.NewProjectDialog.OTTitle'),
        OTTitlePlaceholder: this.$i18n.t(
          'ProjectManage.NewProjectDialog.OTTitlePlaceholder'
        ),
        OTAbstractPlaceholder: this.$i18n.t(
          'ProjectManage.NewProjectDialog.OTAbstractPlaceholder'
        ),
        OPSubmitButton: this.$i18n.t(
          'ProjectManage.NewProjectDialog.OPSubmitButton'
        ),
        VLTitleAlreadyExist: this.$i18n.t(
          'ProjectManage.NewProjectDialog.VLTitleAlreadyExist'
        ),
        OTEnterProjectName: this.$i18n.t(
          'ProjectPage.DescriptionDialog.OTEnterProjectName'
        ),
        OTEnterProjectAbstract: this.$i18n.t(
          'ProjectPage.DescriptionDialog.OTEnterProjectAbstract'
        )
      },
      btnShow: true
    };
  },
  watch: {
    value(val) {
      this.lazyValue = val;
    },
    lazyValue(val) {
      this.toggleStatus(val);
    }
  },
  beforeCreate() {
    this.$_modelEvent =
      (this.$options.model && this.$options.model.event) || 'input';
  },
  created() {},
  methods: {
    textFocus() {
      this.titleErrorMessages = '';
    },
    async create() {
      let self = this;
      let i18n = this.$i18n;
      if (
        !(self.$refs.title.validate(true))
      ) {
        return;
      }
      this.btnShow = false;
      try {
        let data = {
          title: self.title.value,
          abstract: {
            markdown: self.$store.state.stackEdit.markdown.text,
            editorHtml: editorSvc.previewElt.innerHTML,
            previewHtml: editorSvc.previewElt.innerHTML
          }
        };
        let res = await self.$store
          .dispatch('projectCollection/createProject', data)
          .then(res => {
            this.$router.push('/projects/' + res.id);
            this.btnShow = true;
            self.toggleStatus(false);
          });
      } catch (err) {
        this.btnShow = true;
        this.titleErrorMessages = this.trans.VLTitleAlreadyExist;
      }
    },
    toggleStatus(status) {
      this.lazyValue = status;
      if (this.value !== status) {
        this.$emit(this.$_modelEvent, this.lazyValue);
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.3s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
.mgt-10 {
  margin-top: 10px;
}
</style>
