/**
 * Helpers Functions
 */
import pbkdf2 from 'crypto-js/pbkdf2';
import sha1 from 'crypto-js/sha1';

// Get Date
export function getTheDate(timestamp, format) {
  let time = timestamp * 1000;
  let formatDate = format || 'MM-DD-YYYY';
  return moment(time).format(formatDate);
}

// Convert Date To Timestamp
export function convertDateToTimeStamp(date, format) {
  let formatDate = format || 'YYYY-MM-DD';
  return moment(date, formatDate).unix();
}

/**
 * Text Truncate
 */
export function textTruncate(str, length, ending) {
  if (length === null) {
    length = 100;
  }
  if (ending === null) {
    ending = '...';
  }
  if (str.length > length) {
    return str.substring(0, length - ending.length) + ending;
  } else {
    return str;
  }
}

/**
 * Function to convert hex to rgba
 */
export function hexToRgbA(hex, alpha) {
  var c;
  if (/^#([A-Fa-f0-9]{3}){1,2}$/.test(hex)) {
    c = hex.substring(1).split('');
    if (c.length === 3) {
      c = [c[0], c[0], c[1], c[1], c[2], c[2]];
    }
    c = '0x' + c.join('');
    return (
      'rgba(' +
      [(c >> 16) & 255, (c >> 8) & 255, c & 255].join(',') +
      ',' +
      alpha +
      ')'
    );
  }
  throw new Error('Bad Hex');
}

/**
 * Function to return PBKDF2 hash string for password
 */
export function getPbkdf2HashForPassword(text) {
  return pbkdf2(String(text), sha1('starcode'), {
    keySize: 8,
    iterations: 15000
  }).toString();
}

/**
 * get browser‘s language
 */
export function getLanguage() {
  let lang = window.navigator.language || window.navigator.userLanguage;
  if (!lang) {
    lang = 'en';
  }

  return lang;
}

export function mapSubGetters(prefix, getters) {
  return Object.getOwnPropertyNames(getters)
    .filter(x => x.startsWith(prefix))
    .reduce(function(rst, x) {
      rst[x.substring(prefix.length)] = getters[x];
      return rst;
    }, {});
}

export function vueRemove(array, predicate) {
  let result = [];
  if (!(array && array.length)) {
    return result;
  }

  if (typeof predicate !== 'function') {
    throw new Error('predicate is invalid');
  }

  for (let index = 0; index < array.length; ) {
    let value = array[index];
    if (predicate(value, index, array)) {
      result.push(value);
      array.splice(index, 1);
      continue;
    }
    index++;
  }

  return result;
}
