import * as graphMath from '../../../helpers/graphMath';
import { mapGetters } from 'vuex';
export default {
  ...mapGetters({
    userSelectedGraph: 'userGraph/userSelectedGraph',
    userGraphList: 'userGraph/userGraphList',
    projectInfo: 'projectDetail/project',
    selectedGraph: 'projectDetail/selectedGraph',
    selectedGraphNodes: 'projectDetail/selectedGraphNodes',
    selectedGraphEdges: 'projectDetail/selectedGraphEdges',
    userMine: 'session/user',
    currentUrl: 'currentUrl',
    selectedUserGraph: 'userGraph/selectedUserGraph',
    updateGraphCursorListGetters: 'projectDetail/updateGraphCursorList'
  }),
  scaleRateText() {
    return this.selectedUserGraph
      ? Math.floor(this.lastScale * 100) + '%'
      : '100%';
  },
  selectedEdge() {
    if (!this.selectedEdgeKey) {
      return null;
    }
    return this.selectedGraphEdges.find(
      x =>
        x.edge.startNode === this.selectedEdgeKey.edge.startNode &&
        x.edge.endNode === this.selectedEdgeKey.edge.endNode
    );
  },
  experimentNodes() {
    this.$nextTick(() => {
      this.resetD3Interactive4Nodes();
      if (this.selectedGraphNodes.length) {
        for (let i = 0; i < this.selectedGraphNodes.length; i++) {
          let realHeight = $(
            `[data-node-id=${this.selectedGraphNodes[i].id}]`
          ).height();
          let defaultHeight = this.selectedGraphNodes[i].height;
          if (Math.ceil(realHeight) !== defaultHeight) {
            let payload = {
              nodeId: this.selectedGraphNodes[i].id,
              height: Math.ceil(realHeight)
            };
            this.$store.commit('projectDetail/shrinkExperimentNode', payload);
          }
        }
      }
    });

    let selectedGraphNodes = _.cloneDeep(
      this.selectedGraphNodes.filter(x => x.id !== -1)
    );
    selectedGraphNodes.forEach(e => {
      e.experiment.updatedAt = moment(e.experiment.updatedAt).format(
        'MMM Do HH:mm:ss'
      );
    });
    return selectedGraphNodes;
  },
  edges() {
    this.edgesCopy = _.cloneDeep(this.selectedGraphEdges);
    if (this.edgesCopy.length === 0) {
      return;
    }
    this.$nextTick(() => {
      if (this.selectedUserGraph) {
        let scale = this.selectedUserGraph.transform.scale;
        let edgeMap = {};
        this.edgesCopy.forEach(e => (edgeMap[e.key] = e));
        let edgeContainerList = this.$el.querySelectorAll('div.edge-container');
        for (let div of edgeContainerList) {
          let edgeId = div.dataset['edgeId'];
          let edge = edgeMap[edgeId];

          let path = div.querySelector('svg > g > path');
          let pathRect = path.getBoundingClientRect();
          let divRect = div.getBoundingClientRect();

          let delta = {
            x: (divRect.x - pathRect.x) / scale,
            y: (divRect.y - pathRect.y) / scale
          };
          pathRect.height /= scale;
          pathRect.width /= scale;

          const divContainerPadding = 15;
          let sap = graphMath.removeLocationUnit(edge.startAnchorPosition);
          edge.container.width =
            pathRect.width + 2 * divContainerPadding + 'px';
          edge.container.height =
            pathRect.height + 2 * divContainerPadding + 'px';
          edge.container.left = sap.x - delta.x - divContainerPadding + 'px';
          edge.container.top = sap.y - delta.y - divContainerPadding + 'px';
          edge.svg.transform = `translate(${divContainerPadding +
            delta.x}px,${divContainerPadding + delta.y}px)`;
          if (this.selectedEdge && this.selectedEdge.key === edgeId) {
            edge.svg.class = _.compact(
              edge.svg.class.split(' ').concat('active')
            ).join(' ');
            edge.container['z-index'] = edge.edge.zIndex - 2;
          }
        }
      }
    });
    return this.edgesCopy;
  },
  diffExperiments() {
    let graph = this.$store.getters['projectDetail/selectedGraph'];
    if (!graph) {
      return [];
    }
    let experimentList = _.differenceBy(
      this.projectInfo.experiments,
      graph.nodes,
      x => x.id
    ).map(exp =>
      Object.assign({}, exp, {
        contributors: exp.contributors.map(c1 =>
          this.projectInfo.contributors.find(c2 => c2.id === c1)
        )
      })
    );
    return experimentList;
  },
  updateShow() {
    let projectMine = {};
    this.projectInfo.contributors.forEach(c => {
      if (c.id === this.userMine.id) {
        projectMine = c;
      }
    });
    let ability = projectMine.ability === 'Update' ? true : false;
    return ability;
  }
};
