import io from 'socket.io-client';
import store from '../store';

export const socketInstance = {
  default: null
};

export function initialSocket(vueInstance) {
  vueInstance.$watch(
    function() {
      return vueInstance.$store.getters['session/isAuthenticated'];
    },
    function(newVal, oldVal) {
      if (!!newVal) {
        const serverUrl = new URL(envVariable.VUE_APP_API_SERVER_URL);
        socketInstance.default = io(`${serverUrl.origin}`, {
          path: `${serverUrl.pathname}/socket.metaml.io`,
          autoConnect: false,
          query: {
            'x-sid': vueInstance.$store.getters['session/userSid']
          }
        });
        socketInstance.default.open();
        registerSocketEventHandlers(socketInstance.default);
      } else if (!!socketInstance.default) {
        socketInstance.default.disconnect();
        socketInstance.default = void 0;
      }
    },
    {
      immediate: true
    }
  );
}

function registerSocketEventHandlers(socket) {
  //connection
  socket.on('connect', function() {
    console.log('Successful socket connection!');
  });
  //Disconnect
  socket.on('disconnect', function(response) {
    console.log('Socket disconnected!', response);
  });
  //Monitoring messages
  socket.on('message', function(data) {
    let ApiData = data.body;
    let timestamp = data.timestamp;
    let ApiTarget = data.target;
    let TargetArry = ApiTarget ? ApiTarget.split('/') : [];
    let ApiAction = data.action;
    let payload = null;
    let RegObject = {
      //Updating project member information, including updating member information, adding new members, deleting members that are not currently logged in, and modifying user privileges
      updateProjectContributors: /^\/projects\/[0-9a-fA-F]{24}\/contributors\/[0-9a-fA-F]{24}$/,
      //Update project member information, delete members or delete items
      deleteProjectOrContributors: /^\/users\/[0-9a-fA-F]{24}\/projects\/[0-9a-fA-F]{24}$/,
      //Update project, update project information
      updateProject: /^\/projects\/[0-9a-fA-F]{24}$/,
      //modify ideas、add ideas、delete ideas
      updateProjectIdeas: /^\/projects\/[0-9a-fA-F]{24}\/ideas\/[0-9a-fA-F]{24}$/,
      //modify experiments、add experiments、delete experiments
      updateProjectExperiments: /^\/projects\/[0-9a-fA-F]{24}\/experiments\/[0-9a-fA-F]{24}$/,
      //add nodes、delete nodes
      updateProjectNodes: /^\/projects\/[0-9a-fA-F]{24}\/graphs\/[0-9a-fA-F]{24}\/nodes\/[0-9a-fA-F]{24}$/,
      //add graphs、modify graphs、delete graphs
      updateProjectGraphs: /^\/projects\/[0-9a-fA-F]{24}\/graphs\/[0-9a-fA-F]{24}$/,
      //add edges、modify edges、delete edges
      updateProjectEdges: /^\/projects\/[0-9a-fA-F]{24}\/graphs\/[0-9a-fA-F]{24}\/edges\/[0-9a-fA-F]{24}$/,
      //Update mouse position
      updateGraphCursor: /^\/projects\/[0-9a-fA-F]{24}\/graphs\/[0-9a-fA-F]{24}\/cursor$/
    };
    _(RegObject).forEach(function(value, key) {
      let result = value.test(ApiTarget);
      if (result) {
        switch (key) {
          case 'updateProjectContributors':
            payload = {
              action: ApiAction,
              projectId: TargetArry[2],
              contributorsId: TargetArry[4],
              ...ApiData
            };
            store.commit(
              'projectDetail/SOCKET_updateProjectContributors',
              payload
            );
            break;
          case 'deleteProjectOrContributors':
            payload = {
              action: ApiAction,
              projectId: TargetArry[4]
            };
            if (payload.action === 'delete') {
              store.commit(
                'projectDetail/SOCKET_deleteProjectOrContributors',
                payload
              );
            }
            break;
          case 'updateProject':
            payload = {
              action: ApiAction,
              projectId: TargetArry[2],
              ...ApiData
            };
            if (payload.action === 'get') {
              store.commit('projectCollection/SOCKET_updateProject', payload);
            }
            break;
          case 'updateProjectIdeas':
            payload = {
              action: ApiAction,
              projectId: TargetArry[2],
              ideasId: TargetArry[4],
              ...ApiData
            };
            store.commit('projectDetail/SOCKET_updateProjectIdeas', payload);
            break;
          case 'updateProjectExperiments':
            payload = {
              action: ApiAction,
              projectId: TargetArry[2],
              experimentsId: TargetArry[4],
              ...ApiData
            };
            store.commit(
              'projectDetail/SOCKET_updateProjectExperiments',
              payload
            );
            break;
          case 'updateProjectNodes':
            payload = {
              action: ApiAction,
              projectId: TargetArry[2],
              graphsId: TargetArry[4],
              nodesId: TargetArry[6],
              ...ApiData
            };
            store.commit('projectDetail/SOCKET_updateProjectNodes', payload);
            break;
          case 'updateProjectGraphs':
            payload = {
              action: ApiAction,
              projectId: TargetArry[2],
              graphsId: TargetArry[4],
              ...ApiData
            };
            store.commit('projectDetail/SOCKET_updateProjectGraphs', payload);
            break;
          case 'updateProjectEdges':
            payload = {
              action: ApiAction,
              projectId: TargetArry[2],
              graphsId: TargetArry[4],
              edgeId: TargetArry[6],
              ...ApiData
            };
            store.commit('projectDetail/SOCKET_updateProjectEdges', payload);
            break;
          case 'updateGraphCursor':
            let type = ApiData.data.type;
            if (type === 'mouse-move') {
              payload = {
                projectId: TargetArry[2],
                graphsId: TargetArry[4],
                mouseData: ApiData.data,
                operator: ApiData.metadata.operator,
                timestamp: timestamp
              };
              store.commit('projectDetail/SOCKET_updateGraphCursor', payload);
            } else {
              payload = {
                projectId: TargetArry[2],
                graphsId: TargetArry[4],
                data: ApiData.data,
                operator: ApiData.metadata.operator
              };
              store.commit('projectDetail/SOCKET_mouseDownOrUpNodes', payload);
            }
            break;
          default:
            break;
        }
      }
    });
  });
}
