<template>
  <div class="ideas-tips-mouseover">
    <div class="ideas-item-hover">
      <div class="ideas-content-box project-des-tooltip">
        <div class="ideas-title">
          <div class="ideas-title-text">
            #{{ getTips.code }} {{ getTips.title }}
          </div>
        </div>
        <div class="ideas-content">
          <div class="ideas-content-html mt-2">
            <ideas-list-content
              :ideas-content-inner-text="getTips.html"
            ></ideas-list-content>
          </div>
          <div class="time">
            {{ updatedTime }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex';
  import ideasListContent from './ideasListContent';

  export default {
    name: 'IdeasTips',
    components: {
      ideasListContent
    },
    data() {
      return {
        ideaHtml: ''
      };
    },
    computed: {
      ...mapGetters('stackEdit', ['getTips']),
      updatedTime() {
        let updatedTime = moment(this.getTips.updatedAt);
        if (!updatedTime.isValid()) {
          return 'None';
        }
        return updatedTime.format('YYYY-MM-DD HH:mm:ss');
      }
    },
    mounted() {}
  };
</script>
