import NProgress from 'nprogress';
import api from 'Api';
import { getPbkdf2HashForPassword } from '../../../helpers/helpers';

function login(context, user) {
  NProgress.start();
  let data = {
    email: String(user.email).trim(),
    password: getPbkdf2HashForPassword(user.password)
  };

  return new Promise((resolve, rejiect) => {
    api
      .post('/session/login', data)
      .then(response => {
        context.commit('loginSuccess', response.data);
        resolve(response);
      })
      .catch(error => {
        context.commit('loginFailure', error);
        rejiect(error);
      })
      .finally(() => NProgress.done());
  });
}

function logout(context) {
  NProgress.start();

  return new Promise((resolve, rejiect) => {
    api
      .get('/session/logout')
      .then(response => {
        context.commit('logoutSuccess');
        resolve(response);
      })
      .catch(error => {
        context.commit('logoutFailure');
        rejiect(error);
      })
      .finally(() => NProgress.done());
  });
}

function signup(context, user) {
  NProgress.start();
  let data = {
    email: String(user.email).trim(),
    password: getPbkdf2HashForPassword(user.password),
    organization: user.organization
  };

  return new Promise((resolve, rejiect) => {
    api
      .post('/session/signup', data)
      .then(response => {
        resolve(response);
      })
      .catch(error => {
        context.commit('signupFailure', error);
        rejiect(error);
      })
      .finally(() => NProgress.done());
  });
}

export default {
  login,
  logout,
  signup
};
