                    <v-carousel hide-delimiters height="200" interval="15000">
                      <v-carousel-item
                        v-for="(item, i) in superiorityItems"
                        :key="i"
                      >
                        <div class="item-text">
                          <h1 class="h1">{{ item.title }}</h1>
                          <div class="desc">
