  </div>
</template>

<script>
import homeSignUp from '../../components/Widgets/homeSignUp';
import contactUs from '../../components/contactUs';
import { mapGetters } from 'vuex';
export default {
  name: 'homePage',
  created() {
    this.$store.commit('settings/closeGlobalLoadingPage');
    this.$store.getters['session/isAuthenticated']
      ? this.$router.replace('/projects')
      : '';
  },
  components: {
    homeSignUp,
    contactUs
  },
  mounted() {
    let windowHeight = window.innerHeight;
    windowHeight <= 1094
      ? window.addEventListener('scroll', this.scrollImg)
      : (this.imgActive = true);
  },
  data() {
    return {
      contentItems: [
        {
          title: this.$i18n.t('Session.HomePage.OTDashboardHeaderTitle'),
          desc: this.$i18n.t('Session.HomePage.OTDashboardHeaderDesc'),
          contentImg: `/img/third-bg.png`
        },
        {
          title: this.$i18n.t('Session.HomePage.OTGraphsHeaderTitle'),
          desc: this.$i18n.t('Session.HomePage.OTGraphsHeaderDesc'),
          contentImg: `/img/fourth-bg.png`,
          items: [
            {
              title: this.$i18n.t('Session.HomePage.OTInviteTeammates'),
              desc: this.$i18n.t('Session.HomePage.OTInviteTeammatesDesc'),
              itemImg: `/img/fourth-2.png`
            },
            {
              title: this.$i18n.t('Session.HomePage.OTGraphsCardTitle'),
              desc: this.$i18n.t('Session.HomePage.OTGraphsCardDesc'),
              itemImg: `/img/fourth-1.png`
            },
            {
              title: this.$i18n.t('Session.HomePage.OTCreateIdeas'),
              desc: this.$i18n.t('Session.HomePage.OTCreateIdeasDesc'),
              itemImg: `/img/fourth-2-1.png`
            },
            {
              title: this.$i18n.t('Session.HomePage.OTDashboardEditTitle'),
              desc: this.$i18n.t('Session.HomePage.OTDashboardEditDesc'),
              itemImg: `/img/latex.gif`
            }
          ]
        }
      ],
      homeFooterText: {
        title: this.$i18n.t('Session.HomePage.OTHomeFooterTitle'),
        desc: this.$i18n.t('Session.HomePage.OTHomeFooterDesc')
      },
      trans: {
        OTSignIn: this.$i18n.t('Session.HomePage.OTSignIn'),
        OTSignUp: this.$i18n.t('Session.HomePage.OTSignUp'),
        OTWhatDifferent: this.$i18n.t('Session.HomePage.OTWhatDifferent'),
        OTHomePageTitle: this.$i18n.t('Session.HomePage.OTHomePageTitle')
      },
      superiorityItems: [
        {
          id: 'second',
          icon: '/img/projectlist.svg',
          iconActive: '/img/projectlist-active.svg',
          iconShow: false,
          title: this.$i18n.t('Session.HomePage.OTWhatSecondTitle'),
          desc: this.$i18n.t('Session.HomePage.OTWhatSecondDesc')
        },
        {
          id: 'first',
          title: this.$i18n.t('Session.HomePage.OTWhatFirstTitle'),
          desc: this.$i18n.t('Session.HomePage.OTWhatFirstDesc')
        },

        {
          id: 'third',
          icon: '/img/share.svg',
          iconActive: '/img/share-active.svg',
          iconShow: false,
          title: this.$i18n.t('Session.HomePage.OTWhatThirdTitle'),
          desc: this.$i18n.t('Session.HomePage.OTWhatThirdDesc')
        }
      ],
      scroll: '',
      imgActive: false,
      hoverActive: ''
    };
  },
  computed: {
    ...mapGetters({
      selectedLocale: 'settings/selectedLocale'
    }),
    topSize() {
      if (this.selectedLocale.locale === 'en') {
        return '23';
      } else {
        return '19.5';
      }
    }
  },
  methods: {
    goRouter(to) {
      this.$router.push(to);
    },
    scrollImg() {
      this.scroll =
        document.documentElement.scrollTop || document.body.scrollTop;
      this.scroll > 75 ? (this.imgActive = true) : (this.imgActive = false);
    },
    secondEnter(id) {
      this.hoverActive = id + '-active';
      this.superiorityItems.forEach(e => {
        e.id === id ? (e.iconShow = true) : (e.iconShow = false);
      });
    },
    secondLeave() {
      this.hoverActive = '';
      this.superiorityItems.forEach(e => {
        e.iconShow = false;
      });
    }
  }
};
</script>
