<template>
  <div class="session-wrapper signup">
    <div class="session-header">
      <logo-box></logo-box>
    </div>

    <div class="session-main">
      <div class="session-content">
        <error-box ref="errorDom" class="mb-4"></error-box>

        <p v-once class="function-hint">{{ trans.OTOrganization }}</p>
        <input
          v-model="organization"
          class="email"
          type="text"
          :placeholder="trans.OTOrganizationText"
          @keyup.enter="signup"
        />

        <p v-once class="function-hint">
          {{ trans.emailAddress }}
          <span class="required">({{ trans.OTRequired }})</span>
        </p>
        <input
          v-model="email"
          class="email"
          type="text"
          :placeholder="trans.email"
          @keyup.enter="signup"
        />

        <p v-once class="function-hint">
          {{ trans.passwordTitle }}
          <span class="required">({{ trans.OTRequired }})</span>
        </p>
        <input
          v-model="password"
          class="password"
          type="password"
          :placeholder="trans.password"
          @keyup.enter="signup"
        />

        <div class="agree-box-content">
          <v-checkbox
            v-model="AgreeToPrivacyPolicy"
            class="agree-box"
            @change="agreeChange"
          ></v-checkbox>
          <div class="text" v-html="urlPaths"></div>
        </div>

        <button
          class="submit-button"
          @click="signup"
          :disabled="disabledOff"
          v-text="trans.OPSubmitButton"
        ></button>
      </div>
      <div class="session-bottom">
        <span v-once v-text="trans.OTAlreadyHaveAccount"></span>
        <span style="margin: 0 0.25rem;"></span>
        <router-link
          v-once
          class="login"
          to="/login"
          v-text="trans.OTToLogin"
        ></router-link>
      </div>
    </div>
    <div class="session-footer">
      <contact-us class="back-white"></contact-us>
    </div>
  </div>
</template>

<script>
  import logoBox from '../../components/Widgets/logo';
  import errorBox from '../../components/Widgets/session/error';
  import contactUs from '../../components/contactUs';

  export default {
    name: 'Signup',
    created() {
      let v = this.$validator;

      v.attach({
        name: 'email',
        alias: this.trans.email,
        getter: () => this.email,
        rules: {
          required: true,
          email: true,
          regex: /^[^\u4e00-\u9fa5]+$/
        }
      });

      v.attach({
        name: 'password',
        alias: this.trans.password,
        getter: () => this.password,
        rules: {
          required: true,
          alpha_dash: true,
          min: 6,
          regex: /^.*(\D\d)|(\d\D).*$/
        }
      });
    },
    components: {
      logoBox,
      errorBox,
      contactUs
    },
    data() {
      return {
        username: '',
        email: '',
        password: '',
        organization: '',
        AgreeToPrivacyPolicy: false,
        disabledOff: true,
        urlPaths: null,
        trans: {
          username: this.$i18n.t('username'),
          email: this.$i18n.t('email'),
          password: this.$i18n.t('password'),
          OPSubmitButton: this.$i18n.t('Session.SignUp.OPSubmitButton'),
          OTAlreadyHaveAccount: this.$i18n.t(
            'Session.SignUp.OTAlreadyHaveAccount'
          ),
          OTToLogin: this.$i18n.t('Session.SignUp.OTToLogin'),
          OTPasswordRules: this.$i18n.t(
            'Session.ResetPassword.OTPasswordRules'
          ),
          OTPasswordRulesTs: this.$i18n.t(
            'Session.ResetPassword.OTPasswordRulesTs'
          ),
          OTEmailHaveExist: this.$i18n.t('Session.SignUp.OTEmailHaveExist'),
          TheServerIsBusy: this.$i18n.t('TheServerIsBusy'),
          emailAddress: this.$i18n.t('emailAddress'),
          passwordTitle: this.$i18n.t('passwordTitle'),
          OTRequired: this.$i18n.t('Session.SignUp.OTRequired'),
          OTOrganizationText: this.$i18n.t('Session.SignUp.OTOrganizationText'),
          OTOrganization: this.$i18n.t('Session.SignUp.OTOrganization')
        }
      };
    },
    mounted() {
      let signUpEmail = this.$route.query.email;
      signUpEmail ? (this.email = signUpEmail) : (this.email = '');
      let urlPath = window.location;
      let urlph = `${urlPath.protocol}//${urlPath.host}`;
      this.urlPaths = this.$i18n.t('Session.SignUp.OTHave',[
          `<a href="${urlph}/legal/privacyPolicy" target="_blank">${this.$i18n.t('Session.Login.OTPrivacyPolicy')}</a>`,
          `<a href="${urlph}/legal/TermsOfService" target="_blank">${this.$i18n.t('Session.Login.OTTermsOfService')}</a>`
      ])
    },
    methods: {
      agreeChange(){
        this.AgreeToPrivacyPolicy === true ? this.disabledOff = false : this.disabledOff = true
      },
      async signup(event) {
        event.srcElement.blur();
        let v = this.$validator;
        let validateFailed = !(await v.validate());
        let errors = await v.errors.all();
        let errorDom = this.$refs.errorDom;
        errorDom.open = validateFailed;
        if (validateFailed) {
          switch (v.errors.items[0].field) {
            case 'email':
              if (v.errors.items[0].rule === 'required') {
                errorDom.errorText = this.$i18n.t(
                  'Session.SignUp.OTEmailEmpty'
                );
              }
              if (v.errors.items[0].rule === 'email') {
                errorDom.errorText = this.$i18n.t(
                  'Session.SignUp.OTEmaillayout'
                );
              }
              if (v.errors.items[0].rule === 'regex') {
                errorDom.errorText = this.$i18n.t(
                  'Session.SignUp.OTEmailchinese'
                );
              }
              break;
            case 'password':
              if (v.errors.items[0].rule === 'required') {
                errorDom.errorText = this.$i18n.t(
                  'Session.SignUp.OTPasswordEmpty'
                );
              }
              if (v.errors.items[0].rule === 'alpha_dash') {
                errorDom.errorText = this.trans.OTPasswordRulesTs;
              }
              if (
                v.errors.items[0].rule === 'regex' ||
                v.errors.items[0].rule === 'min'
              ) {
                errorDom.errorText = this.trans.OTPasswordRules;
              }
              break;
          }
          return;
        }
        let data = {
          email: this.email,
          password: this.password
        };
        try {
          let data = {
            email: this.email,
            password: this.password,
            organization: this.organization
          };
          await this.$store.dispatch('session/signup', data);
          await this.$store.dispatch('session/login', data);
          await this.$store.commit('session/signupSuccess');
        } catch (error) {
          if (error.response.status === 422) {
            switch (error.response.data.message) {
              case 'EmailHaveExist':
                errorDom.errorText = this.trans.OTEmailHaveExist;
                break;
              default:
                errorDom.errorText = this.trans.TheServerIsBusy;
                break;
            }
          } else {
            errorDom.errorText = this.trans.TheServerIsBusy;
          }
          errorDom.open = true;
        }
      }
    }
  };
</script>
