import _ from 'lodash';

/**
 * Under a given scale, the cursor starting position and cursor current position are used to calculate the position where the "element" should be based on the given initial "element" starting position.
 * @param {*} scale Scale-up ratio
 * @param {*} domPositionOfBeginning Location of the Element at the Beginning of Drag
 * @param {*} cursorPositionOfBeginning The cursor position at the start of the drag
 * @param {*} CursorPositionOfCurrent Current cursor location
 */
function computeThePositionInScaledGraph(
  scale,
  domPositionOfBeginning,
  cursorPositionOfBeginning,
  cursorPositionOfCurrent
) {
  let _scale = parseFloat(scale);
  let positionArray = [
    domPositionOfBeginning,
    cursorPositionOfBeginning,
    cursorPositionOfCurrent
  ];
  if (
    !_.isNumber(_scale) ||
    parseFloat(_scale) === 0 ||
    _.some(positionArray, p => !(_.isNumber(p.x) && _.isNumber(p.y)))
  ) {
    throw new Error('arguments are invalid');
  }

  let domPositionOfCurrent = {
    x: parseInt(
      (cursorPositionOfCurrent.x - cursorPositionOfBeginning.x) / _scale +
        domPositionOfBeginning.x
    ),
    y: parseInt(
      (cursorPositionOfCurrent.y - cursorPositionOfBeginning.y) / _scale +
        domPositionOfBeginning.y
    )
  };

  return domPositionOfCurrent;
}

export default computeThePositionInScaledGraph;
