<template>
  <div v-show="open" class="session-error">
    <p class="my-0">
      {{ errorText }}
    </p>
    <i class="mdi mdi-close-circle-outline" @click="closeError"></i>
  </div>
</template>

<script>
  export default {
    name: 'Error',
    data() {
      return {
        errorText: '',
        open: false
      };
    },
    methods: {
      closeError() {
        this.open = false;
      }
    }
  };
</script>
