<template>
  <div class="home-sign-up">
    <div class="home-sign-up-box" :class="{ active: emailFocusActive }">
      <div class="sign-up-item">
        <div class="sign-up-input">
          <input
            v-model="email"
            type="text"
            :placeholder="trans.email"
            @keyup.enter="signup"
            @focus="emailFocusBlur(true)"
            @blur="emailFocusBlur(false)"
          />
        </div>
      </div>
      <div class="sign-up-btn">
        <div class="sign-up-input">
          <v-btn class="ma-0" depressed large color="primary" @click="signup">
            <span class="long">{{trans.OTSignUpText}}</span>
            <span class="short">{{trans.OTSignUp}}</span>
          </v-btn>
        </div>
      </div>
    </div>
    <div class="small-text mt-3 home-error-box" v-if="errorShow">
      <p>{{ errorText }}</p>
    </div>
    <div class="small-text mt-3" v-else>
      <p>{{ trans.OTHomeFooterHint }}</p>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'homeSignUp',
    created() {
      let v = this.$validator;

      v.attach({
        name: 'email',
        alias: this.trans.email,
        getter: () => this.email,
        rules: {
          required: true,
          email: true,
          regex: /^[^\u4e00-\u9fa5]+$/
        }
      });
    },
    data() {
      return {
        email: '',
        password: '',
        errorText: '',
        organization: '',
        errorShow: false,
        emailFocusActive: false,
        trans: {
          email: this.$i18n.t('email'),
          password: this.$i18n.t('password'),
          OTPasswordRules: this.$i18n.t(
            'Session.ResetPassword.OTPasswordRules'
          ),
          OTPasswordRulesTs: this.$i18n.t(
            'Session.ResetPassword.OTPasswordRulesTs'
          ),
          OTEmailHaveExist: this.$i18n.t('Session.SignUp.OTEmailHaveExist'),
          TheServerIsBusy: this.$i18n.t('TheServerIsBusy'),
          emailAddress: this.$i18n.t('emailAddress'),
          passwordTitle: this.$i18n.t('passwordTitle'),
          OTOrganization: this.$i18n.t('Session.SignUp.OTOrganization'),
          OTRequired: this.$i18n.t('Session.SignUp.OTRequired'),
          OTOrganizationText: this.$i18n.t('Session.SignUp.OTOrganizationText'),
          OTSignUpText: this.$i18n.t('Session.HomePage.OTSignUpText'),
          OTHomeFooterHint: this.$i18n.t('Session.HomePage.OTHomeFooterHint'),
          OTSignUp: this.$i18n.t('Session.HomePage.OTSignUp')
        }
      };
    },
    methods: {
      async signup(event) {
        event.srcElement.blur();
        let v = this.$validator;
        let validateFailed = !(await v.validate());
        let errors = await v.errors.all();

        this.errorShow = validateFailed;
        if (validateFailed) {
          this.errorText = v.errors.items[0].msg;
          return;
        }
        let data = {
          email: this.email,
          password: this.password
        };
        this.$router.push({
          path: '/signup',
          query: {
            email: this.email
          }
        });
      },
      emailFocusBlur(status) {
        this.emailFocusActive = status;
      }
    }
  };
</script>
