import Vue from 'vue';

function refreshUserSelectedGraph(state, payload) {
  state.userSelectedGraph = payload;
}

/**
 * Update the status information of all sketchboards of local cache users and add project experiment and ideas to the target project
 */
function refreshUserGraphList(state, payload) {
  state.userGraphList = payload.data;
  let project = this.getters['projectCollection/project'](payload.projectId);
  let sb = Symbol('orderIndex');
  _.intersectionBy(project.graphs, payload.data, 'id').forEach(g => {
    let e = payload.data.find(x => x.id === g.id);
    Vue.set(g, 'orderIndex', e.orderIndex);
    g[sb] = true;
    e[sb] = true;
  });

  _.vueRemove(project.graphs, x => !x[sb]);
  _.vueRemove(payload.data, x => !x[sb]);
}

/**
 * Update the status information of a local cache user's Sketchpad
 */
function refreshUserGraph(state, graph) {
  state.userGraphList = graph;
}

/**
 * Update Selected Graph and Sort of Local Cache User's Current Graph
 */
function refreshSelectedGraphId(state, payload) {
  this.state.userGraph.userSelectedGraph.selectedGraph = payload.selectedGraph;
}

function refreshSelectedGraphOrderIndex(state, payload) {
  let project = this.getters['projectCollection/project'](payload.projectId);
  if (!_.isEmpty(payload.sortedGraphIdList)) {
    payload.sortedGraphIdList.forEach((id, index) => {
      let g = project.graphs.find(x => x.id === id);
      g.orderIndex = index;
    });
  }
}

export default {
  refreshUserSelectedGraph,
  refreshUserGraphList,
  refreshUserGraph,
  refreshSelectedGraphId,
  refreshSelectedGraphOrderIndex
};
