import api from 'Api';

const state = {};

const getters = {};

const mutations = {};

const actions = {
  getOssPolicies(context, fileData) {
    return new Promise((resolve, rejiect) => {
      api
        .post('/upload/policies', fileData)
        .then(response => {
          resolve(response.data);
        })
        .catch(error => {
          rejiect(error);
        });
    });
  },
  getAsset(context, assetId) {
    return new Promise((resolve, rejiect) => {
      api
        .get('/upload/assets/' + assetId)
        .then(response => {
          resolve(response.data);
        })
        .catch(error => {
          rejiect(error);
        });
    });
  }
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};
