<template>
  <div class="contributors-tips-mouseover">
    <div
      class="ideas-item-hover"
      style="width: auto!important;min-width: 18rem;"
    >
      <div class="ideas-content-box project-des-tooltip">
        <div class="contributors-content">
          <div class="contributors-ico">
            <user-avata-circle
              :user-avata="contributorsObj.avatar"
            ></user-avata-circle>
            <div class="creator" v-if="creatorShow">
              {{ trans.OTTheCreator }}
            </div>
          </div>
          <div class="contributors-text">
            <div class="name">{{ contributorsObj.username }}</div>
            <div class="email">{{ contributorsObj.email }}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex';
  import userAvataCircle from './userAvataCircle';

  export default {
    name: 'contributorsIconTips',
    components: {
      userAvataCircle
    },
    props: {
      contributorsObj: {
        type: Object
      }
    },
    data() {
      return {
        trans: {
          OTTheCreator: this.$i18n.t(
            'ProjectManage.InviteTeammatesDialog.OTTheCreator'
          )
        }
      };
    },
    computed: {
      ...mapGetters({
        projectInfo: 'projectDetail/project'
      }),
      creatorShow() {
        return this.projectInfo.creator === this.contributorsObj.id
          ? true
          : false;
      }
    },
    mounted() {}
  };
</script>
