<template>
  <div class="project-card">
    <div class="header">
      <div class="team-members">
        <div
          v-for="(ctr, index) in contributors"
          :key="ctr.id"
          class="team-member"
          :style="{ 'z-index': 99 - index }"
        >
          <user-avata-circle
            v-if="index < 3"
            :user-name="ctr.username"
            :user-avata="ctr.avatar"
          ></user-avata-circle>
        </div>
      </div>
      <div class="project-create-time lighten-3">
        <span class="mdi mdi-clock-outline mr-1"></span>
        <span style="font-size: 0.875rem" v-text="createDate"></span>
      </div>
    </div>
    <div class="project-abstract">
      <h2 @click="intoProjectPage" v-text="projectName"></h2>
      <div class="preview-html" @click="intoProjectPage" ref="previewHtml">
        <div v-html="projectAbstract" ></div>
      </div>
    </div>
    <div class="project-progress">
      <div class="desc">
        <div>
          <span v-text="trans.OTFinshedAmount"></span>
          <span>/</span>
          <span v-text="trans.OTTotalAmount"></span>
        </div>
        <div>
          <span
            class="success--text"
            style="font-size: 1rem; font-weight:700"
            v-text="finished"
          ></span>
          <span>/</span>
          <span v-text="total"></span>
        </div>
      </div>
      <v-progress-linear
        v-model="precentProgress"
        background-color="grey lighten-4"
        :color="progressBarColor"
        height="4"
      ></v-progress-linear>
    </div>
    <div class="tool-box-card grey--text text--darken-2 new-project-card">
      <slot name="settings-update-project"> </slot>
      <button @click="deleteProjectDialogIsOpenShow">
        <i class="mdi mdi-trash-can-outline"></i>
      </button>
      <public-prompt-dialog
        v-show="deleteProjectDialogIsOpen"
        :public-prompt-dialog-show.sync="deleteProjectDialogIsOpen"
        @deleteButton="deleteProject"
      >
        <div slot="title">
          {{ deleteProjectInfo.title }}
        </div>
        <div slot="content">
          <div
            class="delete-tips"
            v-if="deleteProjectInfo.primaryTitle"
            v-html="deleteProjectInfo.primaryTitle"
          ></div>
          <div class="delete-the-input">
            <v-text-field
              v-model="deleteName"
              autofocus
              ref="deleteInput"
              :rules="deleteRules"
              validate-on-blur
              required
              color="orange"
              :label="deleteProjectInfo.placeholder"
              height="40px"
              outline
            ></v-text-field>
          </div>
        </div>
      </public-prompt-dialog>
    </div>
  </div>
</template>

<script>
  import userAvataCircle from '../../components/userAvataCircle';
  import publicPromptDialog from '../../components/publicPromptDialog';
  import descriptionDialog from '../projectPage/descriptionDialog';
  import { mapGetters } from 'vuex';

  export default {
    name: 'ProjectCard',
    components: {
      userAvataCircle,
      publicPromptDialog,
      descriptionDialog
    },
    props: {
      project: Object
    },
    computed: {
      ...mapGetters(['permissions']),
      ...mapGetters({
        userInfo: 'session/user',
        globalAlertShow: 'settings/globalAlertShow'
      }),
      precentProgress: function() {
        let finished = Math.abs(this.project.progress.finished);
        let total = Math.abs(this.project.progress.total);
        let progress = numeral(finished / (total < 1 ? 1 : total));
        let isNumber = numeral.validate(String(progress.value()));
        return isNumber ? progress.value() * 100 : 0;
      },
      progressBarColor: function() {
        let index = Math.floor(
          (this.precentProgress * this.progressBarColors.length) / 100
        );
        index =
          index >= this.progressBarColors.length
            ? this.progressBarColors.length - 1
            : index;
        return this.progressBarColors[index].color;
      },
      contributors: function() {
        let contributorsList = this.project.contributors;
        return _.orderBy(contributorsList, ['joinedAt']);
      },
      createDate() {
        let createTime = moment(this.project.createdAt);
        if (!createTime.isValid()) {
          return 'None';
        }
        return moment().year() === createTime.year()
          ? createTime.format('MMM Do')
          : createTime.format('YYYY-MMM-Do');
      },
      createTime: function() {
        let createTime = moment(this.project.createdAt);
        return createTime.isValid() ? createTime.format('HH:mm') : 'None';
      },
      projectName: function() {
        return this.project.title;
      },
      projectAbstract: function() {
        let reg = /<(?!a|\/?a|em|\/?em|p|\/?p|strong|\/?strong).*?>/g;
        let str = this.project.abstract.editorHtml.replace(reg, '');
        return str;
      },
      finished: function() {
        return this.project.progress.finished;
      },
      total: function() {
        return this.project.progress.total;
      },
      updateShow() {
        let projectMine = {};
        this.project.contributors.forEach(c => {
          if (c.id === this.userInfo.id) {
            projectMine = c;
          }
        });
        let ability = projectMine.ability === 'Update' ? true : false;
        return ability;
      }
    },
    data() {
      return {
        progressBarColors: [
          {
            color: 'red darken-1',
            range: '[0,20)'
          },
          {
            color: 'amber darken-2',
            range: '[20,40)'
          },
          {
            color: 'light-blue lighten-1',
            range: '[40,60)'
          },
          {
            color: 'teal lighten-2',
            range: '[60,80)'
          },
          {
            color: 'green accent-4',
            range: '[80,100]'
          }
        ],
        trans: {
          OTFinshedAmount: this.$i18n.t(
            'ProjectManage.ProjectCard.OTFinshedAmount'
          ),
          OTTotalAmount: this.$i18n.t('ProjectManage.ProjectCard.OTTotalAmount')
        },
        deleteProjectDialogIsOpen: false,
        deleteProjectInfo: {},
        // descDialogDisplay: false,
        projectInfoContent: {},
        deleteName: '',
        deleteRules: [
          v =>
            v.trim() === this.deleteProjectInfo.projectName ||
            this.$i18n.t('PublicPromptDialog.OTUnknownName')
        ]
      };
    },
    watch: {
      updateShow: function(newValue, oldValue) {
        if (newValue === false) {
          this.$store.commit(
            'projectCollection/descDialogDisplayShowFun',
            false
          );
          this.$store.commit(
            'projectCollection/descDialogDisplayShowToIdFun',
            ''
          );
        }
      }
    },
    methods: {
      deleteProjectDialogIsOpenShow() {
        let type = this.project.creator === this.userInfo.id;
        if (type) {
          this.deleteProjectInfo = {
            id: this.project.id,
            title: this.$i18n.t(
              'ProjectManage.ProjectCard.OTDeleteDialogTitle'
            ),
            content: this.$i18n.t('ProjectManage.ProjectCard.OTDeleteHint', [
              this.projectName
            ]),
            primaryTitle: this.$i18n.t(
              'PublicPromptDialog.OTProjectPrimaryTitle',
              [this.projectName]
            ),
            placeholder: this.$i18n.t('PublicPromptDialog.OTPlaceholder', [
              'project'
            ]),
            projectName: this.projectName
          };
          this.deleteProjectDialogIsOpen = true;
        } else {
          this.$store.commit('settings/showGlobalSnackbar', {
            text: this.$i18n.t('ProjectManage.ProjectCard.OTCreatorDetele')
          });
        }
      },
      // delete Project
      deleteProject() {
        if (this.$refs.deleteInput.validate(true)) {
          this.$store.dispatch(
            'projectCollection/deleteProject',
            this.deleteProjectInfo.id
          );
          this.deleteProjectDialogIsOpen = false;
        }
      },
      intoProjectPage() {
        this.$router.push('/projects/' + this.project.id);
      }
    }
  };
</script>
