<template>
  <div class="session-wrapper reset-password-without-original-password">
    <div class="session-header">
      <logo-box></logo-box>
    </div>

    <div class="session-main">
      <div class="session-content">
        <p v-once class="function-hint" v-text="trans.OTPasswordRules"></p>
        <error-box ref="errorDom"></error-box>
        <input
          v-model="newPassword"
          class="new-password"
          type="password"
          :placeholder="trans.newPassword"
          @keyup.enter="reset"
        />
        <input
          v-model="confirmedPassword"
          class="confirmed-password"
          type="password"
          :placeholder="trans.confirmedPassword"
          @keyup.enter="reset"
        />
        <button
          v-once
          class="submit-button"
          @click="reset"
          v-text="trans.OPSubmitButton"
        ></button>
      </div>
    </div>
    <div class="session-footer">
      <contact-us class="back-white"></contact-us>
    </div>
  </div>
</template>

<script>
  import logoBox from '../../components/Widgets/logo';
  import errorBox from '../../components/Widgets/session/error';
  import contactUs from '../../components/contactUs';

  export default {
    name: 'ResetPasswordWithoutOriginalPassword',
    created() {
      const passwordRules = {
        required: true,
        alpha_dash: true,
        min: 6,
        regex: /^.*(\D\d)|(\d\D).*$/
      };

      let v = this.$validator;
      let newField = v.attach({
        name: 'newPassword',
        alias: this.trans.newPassword,
        getter: () => this.newPassword,
        rules: Object.assign(
          {
            confirmed: 'confirmedPassword'
          },
          passwordRules
        )
      });

      let confirmField = v.attach({
        name: 'confirmedPassword',
        alias: this.trans.confirmedPassword,
        getter: () => this.confirmedPassword,
        rules: passwordRules
      });

      newField.dependencies.push({
        name: 'confirmed',
        field: confirmField
      });
    },
    components: {
      logoBox,
      errorBox,
      contactUs
    },
    data() {
      return {
        newPassword: '',
        confirmedPassword: '',
        trans: {
          newPassword: this.$i18n.t('password'),
          confirmedPassword: this.$i18n.t('confirmedPassword'),
          OTTitle: this.$i18n.t('Session.ResetPassword.OTTitle'),
          OTPasswordRules: this.$i18n.t(
            'Session.ResetPassword.OTPasswordRules'
          ),
          OTPasswordRulesTs: this.$i18n.t(
            'Session.ResetPassword.OTPasswordRulesTs'
          ),
          OTPasswordFormatIsIncorrect: this.$i18n.t(
            'Session.ResetPassword.OTPasswordFormatIsIncorrect'
          ),
          OTResetPasswordLinkInvalid: this.$i18n.t(
            'Session.ResetPassword.OTResetPasswordLinkInvalid'
          ),
          OPSubmitButton: this.$i18n.t('Session.ResetPassword.OPSubmitButton'),
          TheServerIsBusy: this.$i18n.t('TheServerIsBusy')
        }
      };
    },
    methods: {
      async reset(event) {
        event.srcElement.blur();
        let v = this.$validator;
        let validateFailed = !(await v.validate());
        let errors = await v.errors.all();
        let errorDom = this.$refs.errorDom;
        errorDom.open = validateFailed;
        if (validateFailed) {
          switch (v.errors.items[0].rule) {
            case 'confirmed':
              errorDom.errorText = errors[0];
              break;
            case 'alpha_dash':
              errorDom.errorText = this.trans.OTPasswordRulesTs;
              break;
            default:
              errorDom.errorText = this.trans.OTPasswordRules;
              break;
          }
          return;
        }

        try {
          let data = {
            emailId: this.$route.params.emailId,
            newPassword: this.newPassword
          };
          await this.$store.dispatch(
            'emails/resetPasswordWithoutOriginalPassword',
            data
          );
        } catch (error) {
          if (error.response.status === 403) {
            switch (error.response.data) {
              case 'ResetPasswordLinkInvalid':
                errorDom.errorText = this.trans.OTResetPasswordLinkInvalid;
                break;
              default:
                errorDom.errorText = this.trans.OTResetPasswordLinkInvalid;
                break;
            }
          } else if (error.response.status === 404) {
            this.$router.push('/error/404');
          } else {
            errorDom.errorText = this.trans.TheServerIsBusy;
          }
          errorDom.open = true;
        }
      }
    }
  };
</script>
