/**
 * Update the idea list of local projects
 */
function refreshIdeaList(state, payload) {
  let project = this.getters['projectCollection/project'](payload.projectId);
  if (typeof project !== 'undefined') {
    project.ideas.splice(0, project.ideas.length, ...payload.ideaList);
  }
}

/**
 * Update local project ideas
 */
function refreshIdea(state, payload) {
  let project = this.getters['projectCollection/project'](payload.projectId);
  if (!project) {
    return;
  }
  _.vueRemove(project.ideas, x => x.id === payload.idea.id);
  project.ideas.push(payload.idea);
}

function ideasCreateEditDialogDisplayShowFun(state, payload) {
  state.ideasCreateEditDialogDisplayShow = payload;
}

/**
 * Delete an idea data from the local cache
 */
function deleteIdea(state, payload) {
  let project = this.getters['projectCollection/project'](payload.projectId);
  let index = project.ideas.findIndex(x => x.id === payload.ideaId);
  project.ideas.splice(index, 1);
}

/**
 * socket
 */
function SOCKET_updateProjectIdeas(state, payload) {
  let projectId = payload.projectId;
  let project = this.getters['projectCollection/project'](payload.projectId);
  let currentRouterName = window.getVM().$router.history.current.name;
  let selectedProjectId = this.getters['projectDetail/selectedProjectId'];
  let ideasId = payload.ideasId;
  if (currentRouterName === 'projectGraph' && projectId === selectedProjectId) {
    if (payload.action === 'delete')
      _.vueRemove(project.ideas, x => x.id === ideasId);
    if (payload.action === 'get') {
      _.vueRemove(project.ideas, x => x.id === ideasId);
      project.ideas.push(payload.data);
    }
  }
}

export default {
  refreshIdeaList,
  refreshIdea,
  deleteIdea,
  SOCKET_updateProjectIdeas,
  ideasCreateEditDialogDisplayShowFun
};
