export const virtualNodeId = -1;

export const nodeHeight = 110;
export const nodeWidth = 250;
export const nodeContainerPadding = [0, 0, 0, 0];
export const nodeContainerBorderWidth = [1, 1, 1, 1];
export const nodeContainerBoxHeight =
  nodeHeight +
  nodeContainerPadding[0] +
  nodeContainerPadding[2] +
  nodeContainerBorderWidth[0] +
  nodeContainerBorderWidth[2];
export const nodeContainerBoxWidth =
  nodeWidth +
  nodeContainerPadding[1] +
  nodeContainerPadding[3] +
  nodeContainerBorderWidth[1] +
  nodeContainerBorderWidth[3];
export const edgeXOffset =
  nodeWidth / 2 + nodeContainerPadding[3] + nodeContainerBorderWidth[3];
export const edgeYOffset =
  nodeHeight / 2 + nodeContainerPadding[3] + nodeContainerBorderWidth[3];

export const edgeLocationFields = ['ctlPoint1', 'ctlPoint2'];
export const edgeStartPointRadius = 3;
export const edgeEndPointArrowSize = 5;

export const controlLineLength = 100;

export const graphEdgeTypes = ['Motivate', 'Inherent'];

// The anchor configuration of a node, X and Y represent the percentage of the location of the anchor in the X and Y directions on the node from the starting point, respectively.
export const nodeAnchors = {
  '0': {
    x: 0.5,
    y: 0
  },
  '1': {
    x: 0.5,
    y: 1
  },
  '2': {
    x: 0,
    y: 0.5
  },
  '3': {
    x: 1,
    y: 0.5
  }
};

// The default starting anchor is the middle position at the bottom of the node
export const edgeStartAnchorDefalut = 1;

// The default end anchor is the middle position at the top of node
export const edgeEndAnchorDefalut = 0;
