<template>
  <div v-if="projectInfo" class="project-page">
    <div
      ref="projectDescWidth"
      class="project-description"
      @mouseleave="mouseleaveMenu"
    >
      <div class="project-scrolly">
        <div class="project-desc-content">
          <div class="project-item">
            <div class="project-title">
              <div class="return-index" @click="goToRouter('/')">
                <div class="iconfont icon-fanhui1"></div>
              </div>
              <div class="project-name" v-text="projectName"></div>
            </div>
            <div
              class="project-info"
              ref="projectInfoScollTop"
              style="position: relative"
              :class="{ active: projectDescSmall }"
            >
              <!-- info -->
              <div class="project-info-item info1">
                <div class="icon">
                  <span
                    @mouseenter="mouseenterMenu"
                    class="iconfont icon-cengmianfangxiang icon-font"
                  ></span>
                </div>
                <div class="info-content">
                  <div class="item-menu">
                    <div class="text">
                      ({{ trans.OTCreateAt }} {{ createDate }})
                    </div>
                    <div class="edit-icon">
                      <v-btn
                        v-if="updateShow"
                        icon
                        slot="activator"
                        class="icon-edit"
                        @click="descDialogDisplayOpen"
                        :title="trans.Edit"
                      >
                        <i
                          class="mdi mdi-circle-edit-outline"
                          style="font-size: 1rem"
                        ></i>
                      </v-btn>
                    </div>
                  </div>
                  <div
                    class="n-content"
                    style="padding-right: 12px;font-size: .875rem!important;"
                    v-html="abstract"
                  ></div>
                </div>
              </div>
              <!-- ideas -->
              <div class="project-info-item ideas">
                <div class="icon">
                  <span
                    class="mdi mdi-pulse icon-font"
                    @mouseenter="mouseenterMenu"
                    style="font-size: 1.2rem;"
                  ></span>
                </div>
                <div class="info-content">
                  <div class="item-menu">
                    <div class="text text-title">Ideas</div>
                    <div class="edit-icon">
                      <v-btn
                        v-if="updateShow"
                        icon
                        large
                        slot="activator"
                        class="icon-edit"
                        @click.stop="createIdeas"
                      >
                        <i class="mdi mdi-plus" style="font-size: 1.2rem"></i>
                      </v-btn>
                    </div>
                  </div>
                  <div class="n-content">
                    <div class="item-list">
                      <ul class="list-content stack-edit-container pa-0">
                        <div>
                          <li
                            class="ideas-list"
                            v-for="list in ideas"
                            :key="list.id"
                          >
                            <div class="mb-1 mt-1">
                              <div class="item-ideas-list">
                                <div class="ideas-number">
                                  <div class="number mr-2 text-center">
                                    #{{ list.code }}
                                  </div>
                                </div>
                                <div
                                  class="ideas-content"
                                  @click="ideasContent(list.id)"
                                >
                                  {{ list.title }}
                                </div>
                                <div class="ideas-btn" v-if="updateShow">
                                  <v-btn
                                    icon
                                    small
                                    slot="activator"
                                    class="icon-edit"
                                    @click.stop="editIdeas(list)"
                                  >
                                    <i class="mdi mdi-circle-edit-outline"></i>
                                  </v-btn>
                                  <v-btn
                                    icon
                                    small
                                    slot="activator"
                                    class="icon-edit"
                                    @click.stop="deleteIdeasOpen(list)"
                                  >
                                    <i class="mdi mdi-trash-can-outline"></i>
                                  </v-btn>
                                </div>
                              </div>
                            </div>
                          </li>
                        </div>
                      </ul>
                    </div>
                  </div>
                </div>
              </div>
              <!-- TeamMembers -->
              <div class="project-info-item team-members">
                <div class="icon">
                  <span
                    class="iconfont icon-Icon-zhuanjiatuanduiguanli icon-font"
                    @mouseenter="mouseenterMenu"
                    style="font-size: 1rem;font-weight: bold;"
                  ></span>
                </div>
                <div class="info-content">
                  <div class="item-menu">
                    <div
                      class="text text-title"
                      v-text="trans.TeamMembers"
                    ></div>
                    <div class="edit-icon">
                      <v-btn
                        v-if="updateShow"
                        icon
                        large
                        slot="activator"
                        class="icon-edit"
                        @click.stop="inviteTeammatesDialog"
                      >
                        <i
                          class="mdi mdi-circle-edit-outline"
                          style="font-size: 1rem"
                        ></i>
                      </v-btn>
                    </div>
                  </div>
                  <div class="n-content">
                    <div class="item-team mt-2">
                      <ul>
                        <li
                          v-for="item in contributors"
                          :key="item.id"
                          class="item-team-li"
                        >
                          <v-tooltip bottom>
                            <template v-slot:activator="{ on }">
                              <div class="team-ico team-ma" v-on="on">
                                <user-avata-circle
                                  v-if="item"
                                  :user-avata="item.avatar"
                                  :user-name="item.username"
                                >
                                </user-avata-circle>
                              </div>
                            </template>
                            <span v-text="item.username"></span>
                          </v-tooltip>
                        </li>
                        <invite-teammates
                          v-model="inviteTeammatesDialogIsOpen"
                          :project="this.projectInfo"
                        ></invite-teammates>
                      </ul>
                      <contributors-icon-tips
                        :contributorsObj="contributorsObj"
                      ></contributors-icon-tips>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          class="project-desc-full"
          @click="projectDescFull"
          :class="{ active: !projectDescSmall }"
        >
          <span></span>
        </div>
      </div>
    </div>
    <div class="project-content" :class="{ active: !projectDescSmall }">
      <router-view ref="proGraphViews"></router-view>
      <div class="project-graphs-tab">
        <div class="graphs-tab-box">
          <draggable v-model="graphs" class="dragArea">
            <template v-for="item in graphs">
              <router-link
                :key="projectInfo.id + item.id"
                replace
                :to="'/projects/' + projectInfo.id + '/graphs/' + item.id"
                tag="div"
                active-class="tab-active"
                class="tab-item pl-2"
                @click.native="tabClick()"
              >
                <graph-tab-input
                  :title="item.title"
                  :graph-id="item.id"
                  :class="{ 'graph-tab-loading-Open': graphTabLoadingOpen }"
                ></graph-tab-input>
                <div class="button-close">
                  <v-btn
                    slot="activator"
                    icon
                    small
                    :disabled="!updateShow"
                    class="ma-0 ml-2 tab-ico tab-delete"
                    @click.stop="deleteGraphOpen(item)"
                  >
                    <i class="mdi mdi-close"></i>
                  </v-btn>
                </div>
              </router-link>
            </template>
          </draggable>
          <v-btn
            v-show="graphs.length < 5 && updateShow"
            slot="activator"
            icon
            small
            class="ma-0 ml-2 mr-2"
            @click.stop="createGraph"
          >
            <i class="mdi mdi-plus"></i>
          </v-btn>
        </div>
      </div>
    </div>

    <div
      class="project-info-small"
      :class="{ active: !projectDescSmall }"
      @click="pageSmall"
    >
      <div class="mdi mdi-page-first"></div>
    </div>

    <!-- description-dialog -->
    <description-dialog
      ref="descDialogDisplay"
      v-show="descDialogDisplay"
      :desc-dialog-display.sync="descDialogDisplay"
      :projectInfoObj="projectInfoContent"
    ></description-dialog>

    <!-- ideasCreateEditDialog -->
    <ideas-create-edit-dialog
      ref="ideasCreateEditDialogDisplay"
      v-show="ideasCreateEditDialogDisplay"
      :projectInfoObj="projectInfoContent"
      :ideasContentObj="ideasContentObj"
    >
    </ideas-create-edit-dialog>

    <public-prompt-dialog
      v-show="publicPromptDialogShow"
      :public-prompt-dialog-show.sync="publicPromptDialogShow"
      @cancelButton="cancelHandle"
      @deleteButton="deleteHandle"
    >
      <div slot="title">
        <span v-text="deleteContent.title"></span>
      </div>
      <div slot="content" v-if="deleteContent.type === 'graph'">
        <div
          class="delete-tips"
          v-if="deleteContent.primaryTitle"
          v-html="deleteContent.primaryTitle"
        ></div>
        <div class="delete-the-input">
          <v-text-field
            v-model="deleteGraphName"
            autofocus
            ref="deleteGraphInput"
            :rules="deleteGraphRules"
            validate-on-blur
            required
            color="orange"
            :label="deleteContent.placeholder"
            height="40px"
            outline
          ></v-text-field>
        </div>
      </div>
      <div
        slot="content"
        v-else
        v-html="deleteContent.content"
        class="public-prompt-dialog-content"
      ></div>
    </public-prompt-dialog>
  </div>
</template>

<script>
import logoBox from '../../components/Widgets/logo';
import userAvataCircle from '../../components/userAvataCircle';
import inviteTeammates from '../projectManage/inviteTeammates';
import descriptionDialog from './descriptionDialog';
import ideasCreateEditDialog from './ideasCreateEditDialog';
import graphTabInput from '../../components/graphTabInput';
import publicPromptDialog from '../../components/publicPromptDialog';
import contributorsIconTips from '../../components/contributorsIconTips';
import { mapGetters, mapState } from 'vuex';
import draggable from 'vuedraggable';

export default {
  name: 'ProjectPage',
  created() {
    this.projectGraphRoute();
  },
  components: {
    logoBox,
    descriptionDialog,
    inviteTeammates,
    graphTabInput,
    publicPromptDialog,
    userAvataCircle,
    draggable,
    contributorsIconTips,
    ideasCreateEditDialog
  },
  data() {
    return {
      firstZ: 0,
      teamIcoDele: false,
      trans: {
        ProjectDescription: this.$i18n.t('ProjectPage.ProjectDescription'),
        TeamMembers: this.$i18n.t('ProjectPage.TeamMembers'),
        ProblemDefinitionTitle: this.$i18n.t(
          'ProjectPage.ProblemDefinitionTitle'
        ),
        EvaluationCriteriaTitle: this.$i18n.t(
          'ProjectPage.EvaluationCriteriaTitle'
        ),
        DataTitle: this.$i18n.t('ProjectPage.DataTitle'),
        ProblemDefinitionText: this.$i18n.t(
          'ProjectPage.ProblemDefinitionText'
        ),
        EvaluationCriteriaText: this.$i18n.t(
          'ProjectPage.EvaluationCriteriaText'
        ),
        DataPlaceholderText: this.$i18n.t('ProjectPage.DataPlaceholderText'),
        DataText: this.$i18n.t('ProjectPage.DataText'),
        IdeasText: this.$i18n.t('ProjectPage.IdeasText'),
        Edit: this.$i18n.t('edit'),
        Finish: this.$i18n.t('ProjectPage.Finish'),
        ProjectDashboard: this.$i18n.t('ProjectPage.ProjectDashboard'),
        OTCreateAt: this.$i18n.t('ProjectPage.OTCreateAt')
      },
      projectDescSmall: true,
      rightArrow: true,
      inviteTeammatesDialogIsOpen: false,
      publicPromptDialogShow: false,
      deleteContent: {},
      projectInfoContent: {},
      graphDraggable: [],
      graphSortVersion: 1,
      graphTabLoadingOpen: false,
      contributorsObj: {},
      ideasContentObj: {},
      deleteGraphName: '',
      deleteGraphRules: [
        v =>
          v.trim() === this.deleteContent.graphTitle ||
          this.$i18n.t('PublicPromptDialog.OTUnknownName')
      ],
      pageSmallClick: false
    };
  },
  computed: {
    ...mapGetters({
      project: 'projectDetail/project',
      userGraphList: 'userGraph/userGraphList',
      userAvatar: 'session/userAvatar',
      userName: 'session/userName',
      userEmail: 'session/userEmail',
      user: 'session/user',
      descDialogDisplay: 'projectCollection/descDialogDisplayShowGetters',
      ideasCreateEditDialogDisplay:
        'projectDetail/ideasCreateEditDialogDisplayShowGetters',
      globalAlertShow: 'settings/globalAlertShow',
      updateGraphCursorListGetters: 'projectDetail/updateGraphCursorList'
    }),
    projectInfo() {
      return this.project && this.project.id === this.$route.params.projectId
        ? this.project
        : null;
    },
    projectName() {
      return this.projectInfo.title;
    },
    abstract() {
      return this.projectInfo.abstract.previewHtml;
    },
    createDate() {
      let createTime = moment(this.projectInfo.createdAt);
      if (!createTime.isValid()) {
        return 'None';
      }
      return moment().year() === createTime.year()
        ? createTime.format('MMM Do')
        : createTime.format('YYYY-MMM-Do');
    },
    contributors() {
      let contributorsList = this.projectInfo
        ? this.projectInfo.contributors
        : [];
      return _.orderBy(contributorsList, ['joinedAt']);
    },
    graphs: {
      get() {
        if (!(this.graphSortVersion > 0 && this.projectInfo.graphs)) {
          return [];
        }
        return _.orderBy(this.projectInfo.graphs, 'orderIndex');
      },
      set(value) {
        let payload = {
          projectId: this.projectInfo.id,
          sortedGraphIdList: value.map(x => x.id)
        };
        if (this.updateShow) {
          this.$store.dispatch(
            'userGraph/refreshSelectedGraphOrderIndex',
            payload
          );
          this.graphSortVersion++;
        }
      }
    },
    ideas() {
      return _.orderBy(this.projectInfo.ideas, 'id');
    },
    updateShow() {
      let projectMine = {};
      this.contributors.forEach(c => {
        if (c.id === this.user.id) {
          projectMine = c;
        }
      });
      let ability = projectMine.ability === 'Update' ? true : false;
      return ability;
    }
  },
  watch: {
    updateShow: function(newValue, oldValue) {
      if (newValue === false) {
        this.$store.commit('projectCollection/descDialogDisplayShowFun', false);
        this.$store.commit(
          'projectCollection/descDialogDisplayShowToIdFun',
          ''
        );
        this.$store.commit(
          'projectDetail/ideasCreateEditDialogDisplayShowFun',
          false
        );
        this.inviteTeammatesDialogIsOpen = false;
        this.$refs.proGraphViews.nodeDialogDisplay = false;
      }
    }
  },
  beforeRouteLeave(to, from, next) {
    this.$store.commit('projectCollection/descDialogDisplayShowFun', false);
    this.$store.commit('projectCollection/descDialogDisplayShowToIdFun', '');
    this.$store.commit(
      'projectDetail/ideasCreateEditDialogDisplayShowFun',
      false
    );
    this.$store.dispatch('projectCollection/setProjectFocus', {});
    next();
  },
  mounted() {
    window.onbeforeunload = null;
    this.$nextTick(() => setTimeout(() => ++this.graphSortVersion, 16));
    $(document)
      .on('mouseenter', '.contributors-tips-mouseover', function(event) {
        $('.contributors-tips-mouseover').show(0);
      })
      .on('mouseleave', '.contributors-tips-mouseover', function(event) {
        $('.contributors-tips-mouseover').hide(0);
      });
  },
  methods: {
    mouseenterMenu() {
      if (!this.pageSmallClick) return;
      let domWidth = this.$refs.projectDescWidth;
      domWidth.style.width = '20.75rem';
      this.projectDescSmall = true;
    },
    mouseleaveMenu() {
      if (!this.pageSmallClick) return;
      let domWidth = this.$refs.projectDescWidth;
      domWidth.style.width = '50px';
      this.projectDescSmall = false;
    },
    async projectGraphRoute() {
      await this.$store.dispatch(
        'projectCollection/setProjectById',
        this.$route.params.projectId
      );
      let focusProject = {
        projectId: this.$route.params.projectId
      };
      await this.$store.dispatch(
        'projectCollection/setProjectFocus',
        focusProject
      );

      // 获取选中的 graph
      let selectedGraphInfo = await this.$store.dispatch(
        'userGraph/getUserSelectedGraph',
        this.$route.params.projectId
      );

      let userGraphList = await this.$store.dispatch(
        'userGraph/getUserGraphList',
        this.$route.params.projectId
      );

      let projectGraphList = await this.$store.dispatch(
        'projectDetail/refreshGraphList',
        this.$route.params.projectId
      );

      let sb = Symbol('orderIndex');
      _.intersectionBy(projectGraphList.graphList, userGraphList, 'id').forEach(
        g => {
          let e = userGraphList.find(x => x.id === g.id);
          this.$set(g, 'orderIndex', e.orderIndex);
          g[sb] = true;
          e[sb] = true;
        }
      );

      _.vueRemove(projectGraphList.graphList, x => !x[sb]);
      _.vueRemove(userGraphList, x => !x[sb]);
      let selectedGraph = userGraphList.find(
        x => x.id === selectedGraphInfo.selectedGraph
      );
      this.graphSortVersion++;
      let selectedGraphNum = 0;
      if (selectedGraph === undefined) {
        selectedGraphNum = userGraphList[0].id;
        let payload = {
          projectId: this.projectInfo.id,
          selectedGraph: selectedGraphNum
        };
        await this.$store.dispatch('userGraph/refreshSelectedGraphId', payload);
      } else {
        selectedGraphNum = selectedGraphInfo.selectedGraph;
      }

      if (selectedGraph && selectedGraph.transform.z === 1) {
        this.$store.commit('settings/firstToProject', true);
      }

      let payload = {
        projectId: this.$route.params.projectId,
        selectedGraph: selectedGraphNum
      };

      await this.$store.dispatch('userGraph/refreshSelectedGraphId', payload);

      this.$router.replace(
        '/projects/' + this.project.id + '/graphs/' + selectedGraphNum
      );
    },
    hidedescDialog() {
      $('body').removeClass('edit-experment-class-1024');
      let dialogWidth = '';
      this.$store.commit('projectDetail/resizeDialogWidthFun', dialogWidth);
      this.$refs.proGraphViews.hideDesc();
    },
    goToRouter(to) {
      this.$router.push(to);
    },
    ideasContent(item) {
      let projectId = this.projectInfo.id;
      let routeUrl = this.$router.resolve(
        '/projects/' + projectId + '/ideas/' + item
      );
      window.open(routeUrl.href, '_blank');
    },
    async tabClick() {
      this.updateGraphCursorListGetters.forEach(e => {
        this.$set(e, 'isShow', false);
      });
      // If the experiment edit box exists, verify the experiment and save it automatically
      let createNodeShow = this.$refs.proGraphViews.$refs.createExpDialogDom
        .createNodeShow;
      if (createNodeShow) {
        let result = await this.$refs.proGraphViews.$refs.createExpDialogDom.autoSave();
        if (result) {
          this.hidedescDialog();
        }
      } else {
        this.hidedescDialog();
      }

      this.$refs.proGraphViews.zoomGraphOper = false;

      let payload = {
        projectId: this.projectInfo.id,
        selectedGraph: this.$route.params.selectedGraphId
      };
      this.$store.dispatch('userGraph/refreshSelectedGraphId', payload);
    },
    verProjectLock() {
      this.$store
        .dispatch('projectCollection/addLock', {
          target: `/projects/${this.projectInfo.id}`
        })
        .then(response => {
          this.$store.commit(
            'projectCollection/descDialogDisplayShowFun',
            true
          );
          this.$store.commit(
            'projectCollection/descDialogDisplayShowToIdFun',
            this.projectInfo.id
          );
          this.hidedescDialog();
        })
        .catch(error => {
          if (error.response.status === 423) {
            this.$store.commit('settings/showGlobalSnackbar', {
              text: this.$i18n.t('ProjectPage.OTProjectLock')
            });
          }
        });
    },
    async descDialogDisplayOpen() {
      this.projectInfoContent = _.cloneDeep(this.projectInfo);
      // If the experiment edit box exists, verify the experiment and save it automatically
      let createNodeShow = this.$refs.proGraphViews.$refs.createExpDialogDom
        .createNodeShow;
      if (createNodeShow) {
        let result = await this.$refs.proGraphViews.$refs.createExpDialogDom.autoSave();
        if (result) {
          //判断项目详情是否被锁定
          this.verProjectLock();
        }
      } else {
        //判断项目详情是否被锁定
        this.verProjectLock();
      }
    },

    async createIdeas() {
      // If the experiment edit box exists, verify the experiment and save it automatically
      let createNodeShow = this.$refs.proGraphViews.$refs.createExpDialogDom
        .createNodeShow;
      if (createNodeShow) {
        let result = await this.$refs.proGraphViews.$refs.createExpDialogDom.autoSave();
        if (result) {
          this.projectInfoContent = _.cloneDeep(this.projectInfo);
          let value = {
            fileId: 'Ideas-markdown',
            text: '\n'
          };
          this.$store.commit('stackEdit/patchMarkdown', value);
          this.ideasContentObj = {
            action: 'create',
            content: {
              title: '',
              content: ''
            }
          };
          this.$store.commit(
            'projectDetail/ideasCreateEditDialogDisplayShowFun',
            true
          );
          this.hidedescDialog();
        }
      } else {
        this.projectInfoContent = _.cloneDeep(this.projectInfo);
        let value = {
          fileId: 'Ideas-markdown',
          text: '\n'
        };
        this.$store.commit('stackEdit/patchMarkdown', value);
        this.ideasContentObj = {
          action: 'create',
          content: {
            title: '',
            content: ''
          }
        };
        this.$store.commit(
          'projectDetail/ideasCreateEditDialogDisplayShowFun',
          true
        );
        this.hidedescDialog();
      }
    },
    verIdeaLock(operType, idea) {
      this.$store
        .dispatch('projectCollection/addLock', {
          target: `/projects/${this.projectInfo.id}/ideas/${idea.id}`
        })
        .then(response => {
          if (operType === 'updateIdea') {
            this.$store.commit(
              'projectDetail/ideasCreateEditDialogDisplayShowFun',
              true
            );
          } else {
            this.publicPromptDialogShow = true;
          }
        })
        .catch(error => {
          if (error.response.status === 423) {
            this.$store.commit('settings/showGlobalSnackbar', {
              text: this.$i18n.t('ProjectPage.OTIdeaLock')
            });
          }
        });
    },
    async editIdeas(item) {
      this.hidedescDialog();
      this.projectInfoContent = _.cloneDeep(this.projectInfo);
      let value = {
        fileId: 'Ideas-markdown',
        text: item.content.markdown
      };
      this.$store.commit('stackEdit/patchMarkdown', value);
      this.ideasContentObj = {
        action: 'edit',
        content: _.cloneDeep(item)
      };
      //判断idea是否被锁定
      this.verIdeaLock('updateIdea', item);
    },
    async deleteIdeasOpen(list) {
      this.deleteContent = {
        action: 'delete-ideas',
        ideaId: list.id,
        title: this.$i18n.t('ProjectPage.DeleteIdea'),
        tips: this.$i18n.t('PublicPromptDialog.OTTips'),
        content: this.$i18n.t('ProjectPage.DeleteIdeaContent', [list.title])
      };

      //判断idea是否被锁定
      this.verIdeaLock('deleteIdea', list);
    },
    projectDescFull() {
      let domWidth = this.$refs.projectDescWidth;
      if (domWidth.clientWidth < '350') {
        domWidth.style.width = '50%';
      } else {
        domWidth.style.width = '20.75rem';
      }
    },
    pageSmall() {
      let domWidth = this.$refs.projectDescWidth;
      let projectInfoScollTop = this.$refs.projectInfoScollTop;
      projectInfoScollTop.scrollTop = 0;

      if (domWidth.clientWidth < '50') {
        domWidth.style.width = '20.75rem';
        this.projectDescSmall = true;
        this.pageSmallClick = false;
      } else {
        domWidth.style.width = '50px';
        this.pageSmallClick = true;
        this.projectDescSmall = false;
      }
    },
    async inviteTeammatesDialog() {
      // If the experiment edit box exists, verify the experiment and save it automatically
      let createNodeShow = this.$refs.proGraphViews.$refs.createExpDialogDom
        .createNodeShow;
      if (createNodeShow) {
        let result = await this.$refs.proGraphViews.$refs.createExpDialogDom.autoSave();
        if (result) {
          this.hidedescDialog();
          this.$store
            .dispatch('projectCollection/refreshProject', this.projectInfo.id)
            .then(res => {
              this.inviteTeammatesDialogIsOpen = true;
            });
        }
      } else {
        this.hidedescDialog();
        this.$store
          .dispatch('projectCollection/refreshProject', this.projectInfo.id)
          .then(res => {
            this.inviteTeammatesDialogIsOpen = true;
          });
      }
    },
    deleteGraphOpen(item) {
      if (this.graphs.length === 1) {
        this.$store.commit('settings/showGlobalSnackbar', {
          timeout: 5000,
          text: this.$i18n.t('ProjectPage.LastGraphError')
        });
        return;
      }
      this.deleteContent = {
        action: 'delete-graph',
        type: 'graph',
        title: this.$i18n.t('ProjectPage.DeleteGraph'),
        primaryTitle: this.$i18n.t('PublicPromptDialog.OTGraphPrimaryTitle', [
          item.title
        ]),
        content: this.$i18n.t('ProjectPage.DeleteGraphContent', [item.title]),
        placeholder: this.$i18n.t('PublicPromptDialog.OTPlaceholder', [
          'graph'
        ]),
        graphId: item.id,
        graphTitle: item.title,
        graphInfo: _.cloneDeep(this.graphs)
      };
      this.deleteGraphName = '';
      this.publicPromptDialogShow = true;
    },
    cancelHandle() {
      if (this.deleteContent.action === 'delete-ideas') {
        this.$store.dispatch('projectCollection/unLock', {
          target: `/projects/${this.projectInfo.id}/ideas/${this.deleteContent.ideaId}`
        });
      }
    },
    // delete graph
    deleteHandle() {
      if (
        this.deleteContent.action === 'delete-graph' &&
        this.$refs.deleteGraphInput.validate(true)
      ) {
        let payload = {
          projectId: this.$route.params.projectId,
          graphId: this.deleteContent.graphId
        };
        this.$store
          .dispatch('projectDetail/deleteGraphById', payload)
          .then(res => {
            this.publicPromptDialogShow = false;
            let copyGraphList = this.deleteContent.graphInfo;
            if (
              res.graphId ===
              this.$route.params.selectedGraphId
            ) {
              let index = this.deleteContent.graphInfo.findIndex(
                x => x.id === res.graphId
              );
              let graphId =
                index === 0
                  ? copyGraphList[index + 1].id
                  : copyGraphList[index - 1].id;

              this.$router.replace(
                '/projects/' + res.projectId + '/graphs/' + graphId
              );
              let payloadSelectedGraph = {
                projectId: this.projectInfo.id,
                selectedGraph: graphId
              };
              this.$store
                .dispatch(
                  'userGraph/refreshSelectedGraphId',
                  payloadSelectedGraph
                )
                .then(res => {
                  this.$store.dispatch(
                    'userGraph/refreshUserGraphList',
                    this.project.id
                  );
                });
            }
          });
      }
      if (this.deleteContent.action === 'delete-ideas') {
        let payload = {
          projectId: this.projectInfo.id,
          ideaId: this.deleteContent.ideaId
        };
        this.$store.dispatch('projectDetail/deleteIdea', payload).then(res => {
          _.vueRemove(this.projectInfo.ideas, x => x.id === res.ideaId);
        });
        this.publicPromptDialogShow = false;
      }
    },
    createGraph() {
      if (this.project.graphs.length === 5) {
        this.$store.commit('settings/showGlobalSnackbar', {
          timeout: 5000,
          text: this.$i18n.t('ProjectPage.GraphMost')
        });
        return;
      }
      let payload = {
        projectId: this.$route.params.projectId,
        graph: {
          title: '' + _.now()
        }
      };
      let graphTitleNumberArry = [];
      this.graphs.forEach(el => {
        graphTitleNumberArry.push(el.title);
      });
      this.graphTabLoadingOpen = true;
      this.$store.dispatch('projectDetail/createGraph', payload).then(res => {
        let graphTitle = 'graph' + res.graph.code;

        let patt = /^graph\d+(_(\d+))?$/;

        let graphTitlePatt = patt.exec(graphTitle);

        let zhi = graphTitlePatt[0].split(graphTitlePatt[1]).join('');

        if (graphTitleNumberArry.includes(graphTitle)) {
          let title = zhi + '_' + 1;
          for (let n = 0; n < graphTitleNumberArry.length; n++) {
            if (graphTitleNumberArry.includes(title)) {
              let titlePatt = patt.exec(title);
              title = zhi + '_' + (parseInt(titlePatt[2]) + 1);
            }
          }
          graphTitle = title;
        } else {
          graphTitle = 'graph' + res.graph.code;
        }

        let data = {
          projectId: this.projectInfo.id,
          title: graphTitle,
          graphId: res.graph.id
        };
        this.$store
          .dispatch('projectDetail/refreshGraphBasicInfo', data)
          .then(res => {
            this.graphTabLoadingOpen = false;
          });

        this.$router.replace(
          `/projects/${res.projectId}/graphs/${res.graph.id}`
        );
        let payloadSelectedGraph = {
          projectId: this.projectInfo.id,
          selectedGraph: res.graph.id
        };
        this.$store
          .dispatch('userGraph/refreshSelectedGraphId', payloadSelectedGraph)
          .then(res => {
            this.$store.dispatch(
              'userGraph/refreshUserGraphList',
              this.project.id
            );
          });
      });
    },
    contributorsMouseenter(event, contributors) {
      let leftStyle = event.clientX - event.offsetX;
      let topStyle = event.clientY - event.offsetY;
      $('.contributors-tips-mouseover').css({
        top: '',
        bottom: 'calc(100vh + 14px - ' + topStyle + 'px)',
        left: leftStyle - 8
      });
      $('.contributors-tips-mouseover').show(0);
      this.contributorsObj = contributors;
    },
    contributorsLeave() {
      $('.contributors-tips-mouseover')
        .delay(200)
        .hide(0);
    },
    aLinkTarget(evt) {
      let elt = evt.target;
      while (elt !== this.$el) {
        if (
          elt.href &&
          elt.href.match(/^((https|http|ftp|rtsp|mms)?:\/\/)[^\s]+/) &&
          (!elt.hash || elt.href.slice(0, appUri.length) !== appUri)
        ) {
          evt.preventDefault();
          const wnd = window.open(elt.href, '_blank');
          wnd.focus();
          return;
        }
        elt = elt.parentNode;
      }
    }
  }
};
</script>

