import vueApp from '../main';
import * as graphCfg from '../constants/peojectGraphConfig';

function calculateNodeInitialLocation(offsetX, offsetY) {
  let selectedGraph = vueApp.$store.getters['projectDetail/selectedGraph'];
  let userGraph = vueApp.$store.getters['userGraph/userGraph'](
    selectedGraph.id
  );
  let graphContainerEle = vueApp.$el.querySelector('.project-graph-content');
  let projectGraphRect = graphContainerEle.getBoundingClientRect();

  let graphContentEl = vueApp.$el.querySelector('.project-graph');
  let graphContentRect = graphContentEl.getBoundingClientRect();

  // project-graph-content Central Point
  let graphContainerCenter = {
    x: (projectGraphRect.right + projectGraphRect.left) / 2,
    y: (projectGraphRect.top + projectGraphRect.bottom) / 2
  };

  // project-graph Central Point
  let graphContentCenter = {
    x: (graphContentRect.right + graphContentRect.left) / 2,
    y: (graphContentRect.top + graphContentRect.bottom) / 2
  };
  let d = {
    x: parseInt(
      (parseInt(graphContainerCenter.x) - parseInt(graphContentCenter.x)) /
        userGraph.transform.scale
    ),
    y: parseInt(
      (parseInt(graphContainerCenter.y) - parseInt(graphContentCenter.y)) /
        userGraph.transform.scale
    )
  };

  let selectedGraphNodes =
    vueApp.$store.getters['projectDetail/selectedGraphNodes'];
  let zArry = [];
  let z = 1;
  if (selectedGraphNodes) {
    _.forEach(selectedGraphNodes, function(value, key) {
      zArry.push(value.location.z);
      z = parseInt(_.max(zArry)) + 1;
    });
  }
  let graphCenterX = graphContentRect.x;
  let graphCenterY = graphContentRect.y;
  let location = {};
  if (offsetX) {
    location = {
      x: parseInt((offsetX - graphCenterX) / userGraph.transform.scale - 130),
      y: parseInt((offsetY - graphCenterY) / userGraph.transform.scale),
      z: z
    };
  } else {
    location = {
      x: parseInt(
        (projectGraphRect.width - graphCfg.nodeContainerBoxWidth) / 2 + d.x
      ),
      y: parseInt(
        (projectGraphRect.height - graphCfg.nodeContainerBoxHeight) / 2 + d.y
      ),
      z: z
    };
  }
  let node = null;
  do {
    node = selectedGraphNodes.find(
      x =>
        parseInt(x.location.x) === parseInt(location.x) &&
        parseInt(x.location.y) === parseInt(location.y)
    );
    if (node) {
      location.x = parseInt(location.x) + 15;
      location.y = parseInt(location.y) + 15;
      location.z = z;
    }
  } while (node);
  return location;
}

async function createNodeWithNewExpriment() {
  let i18nDefaultExperimentName =
    'ProjectPage.CreateExperimentDialog.DefaultExperimentName';
  let i18nDefaultExperimentContent =
    'ProjectPage.CreateExperimentDialog.DefaultExperimentContent';

  let reg = new RegExp(
    `^\\s*(${vueApp.$i18n.t(i18nDefaultExperimentName, [''])})(\\d+)\\s*$`,
    'ig'
  );
  let project = vueApp.$store.getters['projectDetail/project'];
  let selectedGraph = vueApp.$store.getters['projectDetail/selectedGraph'];
  let experimentTitles = project.experiments
    .map(x => new RegExp(reg).exec(x.title))
    .filter(x => !!x);
  let maxExperimentTitleNumber = _.isEmpty(experimentTitles)
    ? 0
    : _(experimentTitles)
        .map(x => parseInt(x[2]))
        .max();
  let payload = {
    projectId: project.id,
    graphId: selectedGraph.id,
    node: {
      location: calculateNodeInitialLocation()
    },
    experiment: {
      title: vueApp.$i18n.t(i18nDefaultExperimentName, [
        maxExperimentTitleNumber + 1
      ]),
      content: {
        markdown:
          'Contents of the experiment\n\n\n\n\n\n\n\n```parameter\nSuper parameters of experiment\n```\n\n\n\n\n\n\n\n```result\nResults of the experiment\n```',
        editorHtml: '\n',
        previewHtml: '\n'
      },
      goal: {
        markdown: '\n',
        editorHtml: '\n',
        previewHtml: '\n'
      },
      result: {
        markdown: '\n',
        editorHtml: '\n',
        previewHtml: `<div class="cl-preview-section"><pre class=" language-result"><code class="prism  language-result">Results of the experiment</code></pre></div>`
      },
      settings: {
        markdown: '\n',
        editorHtml: '\n',
        previewHtml: `<div class="cl-preview-section"><pre class=" language-parameter"><code class="prism  language-parameter">Super parameters of experiment</code></pre></div>`
      }
    }
  };
  let data = await vueApp.$store
    .dispatch('projectDetail/createExperimentWithNode', payload)
    .catch(error => {
      if (error.response.status === 422) {
        vueApp.$store.commit('settings/showGlobalSnackbar', {
          text: vueApp.$i18n.t('frequentOperation')
        });
      }
    });
}

export default {
  calculateNodeInitialLocation,
  createNodeWithNewExpriment
};
