export default {
  userSelectedGraph: function(newValue, oldVaule) {
    let itemGraph = this.userGraphList.find(x => x.id === newValue) || {
      transform: {
        scale: 1,
        x: 0,
        y: 0,
        z: 0
      }
    };
    let t = itemGraph.transform;
    if (this.contentContainer) {
      let contentContainerTs = d3.zoomTransform(this.contentContainer);
      contentContainerTs.k = 1;
      contentContainerTs.x = 0;
      contentContainerTs.y = 0;
      let transform = contentContainerTs.translate(t.x, t.y).scale(t.scale);
      this.contentContainer.call(this.zoomHandler.transform, transform);
      this.$nextTick(() => {
        let firstToproject = this.$store.getters['settings/firstToProject'];
        if (firstToproject) {
          this.moveToCenter();
        }
      });
    }
  },
  userGraphList: function(newValue, oldVaule) {
    let itemGraph = this.userGraphList.find(
      x => x.id === this.userSelectedGraph
    ) || {
      transform: {
        scale: 1,
        x: 0,
        y: 0,
        z: 0
      }
    };
    let t = itemGraph.transform;
    if (this.contentContainer) {
      let contentContainerTs = d3.zoomTransform(this.contentContainer);
      contentContainerTs.k = 1;
      contentContainerTs.x = 0;
      contentContainerTs.y = 0;
      let transform = contentContainerTs.translate(t.x, t.y).scale(t.scale);
      this.contentContainer.call(this.zoomHandler.transform, transform);
      this.$nextTick(() => {
        let firstToproject = this.$store.getters['settings/firstToProject'];
        if (firstToproject) {
          this.moveToCenter();
        }
      });
    }
  },
  $route(to, from) {
    if (from.name === 'projectGraph') {
      this.$nextTick(function() {
        // 页面加载完成后执行
        this.$store.commit('settings/closeGlobalLoadingPage');
      });
    }
  },
  nodeInfo: function(newValue, oldValue) {
    if (oldValue.id && newValue.id !== oldValue.id) {
      this.$store.dispatch('projectCollection/unLock', {
        target: `/projects/${this.projectInfo.id}/experiments/${oldValue.id}`
      });
    }
    if (newValue.id === oldValue.id) {
      this.$store.dispatch('projectCollection/addLock', {
        target: `/projects/${this.projectInfo.id}/experiments/${oldValue.id}`
      });
    }
  }
};
