/**
 * Update the list of members of the project Id specified project
 */
function refreshContributorList(state, payload) {
  let projectId = payload.projectId;
  let contributors = payload.contributors;
  let project = this.getters['projectCollection/project'](projectId);
  project.contributors.splice(0, project.contributors.length, ...contributors);
}

/**
 * Delete contributorId-specified members from the list of members of the project specified by projectId
 */
function removeOneContributor(state, payload) {
  let projectId = payload.projectId;
  let contributorId = payload.deleteContributorId;
  let project = this.getters['projectCollection/project'](projectId);
  _.vueRemove(project.contributors, x => x.id === contributorId);
}

/**
 * socket
 */
function SOCKET_updateProjectContributors(state, payload) {
  let projectId = payload.projectId;
  let project = this.getters['projectCollection/project'](projectId);
  let currentUser = this.getters['session/user'];
  if (project) {
    if (payload.action === 'delete') {
      _.vueRemove(project.contributors, x => x.id === payload.contributorsId);
    } else {
      let contributorsLength = project.contributors.length;
      if (contributorsLength > 0) {
        let currentAbility = _.find(
          project.contributors,
          x => x.id === currentUser.id
        ).ability;
        if (
          payload.contributorsId === currentUser.id &&
          payload.data.ability !== currentAbility &&
          currentAbility
        ) {
          _.vueRemove(
            project.contributors,
            x => x.id === payload.contributorsId
          );
          project.contributors.push(payload.data);
          this.commit('settings/showGlobalAlert', {
            globalAlertShow: true,
            globalAlertText: getVM().$i18n.t(
              'ProjectPage.VLJurisdictionChange',
              [project.title]
            ),
            timeout: 3000
          });
        } else {
          _.vueRemove(
            project.contributors,
            x => x.id === payload.contributorsId
          );
          project.contributors.push(payload.data);
        }
      }
    }
  }
}

function SOCKET_deleteProjectOrContributors(state, payload) {
  let projectId = payload.projectId;
  let projectList = this.getters['projectCollection/projectList'];
  let currentRouterName = window.getVM().$router.history.current.name;
  let descDialogDisplayShowToId = this.getters[
    'projectCollection/descDialogDisplayShowToIdGetters'
  ];
  let itemProject = _.find(projectList, x => x.id === projectId);
  if (currentRouterName === 'projectGraph') {
    let currentProjectId = window.getVM().$router.history.current.params
      .projectId;
    if (currentProjectId === projectId) {
      window.getVM().$router.push('/projects');
      this.commit('settings/showGlobalAlert', {
        globalAlertShow: true,
        globalAlertText: getVM().$i18n.t('ProjectPage.OTProjectRemove', [
          itemProject.title
        ]),
        timeout: 3000
      });
    }
  } else if (currentRouterName === 'projectManageLayout') {
    if (descDialogDisplayShowToId === projectId) {
      _.vueRemove(projectList, x => x.id === payload.projectId);
      this.commit('projectCollection/descDialogDisplayShowFun', false);
      this.commit('settings/showGlobalAlert', {
        globalAlertShow: true,
        globalAlertText: getVM().$i18n.t('ProjectPage.OTProjectRemove', [
          itemProject.title
        ]),
        timeout: 3000
      });
    } else {
      let myDeleteProject = this.getters['projectCollection/myDeleteProject'];
      let deleteItemProjrct = myDeleteProject || itemProject;
      _.vueRemove(projectList, x => x.id === payload.projectId);
      this.commit('settings/showGlobalAlert', {
        globalAlertShow: true,
        globalAlertText: getVM().$i18n.t('ProjectPage.OTProjectRemove', [
          deleteItemProjrct.title
        ]),
        timeout: 3000
      });
    }
  }
}

export default {
  refreshContributorList,
  removeOneContributor,
  SOCKET_updateProjectContributors,
  SOCKET_deleteProjectOrContributors
};
