import api from 'Api';
import socketAndHttp from '../../../services/socketAndHttp';
/**
 * Get a list of items for the current account
 */
async function refreshProjectList(context) {
  this.commit('projectCollection/refreshProjectList', []);
  let response = await api.get('/projects');
  let projectList = response.data;
  projectList.forEach(x => addProjectContributors.call(this, x));
  return projectList;
}

/**
 * Update the project information in the local cache and asynchronously obtain the contributor information of the project to render the project list more quickly
 */
function addProjectContributors(project) {
  this.commit('projectCollection/refreshProject', project);
  let promisesList = [];
  promisesList.push(
    this.dispatch('projectDetail/refreshContributorList', project.id)
  );
  return Promise.all(promisesList).then(dataList => project);
}

/**
 * Set the project with the specified ID as the focus item
 */
async function setProjectById(context, projectId) {
  let project = await this.dispatch(
    'projectCollection/getProjectById',
    projectId
  );
  this.commit('projectCollection/setProject', project);
  return project;
}

/**
 * add or remove focus project
 */
async function setProjectFocus(context, payload) {
  let response = await socketAndHttp(`/users/projects`, 'patch', payload);
  return response;
}

/**
 * Unlike refreshProject, which retrieves the item with the specified id, this method will attempt to retrieve the local cached item and retrieve it from the server if it does not exist locally.
 */
async function getProjectById(context, projectId) {
  let project = this.getters['projectCollection/project'](projectId);
  return new Promise((resolve, reject) => {
    if (project) {
      resolve(project);
    } else {
      this.dispatch('projectCollection/refreshProject', projectId)
        .then(p => resolve(p))
        .catch(e => reject(e));
    }
  });
}

/**
 * Get project information for the specified ID
 */
async function refreshProject(context, projectId) {
  let response = await api.get(`/projects/${projectId}`);
  let project = response.data;
  await addProjectContributors.call(this, project);
  return project;
}

/**
 * Update the basic information of the project, update the details of the project, problem definition, evaluation criteria, experimental data set information, please use special API.
 */
async function updateProject(context, payload) {
  let projectId = payload.id;
  delete payload.id;
  let response = await socketAndHttp(
    `/projects/${projectId}`,
    'patch',
    payload
  );
  let project = response.data;
  this.commit('projectCollection/refreshProject', project);
  return project;
}

/**
 * Delete the item with the specified ID
 */
async function deleteProject(context, projectId) {
  await socketAndHttp(`/projects/${projectId}`, 'delete');
  this.commit('projectCollection/deleteProject', projectId);
  return projectId;
}

/**
 * Create a project
 */
async function createProject(context, payload) {
  let response = await socketAndHttp(`/projects`, 'post', payload, false);
  let project = response.data;
  await addProjectContributors.call(this, project);
  return project;
}

/**
 *
 * Refresh basic project information, but project information does not include sketchpad, experiment, Idea, member information
 * Mainly used for Idea, experimental independent page display data loading
 */
async function refreshProjectWithoutDetail(context, projectId) {
  let response = await api.get(`/projects/${projectId}`);
  let project = response.data;
  this.commit('projectCollection/refreshProject', project);
  return project;
}

/**
 * Lock a resource
 */
async function addLock(context, payload) {
  let response = await socketAndHttp(`/edit/lock`, 'post', payload);
  return response.data;
}

/**
 * Unlock a resource
 */
async function unLock(context, payload) {
  let response = await socketAndHttp(`/edit/unlock`, 'post', payload);
  return response.data;
}

export default {
  refreshProjectList,
  refreshProject,
  refreshProjectWithoutDetail,
  createProject,
  updateProject,
  deleteProject,
  getProjectById,
  setProjectById,
  setProjectFocus,
  addLock,
  unLock
};
