import axios from 'axios';
import qs from 'qs';

// 获取环境变量配置
let debug = process.env.VUE_APP_SERVER_DEBUG || false;
$.ajax({
  url: `${process.env.VUE_APP_API_SERVER_URL}?env=${process.env.NODE_ENV}&debug=${debug}`,
  async: false,
  type: 'get',
  success: res => {
    window.envVariable = res;
  },
  error: error => {
    console.log('获取Api地址失败');
  }
});

// 获取vue实例
window.getVM = function() {
  return document.querySelector('[data-app]').__vue__;
};
// 发送日志到日志服务器
window.setLog = function(payload) {
  let project = `${envVariable.VUE_APP_SLS_LOG_PROJECT}`;
  let host = `${envVariable.VUE_APP_SLS_LOG_HOST}`;
  let logstore = `${envVariable.VUE_APP_SLS_LOG_LOGSTORE}`;
  let logger = new window.Tracker(host, project, logstore);
  switch (payload.topic) {
    case 'clientError':
      logger.push('userId', payload.userId);
      logger.push('url', payload.url);
      logger.push('message', payload.message);
      logger.push('stack', payload.stack);
      logger.push('__topic__', payload.topic);
      logger.logger();
      break;
    case 'apiEvent':
      logger.push('userId', payload.userId);
      logger.push('url', payload.url);
      logger.push('requestMethod', payload.requestMethod);
      logger.push('referer', payload.referer);
      logger.push('userAgent', payload.userAgent);
      logger.push('statusCode', payload.statusCode);
      logger.push('message', payload.message);
      logger.push('__topic__', payload.topic);
      logger.logger();
      break;
    case 'userEvent':
      logger.push('userId', payload.userId);
      logger.push('url', payload.url);
      logger.push(
        'mouseEvent',
        `{type:${payload.mouseEvent.type},path:${payload.mouseEvent.path},altKey:${payload.mouseEvent.altKey},ctrlKey:${payload.mouseEvent.ctrlKey},metaKey:${payload.mouseEvent.metaKey},shiftKey:${payload.mouseEvent.shiftKey}}`
      );
      logger.push('__topic__', payload.topic);
      logger.logger();
      break;
    case 'firstLoading':
      logger.push('resource', JSON.stringify(payload.payloadArry));
      logger.push('__topic__', payload.topic);
      logger.logger();
      break;
  }
};

// api请求超时时间
axios.defaults.timeout = 60000;

let apiAxios = axios.create({
  baseURL: envVariable.VUE_APP_API_SERVER_URL,
  // 参数序列化
  paramsSerializer: function(params) {
    return qs.stringify(params, { arrayFormat: 'repeat' });
  }
});

// Declare an array to store cancellation functions and Ajax identifiers for each Ajax request
let pending = [];
let removeRepeatUrl = ever => {
  for (let p in pending) {
    // console.log(ever.method)
    if (
      pending[p].u === ever.url + '&' + ever.method &&
      ever.method !== 'get' &&
      ever.method !== 'patch'
    ) {
      // Execute the body of the function when the current request exists in the array
      pending[p].f(); // Perform Cancel Operations
      pending.splice(p, 1); // Remove this record from the array
    }
  }
};

apiAxios.interceptors.request.use(
  function(config) {
    // Do a cancel before sending an Ajax
    removeRepeatUrl(config);
    config.cancelToken = new axios.CancelToken(c => {
      // Customize Unique Identity
      pending.push({
        u: config.url + '&' + config.method,
        f: c
      });
    });
    // Distinguish whether it is an API request
    config.headers['x-usage'] = 'api';
    let getters = getVM().$store.getters;
    if (getters['session/isAuthenticated']) {
      config.headers['x-sid'] = getters['session/userSid'];
    }
    return config;
  },
  function(error) {
    // console.log(error);
    return Promise.reject(error);
  }
);

apiAxios.interceptors.response.use(
  function(response) {
    // 存储x-sid
    if (response.headers['x-sid']) {
      getVM().$store.commit('session/saveSid', response.headers['x-sid']);
    }
    // The API request succeeded, submitting the relevant data to the log server
    let payload = {
      topic: 'apiEvent',
      userId: getVM().$store.getters['session/user'].id,
      url: response.config.url,
      requestMethod: response.config.method,
      referer: window.location.href,
      userAgent: window.navigator.userAgent,
      statusCode: response.status,
      message: ' '
    };
    setLog(payload);
    // Remove completed requests from pending by performing a cancellation after an Ajax response
    removeRepeatUrl(response.config);
    return response;
  },
  function(error) {
    // 存储x-sid
    if (error.response.headers['x-sid']) {
      getVM().$store.commit('session/saveSid', error.response.headers['x-sid']);
    }
    // API request failed, submit relevant data to log server
    if (error.message && error.response !== undefined) {
      let payload = {
        topic: 'apiEvent',
        userId: getVM().$store.getters['session/user'].id,
        url: error.response.config.url,
        requestMethod: error.response.config.method,
        referer: window.location.href,
        userAgent: window.navigator.userAgent,
        statusCode: error.response.status,
        message: error.message
      };
      setLog(payload);
    }
    if (error.response) {
      switch (error.response.status) {
        case 401:
          getVM().$store.commit('session/clearUserDataInLocalStorage');
          break;
        case 403:
          getVM().$store.commit('settings/showGlobalSnackbar', {
            text: getVM().$i18n.t('authority')
          });
          break;
        case 404:
          errorNotFind(error);
          break;
      }
    }
    if (error.request) {
      if (error.request.readyState === 4 && error.request.status === 0) {
        getVM().$router.replace('/404');
        getVM().$store.commit('settings/closeGlobalLoadingPage');
      }
    }
    return Promise.reject(error);
  }
);

export default apiAxios;
