import Vue from 'vue';
import ossUploadFiles from '../../../oss';
import { defineAbilitiesForProjectSubject } from '../../../services/cals';
// import html2canvas from 'html2canvas';
import vueApp from '../../../main';

function loginSuccess(state, user) {
  this.commit('session/saveUserDataToLocalStorage', user);
  this.commit('session/LoginToRouter');
}

function saveSid(state, sid) {
  localStorage.setItem('sid', sid);
  state.sid = sid;
}

function loginFailure(state, error) {
  console.log('loginFailure:', error);
}

function logoutSuccess(state) {
  this.commit('session/clearUserDataInLocalStorage');
}

function logoutFailure(state, error) {
  this.commit('session/clearUserDataInLocalStorage');
}

async function signupSuccess(state) {
  let colorArry = [
    '#F44336',
    '#E91E63',
    '#9C27B0',
    '#673AB7',
    '#3F51B5',
    '#2196F3',
    '#03A9F4',
    '#00BCD4',
    '#009688',
    '#4CAF50',
    '#8BC34A',
    '#FF9800',
    '#FF5722',
    '#795548',
    '#607D8B',
    '#212121',
    '#263238',
    '#3E2723',
    '#33691E',
    '#1B5E20',
    '#004D40',
    '#006064',
    '#01579B',
    '#0D47A1',
    '#1A237E',
    '#311B92',
    '#4A148C',
    '#880E4F',
    '#B71C1C'
  ];
  let color = colorArry[Math.floor(Math.random() * colorArry.length)];
  state.avarBackgtoundColor = color;

  let dom = vueApp.$el.querySelector('.avar-ico-avatar');
  let self = this;

  let files = await new Promise((resolve, reject) => {
    html2canvas(dom, {
      backgroundColor: null
    }).then(canvas => {
      // Canvas-generated image background removal
      let context = canvas.getContext('2d');
      let w = canvas.width;
      let h = canvas.height;
      let data = context.getImageData(0, 0, w, h);
      let compositeOperation = context.globalCompositeOperation;
      context.globalCompositeOperation = 'destination-over';
      context.fillStyle = '#fff';
      context.fillRect(0, 0, w, h);
      // Get Picture Links
      let dataURL = canvas.toDataURL('image/jpeg');
      context.clearRect(0, 0, w, h);
      context.putImageData(data, 0, 0);
      context.globalCompositeOperation = compositeOperation;
      // Local store modifies Avatar
      // Vue.set(state.user, 'avatar', dataURL);

      let base64String = dataURL;
      // Here we operate on the base64 string, remove the URL header, and convert it to byte
      let bytes = window.atob(base64String.split(',')[1]);
      let array = [];
      for (let i = 0; i < bytes.length; i++) {
        array.push(bytes.charCodeAt(i));
      }
      let blob = new Blob([new Uint8Array(array)], {
        type: 'image/jpeg'
      });
      let files = [
        new File([blob], 'userAvatar.jpg', {
          type: blob.type,
          lastModified: Date.now()
        })
      ];
      resolve(files);
    });
  });
  await ossUploadFiles(files, 'user_avatar');
  self.dispatch('user/getAccount');
}

function signupFailure(state, error) {
  console.log('signupFailure:', error);
}

function saveUserDataToLocalStorage(state, user) {
  _.forOwn(user, (v, k) => Vue.set(state.user, k, v) || true);
  let dataJson = JSON.stringify(state.user);
  localStorage.setItem('userData', dataJson);
}

function LoginToRouter() {
  this.dispatch('projectCollection/refreshProjectList');
  let router = window.getVM().$router;
  let nextJump = '/projects';
  if (router.currentRoute && router.currentRoute.query.redirect) {
    nextJump = router.currentRoute.query.redirect;
  }
  router.replace(nextJump);
}

function clearUserDataInLocalStorage(state) {
  localStorage.clear();
  state.user = {};
  state.sid = '';
  window.getVM().$router.push('/login');
}

function clearUserDataInLocalStorageNoRouter(state) {
  localStorage.removeItem('userData');
  state.user = {};
}

export default {
  loginSuccess,
  saveSid,
  loginFailure,
  logoutSuccess,
  logoutFailure,
  signupSuccess,
  signupFailure,
  saveUserDataToLocalStorage,
  LoginToRouter,
  clearUserDataInLocalStorage,
  clearUserDataInLocalStorageNoRouter
};
