/**
 * Update Local Cache Item List
 */
function refreshProjectList(state, projects) {
  let projectList = this.state.projectCollection.projectList;
  projectList.splice(0, projectList.length, ...projects);
}

/**
 * Update the basic information of the local cache project, contributors information will be maintained by the proprietary API
 */
function refreshProject(state, project) {
  let projectList = _.vueRemove(state.projectList, x => x.id === project.id);
  let oldProject = projectList.length ? projectList[0] : project;
  let standaloneFields = ['contributors', 'experiments', 'ideas', 'graphs'];
  let arrBackup = _.pick(oldProject, standaloneFields);
  Object.assign(project, arrBackup);
  for (const key of standaloneFields) {
    if (!project[key]) {
      project[key] = [];
    }
  }
  state.projectList.push(project);
}

/**
 * Setting Focus Items
 */
function setProject(state, project) {
  this.state.projectDetail.selectedProjectId = project.id;
}

/**
 * Clear Focus Items
 */
function clearProject(state) {
  this.state.projectDetail.selectedProjectId = null;
}

/**
 * Delete an item data from the local cache
 */
function deleteProject(state, projectId) {
  let projectList = this.getters['projectCollection/projectList'];
  let itemProject = _.find(projectList, x => x.id === projectId);
  this.commit('projectCollection/myDeleteProjectFun', itemProject);
  _.vueRemove(state.projectList, x => x.id === projectId);
}

function descDialogDisplayShowFun(state, payload) {
  state.descDialogDisplayShow = payload;
}

function descDialogDisplayShowToIdFun(state, payload) {
  state.descDialogDisplayShowToId = payload;
}

function myDeleteProjectFun(state, payload) {
  state.myDeleteProject = payload;
}

/**
 * socket
 */

//Update project, update project information
function SOCKET_updateProject(state, payload) {
  let projectList = state.projectList;
  let project = _.find(projectList, function(x) {
    return x.id === payload.projectId;
  });
  if (project) {
    Object.assign(project, payload.data);
  }
}

//Update messages
function autoSaveMessagesFun(state, messages) {
  state.autoSaveMessages = messages;
}

export default {
  refreshProjectList,
  refreshProject,
  deleteProject,
  setProject,
  clearProject,
  descDialogDisplayShowFun,
  descDialogDisplayShowToIdFun,
  SOCKET_updateProject,
  autoSaveMessagesFun,
  myDeleteProjectFun
};
