import store from '../store';
import OSS from 'ali-oss';
export default async function ossUploadFiles(files, category) {
  let formData = new FormData();
  for (let i = 0; i < files.length; i++) {
    formData.append('userImages', files[i]);
  }
  let imagesArry = [];
  category = category ? category : 'user_images';
  for (let value of formData.values()) {
    let fileData = {
      name: value.name,
      size: value.size,
      contentType: value.type,
      category: category
    };
    let response = await store.dispatch('oss/getOssPolicies', fileData);
    const client = new OSS({
      region: response.oss.region,
      accessKeyId: response.oss.accessKeyId,
      accessKeySecret: response.oss.accessKeySecret,
      bucket: response.oss.bucket,
      stsToken: response.oss.securityToken
    });
    let fileName = response.oss.key;
    let options = {
      timeout: 30 * 1000,
      callback: {
        url: `${envVariable.VUE_APP_API_SERVER_URL}/upload/confirm`,
        body:
          '{"bucket":${bucket},"key":${object},"etag":${etag},"size":${size},"mimeType":${mimeType},"href":${x:href}}',
        contentType: 'application/json',
        customValue: {
          href: response.asset.href
        }
      }
    };

    await client.put(fileName, value, options).then(res => {
      let picFile = [
        {
          name: response.asset.name,
          href: response.asset.href
        }
      ];
      imagesArry.push(picFile);
    });
  }
  return imagesArry;
}
