import NProgress from 'nprogress';
import api from 'Api';
import { getPbkdf2HashForPassword } from '../../../helpers/helpers';
import socketAndHttp from '../../../services/socketAndHttp';

const states = {
  avatarCropperDialogIsOpen: false
};

// getters
const getters = {
  avatarCropperDialogIsOpen(states) {
    return states.avatarCropperDialogIsOpen;
  }
};

const actions = {
  async updateUserAccount(context, payload) {
    NProgress.start();
    let res = await socketAndHttp('/users/account', 'patch', payload);
    context.commit('updateUserAccountSuccess', res.data);
    NProgress.done();
  },
  getAccount(context) {
    return new Promise((resolve, rejiect) => {
      api
        .get('/users/account')
        .then(response => {
          context.commit('updateUserAccountSuccess', response.data);
          resolve(response);
        })
        .catch(error => {
          rejiect(error);
        });
    });
  },
  resetPassword(context, passwords) {
    NProgress.start();
    let data = {
      originalPassword: getPbkdf2HashForPassword(passwords.originalPassword),
      newPassword: getPbkdf2HashForPassword(passwords.newPassword)
    };

    return new Promise((resolve, rejiect) => {
      api
        .post('/users/account/reset-password', data)
        .then(response => {
          context.commit('resetPasswordSuccess');
          resolve(response);
        })
        .catch(error => {
          context.commit('resetPasswordFailure', error);
          rejiect(error);
        })
        .finally(() => NProgress.done());
    });
  }
};

const mutations = {
  updateUserAccountSuccess(state, user) {
    this.commit('user/closeUserAvatarCropperDialog');
    this.commit('session/saveUserDataToLocalStorage', user);
  },
  updateUserAccountFailure(state, error) {
    console.log('updateUserAccountFailure', error);
  },
  resetPasswordSuccess(state) {
    this.commit('session/clearUserDataInLocalStorage');
  },
  resetPasswordFailure(state, error) {
    console.log('resetPasswordFailure:', error);
  },
  openUserAvatarCropperDialog(state) {
    state.avatarCropperDialogIsOpen = true;
  },
  closeUserAvatarCropperDialog(state) {
    state.avatarCropperDialogIsOpen = false;
  }
};

export default {
  namespaced: true,
  state: states,
  getters,
  actions,
  mutations
};
