import api from 'Api';
import { socketInstance as socket } from '../services/socket';
function errorNotFind(error) {
  if (error.response.status === 404) {
    switch (error.response.data.message) {
      case 'Project':
        getVM().$store.commit('settings/showGlobalSnackbar', {
          text: getVM().$i18n.t('OTNoProject'),
          timeout: 60000
        });
        break;
      case 'Graph':
        getVM().$store.commit('settings/showGlobalSnackbar', {
          text: getVM().$i18n.t('OTNoGraph'),
          timeout: 60000
        });
        break;
      case 'Idea':
        getVM().$store.commit('settings/showGlobalSnackbar', {
          text: getVM().$i18n.t('OTNoIdea'),
          timeout: 60000
        });
        break;
      case 'Experiment':
        getVM().$store.commit('settings/showGlobalSnackbar', {
          text: getVM().$i18n.t('OTNoExperiment'),
          timeout: 60000
        });
        break;
      case 'GraphNode':
        getVM().$store.commit('settings/showGlobalSnackbar', {
          text: getVM().$i18n.t('OTNoNode'),
          timeout: 60000
        });
        break;
      case 'GraphEdge':
        getVM().$store.commit('settings/showGlobalSnackbar', {
          text: getVM().$i18n.t('OTNoEdge'),
          timeout: 60000
        });
        break;
      default:
        break;
    }
  }
}
window.errorNotFind = errorNotFind;

function socketAndHttp(target, action, body, only) {
  // let connected = false;
  let connected = socket.default.connected;
  // console.log(connected)
  if (connected || (connected && only)) {
    return new Promise((resolve, reject) => {
      socket.default.emit(
        'message',
        {
          timestamp: new Date().getTime(),
          action: action,
          target: target,
          body: body
        },
        function(response) {
          let result = {
            data: response.body ? response.body.data : {},
            status: response.statusCode
          };
          if (/^[123]\d{2}$/.test(response.statusCode)) {
            resolve(result);
            return;
          }

          if (/^4\d{2}$/.test(response.statusCode)) {
            let error = {
              response: result
            };
            errorNotFind(error);
            reject(error);
          }
        }
      );
    });
  } else if (!only) {
    return new Promise((resolve, reject) => {
      let response = null;
      switch (action) {
        case 'post':
          response = api.post(target, body);
          if (response) {
            resolve(response);
          } else {
            reject(error);
          }
          break;
        case 'patch':
          response = api.patch(target, body);
          if (response) {
            resolve(response);
          } else {
            reject(error);
          }
          break;
        case 'put':
          response = api.put(target, body);
          if (response) {
            resolve(response);
          } else {
            reject(error);
          }
          break;
        case 'delete':
          response = api.delete(target);
          if (response) {
            resolve(response);
          } else {
            reject(error);
          }
          break;
      }
    });
  }
}
export default socketAndHttp;