<template>
  <div class="session-wrapper login">
    <div class="session-header">
      <logo-box></logo-box>
    </div>

    <div class="session-main">
      <div class="session-content">
        <error-box ref="errorDom" class="mb-4"></error-box>
        <p v-once class="function-hint" v-text="trans.emailAddress"></p>
        <input
          v-model="email"
          class="email mb-4"
          type="text"
          :placeholder="trans.email"
          @keyup.enter="login"
        />
        <p v-once class="function-hint">
          <span v-text="trans.passwordTitle"></span>
          <span
            v-text="trans.OTForgotPassword"
            @click="goToRouter('/forgot-password')"
          >
          </span>
        </p>
        <input
          v-model="password"
          class="password mb-4"
          type="password"
          :placeholder="trans.password"
          @keyup.enter="login"
        />
        <button
          v-once
          class="submit-button"
          @click="login"
          v-text="trans.OPSubmitButton"
        ></button>
        <div class="text-bottom">
          <span v-once v-text="trans.OTSigUpText"></span>
          <span style="margin: 0 0.25rem;"></span>
          <router-link
            v-once
            class="login"
            to="/signup"
            v-text="trans.OTSignUp"
          ></router-link>
        </div>
      </div>
      <div class="session-bottom">
        <router-link
          v-once
          target="_blank"
          to="/legal/privacyPolicy"
          v-text="trans.OTPrivacyPolicy"
        ></router-link>
        <span class="line"></span>
        <router-link
          v-once
          target="_blank"
          to="/legal/TermsOfService"
          v-text="trans.OTTermsOfService"
        ></router-link>
      </div>
    </div>
    <div class="session-footer">
      <contact-us class="back-white"></contact-us>
    </div>
  </div>
</template>

<script>
  import logoBox from '../../components/Widgets/logo';
  import errorBox from '../../components/Widgets/session/error';
  import contactUs from '../../components/contactUs';
  import api from 'Api';

  export default {
    name: 'Login',
    created() {
      let v = this.$validator;
      v.attach({
        name: 'email',
        alias: this.trans.email,
        getter: () => this.email,
        rules: 'required|email'
      });

      v.attach({
        name: 'password',
        alias: this.trans.password,
        getter: () => this.password,
        rules: 'required'
      });
    },
    components: {
      logoBox,
      errorBox,
      contactUs
    },
    data() {
      return {
        email: '',
        password: '',
        trans: {
          email: this.$i18n.t('email'),
          password: this.$i18n.t('password'),
          OTTitle: this.$i18n.t('Session.Login.OTTitle'),
          OPSubmitButton: this.$i18n.t('Session.Login.OPSubmitButton'),
          OTForgotPassword: this.$i18n.t('Session.Login.OTForgotPassword'),
          OTSignUp: this.$i18n.t('Session.Login.OTSignUp'),
          OTSigUpText: this.$i18n.t('Session.Login.OTSigUpText'),
          OTEmailOrPasswdInvalid: this.$i18n.t(
            'Session.Login.OTEmailOrPasswdInvalid'
          ),
          emailAddress: this.$i18n.t('emailAddress'),
          passwordTitle: this.$i18n.t('passwordTitle'),
          OTPrivacyPolicy: this.$i18n.t('Session.Login.OTPrivacyPolicy'),
          OTTermsOfService: this.$i18n.t('Session.Login.OTTermsOfService')
        }
      };
    },
    methods: {
      async login(event) {
        let invitingProjectId = this.$route.query.projectId;
        event.srcElement.blur();
        let v = this.$validator;
        let validateFailed = !(await v.validate());
        let errors = await v.errors.all();

        let errorDom = this.$refs.errorDom;
        errorDom.open = validateFailed;
        if (validateFailed) {
          errorDom.errorText = errors[0];
          return;
        }

        try {
          let data = {
            email: this.email,
            password: this.password
          };
          await this.$store.dispatch('session/login', data).then(res => {
            if (invitingProjectId) {
              this.$router.push('/projects/' + invitingProjectId);
            }
          });
        } catch (error) {
          errorDom.errorText = this.trans.OTEmailOrPasswdInvalid;
          errorDom.open = true;
        }
      },
      goToRouter(to) {
        this.$router.push(to);
      }
    }
  };
</script>
