import actions from './actions';
import mutations from './mutations';

const states = {
  userSelectedGraph: {},
  userGraphList: []
};

// getters
const getters = {
  userSelectedGraph: state => state.userSelectedGraph.selectedGraph,
  userGraphList: state => state.userGraphList,
  /**
   * Find out the status information of the panel selected by the current user from the graphList
   */
  userGraph: state => selectedGraphId =>
    state.userGraphList.find(x => x.id === selectedGraphId),

  selectedUserGraph: state => {
    let userGraph = state.userGraphList;
    if (!userGraph) {
      return null;
    }
    return userGraph.find(x => x.id === state.userSelectedGraph.selectedGraph);
  }
};

export default {
  namespaced: true,
  state: states,
  getters,
  actions,
  mutations
};
