/**
 * This is a standalone model,need pass the projectId and graphId for it,so that this model render anywhere
 */
import contributorActions from './contributorActions';
import contributorMutations from './contributorMutations';
import experimentActions from './experimentActions';
import experimentMutations from './experimentMutations';
import graphActions from './graphActions';
import graphMutations from './graphMutations';
import ideaActions from './ideaActions';
import ideaMutations from './ideaMutations';
import nodeActions from './nodeActions';
import nodeMutations from './nodeMutations';
import edgeActions from './edgeActions';
import edgeMutations from './edgeMutations';

import * as graphCfg from '../../../constants/peojectGraphConfig';
import * as graphMath from '../../../helpers/graphMath';

const state = {
  /**
   * The current focus project, which is a reference in the project Collection/project List, is used to anchor the focus project
   */
  selectedProjectId: null,
  resizeDialogWidth: '',
  ExitEditSaveDialog: {
    title: '',
    status: false
  },
  ideasCreateEditDialogDisplayShow: false,
  updateGraphCursorList: []
};

const getters = {
  selectedProjectId(state) {
    return state.selectedProjectId;
  },
  updateGraphCursorList(state) {
    return state.updateGraphCursorList;
  },
  ideasCreateEditDialogDisplayShowGetters(state) {
    return state.ideasCreateEditDialogDisplayShow;
  },
  ExitEditSaveDialog(state) {
    return state.ExitEditSaveDialog;
  },
  resizeDialogWidth(state) {
    return state.resizeDialogWidth;
  },
  project(state, getters, rootState, rootGetters) {
    let project = rootGetters['projectCollection/project'](
      state.selectedProjectId
    );
    return rootGetters['projectCollection/project'](state.selectedProjectId);
  },
  /**
   * Back up the focus panel data when the selected GraphId changes for use in the editing and updating operation of the Sketchpad
   */
  selectedGraph(state, getters, rootState, rootGetters) {
    let graph = (getters.project ? getters.project.graphs : []).find(
      x => x.id === rootGetters['userGraph/userSelectedGraph']
    );
    return graph;
  },
  /**
   * The edges of the current focus panel, which are different from the stored edges, are mainly extended for svg
   */
  selectedGraphEdges(state, getters) {
    let graph = getters.selectedGraph;
    let edges = graph ? graph.edges : [];
    let nodes = graph ? graph.nodes : [];
    let extendEdges = [];
    for (const edge of edges) {
      let startNode = nodes.find(x => x.id === edge.startNode);
      let endNode = nodes.find(x => x.id === edge.endNode);
      if (!(startNode && endNode)) {
        continue;
      }
      let boxWidth = graphCfg.nodeContainerBoxWidth;
      let boxStartHeight = startNode.height
        ? startNode.height
        : 0 +
          graphCfg.nodeContainerPadding[0] +
          graphCfg.nodeContainerPadding[2] +
          graphCfg.nodeContainerBorderWidth[0] +
          graphCfg.nodeContainerBorderWidth[2];
      let boxEndHeight = endNode.height
        ? endNode.height
        : 0 +
          graphCfg.nodeContainerPadding[0] +
          graphCfg.nodeContainerPadding[2] +
          graphCfg.nodeContainerBorderWidth[0] +
          graphCfg.nodeContainerBorderWidth[2];

      let startLocation = graphMath.removeLocationUnit(startNode.location);
      let endLocation = graphMath.removeLocationUnit(endNode.location);

      let positionStartArray = _(graphCfg.nodeAnchors)
        .values()
        .map(anchor => ({
          x: startLocation.x + boxWidth * anchor.x,
          y: startLocation.y + boxStartHeight * anchor.y
        }))
        .value();

      let positionEndArray = _(graphCfg.nodeAnchors)
        .values()
        .map(anchor => ({
          x: endLocation.x + boxWidth * anchor.x,
          y: endLocation.y + boxEndHeight * anchor.y
        }))
        .value();
      let NearestPositionPairs = graphMath.computNearestPosition(
        positionStartArray,
        endNode.id === graphCfg.virtualNodeId && !edge.isItemNode
          ? [endLocation]
          : positionEndArray
      );

      let startPosition = NearestPositionPairs[0];
      let endPosition = NearestPositionPairs[1];

      let edgeXOffset = graphCfg.edgeXOffset;
      let edgeStartYOffset =
        startNode.height / 2 +
        graphCfg.nodeContainerPadding[3] +
        graphCfg.nodeContainerBorderWidth[3];

      let edgeEndYOffset =
        endNode.height / 2 +
        graphCfg.nodeContainerPadding[3] +
        graphCfg.nodeContainerBorderWidth[3];

      let startCenterPosition = {
        x: startLocation.x + edgeXOffset,
        y: startLocation.y + edgeStartYOffset
      };
      let endCenterPosition = {
        x: endLocation.x + edgeXOffset,
        y: endLocation.y + edgeEndYOffset
      };

      let isHorizontal = Math.abs(startPosition.y - startCenterPosition.y) < 20;

      edge.ctlPoint1 = {
        x:
          (isHorizontal
            ? startPosition.x - startCenterPosition.x > 0
              ? 1
              : -1
            : 0) * graphCfg.controlLineLength,
        y:
          (!isHorizontal
            ? startPosition.y - startCenterPosition.y > 0
              ? 1
              : -1
            : 0) * graphCfg.controlLineLength
      };

      isHorizontal = Math.abs(endPosition.y - endCenterPosition.y) < 20;
      edge.ctlPoint2 = {
        x:
          (isHorizontal
            ? endPosition.x - endCenterPosition.x > 0
              ? 1
              : -1
            : 0) * graphCfg.controlLineLength,
        y:
          (!isHorizontal
            ? endPosition.y - endCenterPosition.y > 0
              ? 1
              : -1
            : 0) * graphCfg.controlLineLength
      };

      if (endNode.id === graphCfg.virtualNodeId && !edge.isItemNode) {
        edge.ctlPoint2 = {
          x: -1 * edge.ctlPoint1.x,
          y: -1 * edge.ctlPoint1.y
        };
      }

      let xDistance = Math.abs(endPosition.x - startPosition.x);
      if (xDistance < graphCfg.controlLineLength) {
        edge.ctlPoint1.x =
          edge.ctlPoint1.x &&
          (edge.ctlPoint1.x / Math.abs(edge.ctlPoint1.x)) * xDistance;
        edge.ctlPoint2.x =
          edge.ctlPoint2.x &&
          (edge.ctlPoint2.x / Math.abs(edge.ctlPoint2.x)) * xDistance;
      }

      let yDistance = Math.abs(endPosition.y - startPosition.y);
      if (yDistance < graphCfg.controlLineLength) {
        edge.ctlPoint1.y =
          edge.ctlPoint1.y &&
          (edge.ctlPoint1.y / Math.abs(edge.ctlPoint1.y)) * yDistance;
        edge.ctlPoint2.y =
          edge.ctlPoint2.y &&
          (edge.ctlPoint2.y / Math.abs(edge.ctlPoint2.y)) * yDistance;
      }

      let offset = {
        x: endPosition.x - startPosition.x,
        y: endPosition.y - startPosition.y
      };
      // Calculating arrow plotting coordinates
      let arrowSize = graphCfg.edgeEndPointArrowSize;
      // Calculate the control line
      let pLine = graphMath.getLineFx(
        offset.x + edge.ctlPoint2.x,
        offset.y + edge.ctlPoint2.y,
        offset.x,
        offset.y
      );

      // On the straight line where the control line is located, the intersection point between the two points of arrow V-shaped opening and the control line is calculated by taking the endpoint as the center point.
      let pointSet = graphMath.getPointsOnline(
        pLine.k,
        pLine.b,
        offset.x,
        offset.y,
        arrowSize * Math.SQRT1_2
      );

      let d1 = graphMath.getTheDistanceFrom2Point(
        offset.x + edge.ctlPoint2.x,
        offset.y + edge.ctlPoint2.y,
        pointSet.x1,
        pointSet.y1
      );
      let d2 = graphMath.getTheDistanceFrom2Point(
        offset.x + edge.ctlPoint2.x,
        offset.y + edge.ctlPoint2.y,
        pointSet.x2,
        pointSet.y2
      );

      // The nearest point to the control point is the desired intersection point.
      let targetPoint =
        d1 < d2
          ? {
              x: pointSet.x1,
              y: pointSet.y1
            }
          : {
              x: pointSet.x2,
              y: pointSet.y2
            };

      // Calculate the line where the two points of arrow's V-shaped opening are located
      let pLine2 = graphMath.getNormalLineFx(
        pLine.k,
        targetPoint.x,
        targetPoint.y
      );

      // Calculate the coordinates of the opening starting point of arrow
      let pointSet2 = graphMath.getPointsOnline(
        pLine2.k,
        pLine2.b,
        targetPoint.x,
        targetPoint.y,
        arrowSize * Math.SQRT1_2
      );

      extendEdges.push({
        edge,
        key: edge.startNode + ',' + edge.endNode,
        startAnchorPosition: startPosition,
        endAnchorPosition: endPosition,
        container: {
          width: '1px',
          height: '1px',
          left: `${startPosition.x}px`,
          top: `${startPosition.y}px`,
          zIndex: edge.zIndex
        },
        svg: {
          linePath: `M0 0 C${edge.ctlPoint1.x},${edge.ctlPoint1.y} ${offset.x +
            edge.ctlPoint2.x},${offset.y + edge.ctlPoint2.y} ${offset.x},${
            offset.y
          }`,
          transform: 'translate(0,0)',
          edgeType: edge.edgeType,
          arrow: `M${pointSet2.x1} ${pointSet2.y1} L${offset.x} ${offset.y} L${pointSet2.x2} ${pointSet2.y2}`,
          class: ''
        }
      });
    }

    return extendEdges;
  },
  /**
   * Nodes of the current focus palette, which are different from stored nodes, are mainly extended for the integration of UI display and experience
   */
  selectedGraphNodes(state, getters) {
    let selectedGraphYN = getters.selectedGraph
      ? getters.selectedGraph.nodes
      : [];
    let nodes = selectedGraphYN.filter(x => x.id !== graphCfg.virtualNodeId);
    let contributors = [];
    let experiments = [];
    if (getters.project) {
      contributors = getters.project.contributors;
      experiments = getters.project.experiments;
    }
    if (
      !(nodes.length > 0 && experiments.length > 0 && contributors.length > 0)
    ) {
      return [];
    }
    // node referenced expreiment must be exist
    let nodeRelationExperimentIdList = _(nodes.map(x => x.experimentId))
      .uniq()
      .value();
    let experimentIdList = _(experiments.map(x => x.id))
      .uniq()
      .value();
    let notExistExpCodeList = _(nodeRelationExperimentIdList)
      .pullAll(experimentIdList)
      .uniq()
      .value();
    if (notExistExpCodeList.length > 0) {
      throw Error(`node id "${notExistExpCodeList.join(',')}" not exist`);
    }
    let nodeList = [];
    for (const node of nodes) {
      let e = experiments.find(x => x.id === node.experimentId);
      let n = Object.assign({}, node);
      n.experiment = e;
      if (n.height) {
        n.height = n.height;
        e.settings.isActive = n.experiment
          ? n.experiment.settings.isActive
          : e.settings.isActive;
        e.result.isActive = n.experiment
          ? n.experiment.result.isActive
          : e.result.isActive;
      } else {
        if (e.settings.markdown || e.result.markdown) {
          n.height = 120;
        }
        if (!e.settings.markdown && !e.result.markdown) {
          n.height = 94;
        }
        if (e.settings.markdown && e.result.markdown) {
          n.height = 142;
        }
      }
      nodeList.push(n);
    }
    getters.selectedGraph.nodes = nodeList;
    return nodeList;
  },
  /**
   * Used to get virtual nodes in the current focus palette
   */
  selectedGraphVirtualNode: (state, getters) =>
    getters.selectedGraph
      ? getters.selectedGraph.nodes.find(x => x.id === graphCfg.virtualNodeId)
      : null
};

export default {
  namespaced: true,
  state,
  getters,
  actions: Object.assign(
    {},
    contributorActions,
    experimentActions,
    graphActions,
    nodeActions,
    edgeActions,
    ideaActions
  ),
  mutations: Object.assign(
    {},
    contributorMutations,
    experimentMutations,
    graphMutations,
    nodeMutations,
    edgeMutations,
    ideaMutations
  )
};
