import * as graphMath from '../../../helpers/graphMath';
import * as graphCfg from '../../../constants/peojectGraphConfig';
/**
 * Use only the current document
 */
function getSelectedGraph(store) {
  return store.getters['projectDetail/selectedGraph'];
}
/**
 * Verify the creation of the new edge and redraw the edge, delete the virtual node and edge
 */
function addEdageConfirm(state, payload) {
  _.vueRemove(
    getSelectedGraph(this).edges,
    x =>
      x.startNode === payload.startNode.id &&
      x.endNodeVirtualId === graphCfg.virtualNodeId
  );
  getSelectedGraph(this).edges.push(payload);
  this.commit('projectDetail/addEdageCancel');
}

/**
 * A new edge is added to the drawing board and implemented by virtual nodes whose coordinates are the current cursor position of the mouse.
 */
function addEdageBegin(state, payload) {
  let selectedGraph = getSelectedGraph(this);
  let startNode = selectedGraph.nodes.find(x => x.id === payload.startNodeId);
  if (!startNode) {
    return;
  }
  this.commit('projectDetail/addEdageCancel');
  let virtualEndNode = {
    id: graphCfg.virtualNodeId,
    location: {
      x: payload.cursorLocation.x,
      y: payload.cursorLocation.y,
      z: payload.cursorLocation.z
    }
  };
  virtualEndNode.location = graphMath.addLocationUnitPX(
    virtualEndNode.location
  );
  selectedGraph.nodes.push(virtualEndNode);
  let edge = graphMath.generateDefaultEdge(
    payload.startNodeId,
    graphCfg.virtualNodeId
  );
  selectedGraph.edges.push(edge);
}

/**
 * Cancel adding new edges and delete virtual nodes and edges added due to the introduction of virtual nodes
 */
function addEdageCancel(state) {
  _.vueRemove(
    getSelectedGraph(this).edges,
    x => x.endNodeVirtualId === graphCfg.virtualNodeId
  );
  _.vueRemove(
    getSelectedGraph(this).nodes,
    x => x.id === graphCfg.virtualNodeId
  );
}

/**
 * Update the coordinates of control points when dragging edge control points in the palette
 */
function dragControlPoint(state, payload) {
  let edge = _(getSelectedGraph(this).edges).find(
    _.pick(payload.edge, ['startNode', 'endNode'])
  );
}

/**
 * Update edge type
 */
function updateEdgeType(state, payload) {
  let fEdge = getSelectedGraph(this).edges.find(
    x => x.startNode === payload.startNode && x.endNode === payload.endNode
  );
  if (fEdge) {
    Object.assign(fEdge, _.pick(payload, ['edgeType']));
  }
}

/**
 * Delete the specified edge
 */
function removeEdage(state, edge) {
  _.vueRemove(
    getSelectedGraph(this).edges,
    x => x.startNode === edge.startNode && x.endNode === edge.endNode
  );
}

/**
 * socket
 */
function SOCKET_updateProjectEdges(state, payload) {
  let projectId = payload.projectId;
  let graphsId = payload.graphsId;
  let edgeId = payload.edgeId;
  let project = this.getters['projectCollection/project'](projectId);
  let currentRouterName = window.getVM().$router.history.current.name;
  if (currentRouterName === 'projectGraph') {
    let itemGraph = _.find(project.graphs, x => x.id === graphsId);
    _.vueRemove(itemGraph.edges, x => x.id === edgeId);
    if (payload.action !== 'delete') {
      itemGraph.edges.push(payload.data);
    }
  }
}

export default {
  addEdageConfirm,
  addEdageBegin,
  addEdageCancel,
  updateEdgeType,
  dragControlPoint,
  removeEdage,
  SOCKET_updateProjectEdges
};
