<template>
  <v-app
    id="inspire"
    :dark="darkMode"
    :class="[
      {
        'box-layout': boxLayout,
        'collapse-sidebar': collapseSidebar,
        'rtl-layout': rtlLayout
      }
    ]"
  >
    <router-view></router-view>
    <notifications
      group="ServerSideError"
      position="top right"
      animation-type="velocity"
    ></notifications>
    <v-snackbar
      v-model="globalSnackbarConfig.isOpen"
      :timeout="globalSnackbarTimeout"
      :top="globalSnackbarTop"
      :color="globalSnackbarColor"
    >
      <span v-text="globalSnackbarText"></span>
      <v-btn
        :color="globalSnackbarCloseButtonColor"
        flat
        @click="closeGlobalSnackbar"
      >
        <span v-once v-text="globalSnackbarCloseButtonText"></span>
      </v-btn>
    </v-snackbar>
    <ideas-tips></ideas-tips>
    <div class="imageWrapper-ico" style="opacity: 0">
      <slot>
        <div
          class="avar-ico-avatar"
          ref="imageWrapper"
          :style="'background-color:' + getAvarBackgtoundColor"
        >
          {{ userNameOne }}
        </div>
      </slot>
    </div>

    <v-alert
      class="change-error-message"
      :value="globalAlertShow"
      transition="scale-transition"
    >
      <div class="messages-box">
        <div class="ico">
          <span class="mdi mdi-alert-circle"></span>
        </div>
        <div class="messages-text" v-text="globalAlertText"></div>
      </div>
    </v-alert>
  </v-app>
</template>

<script>
  import { mapGetters } from 'vuex';
  import ideasTips from './components/ideasTips';

  export default {
    components: {
      ideasTips
    },
    computed: {
      ...mapGetters({
        darkMode: 'settings/darkMode',
        collapseSidebar: 'settings/collapseSidebar',
        boxLayout: 'settings/boxLayout',
        rtlLayout: 'settings/rtlLayout',
        selectedLocale: 'settings/selectedLocale',
        globalSnackbarConfig: 'settings/globalSnackbarConfig',
        globalSnackbarTimeout: 'settings/globalSnackbarTimeout',
        globalSnackbarTop: 'settings/globalSnackbarTop',
        globalSnackbarColor: 'settings/globalSnackbarColor',
        globalSnackbarText: 'settings/globalSnackbarText',
        globalSnackbarCloseButtonColor:
          'settings/globalSnackbarCloseButtonColor',
        globalSnackbarCloseButtonText: 'settings/globalSnackbarCloseButtonText',
        getAvarBackgtoundColor: 'session/getAvarBackgtoundColor',
        geturl: 'session/geturl',
        userName: 'session/userName',
        globalAlertShow: 'settings/globalAlertShow',
        globalAlertText: 'settings/globalAlertText'
      }),
      userNameOne() {
        return this.userName.slice(0, 1);
      }
    },
    created() {
      this.$store.commit(
        'projectCollection/autoSaveMessagesFun',
        this.$i18n.t('StackEdit.Layout.OTNoContentChanges')
      );
    },
    mounted() {
      let self = this;
      $(document).on('click', '.application', function (e) {
        let id = '';
        let className = '';
        if (e.target.id !== '') {
          id = `#${e.target.id}`;
        }
        if (e.target.className !== '' && typeof (e.target.className) !== 'object') {
          className = `.${e.target.className.replace(/\s+/g, '.')}`;
        }
        let payload = {
          topic: 'userEvent',
          userId: self.$store.getters['session/user'].id,
          url: window.location.href,
          mouseEvent: {
            type: e.type,
            path: e.target.tagName.toLowerCase() + id + className,
            altKey: e.altKey,
            ctrlKey: e.ctrlKey,
            metaKey: e.metaKey,
            shiftKey: e.shiftKey
          }
        };
        console.log(payload)
        setLog(payload);
      });
    },
    methods: {
      closeGlobalSnackbar() {
        this.$store.commit('settings/closeGlobalSnackbar');
      }
    }
  };
</script>

<style lang="scss" scoped>
  .imageWrapper-ico {
    position: absolute;
    z-index: -5;
    overflow: hidden;

    .avar-ico-avatar {
      width: 21.875rem;
      height: 21.875rem;
      line-height: 21.875rem;
      font-size: 10rem;
      font-weight: 400;
      color: #fff;
      border-radius: 50%;
      overflow: hidden;
      text-align: center;
      text-transform: capitalize;
    }
  }

  .fade-enter-active,
  .fade-leave-active {
    transition: all 0.3s ease;
  }

  .fade-enter {
    opacity: 1;
  }

  .fade-leave-to {
    opacity: 0;
  }
</style>
