import * as graphMath from '../../../helpers/graphMath';
import * as graphCfg from '../../../constants/peojectGraphConfig';

/**
 * Use only the current document
 */
function getSelectedGraph(store) {
  return store.getters['projectDetail/selectedGraph'];
}

/**
 * Transform the location expressions of node and edge in the sketchpad, and convert the number into the form with px units. It is mainly used for the transformation after obtaining data from the server, which is only used for the current document
 */
function parseGraphComponentsLocation(graph) {
  for (const node of graph.nodes) {
    node.location = graphMath.addLocationUnitPX(node.location);
  }
}

/**
 * Update the Local Sketchpad List
 */
function refreshGraphList(state, payload) {
  payload.graphList.forEach(g => parseGraphComponentsLocation(g));
  let project = this.getters['projectCollection/project'](payload.projectId);
  if (typeof project !== 'undefined') {
    project.graphs.splice(0, project.graphs.length, ...payload.graphList);
  }
}

function resetGraphsOrderIndex(state, payload) {
  let project = this.getters['projectCollection/project'](payload.projectId);
  payload.sortedGraphIdList.forEach((id, index) => {
    let g = project.graphs.find(x => x.id === id);
    g.orderIndex = index;
  });
}

/**
 * Update the local palette information for the specified ID
 */
function refreshGraphById(state, payload) {
  this.state.userGraph.userGraphList.selectedGraph = payload.graph.id;
  parseGraphComponentsLocation(payload.graph);
  let project = this.getters['projectCollection/project'](payload.projectId);
  _.vueRemove(project.graphs, x => x.id === payload.graph.id);
  project.graphs.push(payload.graph);
}

/**
 * Delete the local palette of the specified ID
 */
function deleteGraphById(state, payload) {
  let project = this.getters['projectCollection/project'](payload.projectId);
  _.vueRemove(project.graphs, x => x.id === payload.graphId);
}

function dragInItemNode(state, nodePayload) {
  let virtualEdge = getSelectedGraph(this).edges.find(
    x => x.endNode === graphCfg.virtualNodeId
  );
  let virtualNode = getSelectedGraph(this).nodes.find(
    x => x.id === graphCfg.virtualNodeId
  );

  let itemNode = this.getters['projectDetail/selectedGraphNodes'].find(
    x => x.id === nodePayload.itemNodeId
  );
  if (virtualEdge) {
    virtualNode = itemNode;
    virtualEdge.endNode = nodePayload.itemNodeId;
    virtualEdge.endNodeVirtualId = graphCfg.virtualNodeId;
    virtualEdge.location = itemNode.location;
    virtualEdge.isItemNode = true;
  }
}

function dragOutItemNode(state) {
  let virtualEdge = getSelectedGraph(this).edges.find(
    x => x.isItemNode === true
  );
  let virtualNode = getSelectedGraph(this).nodes.find(
    x => x.id === graphCfg.virtualNodeId
  );
  if (virtualEdge) {
    virtualEdge.isItemNode = false;
    virtualEdge.endNode = graphCfg.virtualNodeId;
  }
}

function activeNodeEdegZindex(state, payload) {
  if (payload.itemNodeArr.length > 0) {
    for (let i = 0; i < payload.itemNodeArr.length; i++) {
      payload.itemNodeArr[i].location.z = payload.setZindex - 2 * i;
    }
  }
  if (payload.itemEdgeArr.length > 0) {
    for (let i = 0; i < payload.itemEdgeArr.length; i++) {
      payload.itemEdgeArr[i].zIndex = payload.setZindex - 4 - 2 * i;
    }
  }
}

/**
 * Update the coordinates of the node in the drag-and-drop panel and the associated edges and control line coordinates of the edge
 */
function dragExperimentNode(state, payload) {
  let nodeId = payload.nodeId;
  let location = {
    x: parseInt(payload.x),
    y: parseInt(payload.y),
    z: parseInt(payload.z)
  };
  let node = getSelectedGraph(this).nodes.find(x => x.id === nodeId);

  Object.assign(node.location, location);
  let data = {
    nodeId: nodeId,
    node: node
  };
  this.commit('projectDetail/refreshNode', data);

  // node.location = graphMath.addLocationUnitPX(node.location);
}

/**
 * Add a node to the sketchpad. Note that this method only adds nodes, not experiments.
 */
function addNode(state, newNode) {
  let selectedGraph = getSelectedGraph(this);
  let node = selectedGraph.nodes.find(x => x.id === newNode.id);
  if (!node) {
    selectedGraph.nodes.push(newNode);
  }
}

/**
 * Update the basic information of the sketchpad, update nodes and edges, use the proprietary API
 */
function updateGraphBasicInfo(state, payload) {
  let fields = ['title'];
  if (getSelectedGraph(this).id === payload.id) {
    Object.assign(getSelectedGraph(this), _.pick(payload, fields));
  }
}

/**
 * Update the basic information of the sketchpad, update nodes and edges, use the proprietary API
 */
function userExpandGraph(state, payload) {
  let userGraphList = this.state.userGraph.userGraphList;
  let itemGraph = userGraphList.find(x => x.id === payload.graphId);
  if (itemGraph) {
    Object.assign(itemGraph.transform, payload.transform);
  }
}

/**
 * Update the coordinates of the node in the drag-and-drop panel and the associated edges and control line coordinates of the edge
 */
function shrinkExperimentNode(state, payload) {
  let selectedGraphNodes = this.getters['projectDetail/selectedGraph'].nodes;
  let node = selectedGraphNodes.find(x => x.id === payload.nodeId);
  node.height = payload.height;
}

/**
 * socket
 */
function SOCKET_updateProjectGraphs(state, payload) {
  let projectId = payload.projectId;
  let graphsId = payload.graphsId;
  let project = this.getters['projectCollection/project'](projectId);
  let itemGraph = _.find(project.graphs, x => x.id === graphsId);
  let currentRouterName = window.getVM().$router.history.current.name;
  if (payload.action !== 'delete') {
    payload.data.nodes = itemGraph ? itemGraph.nodes : [];
    payload.data.edges = itemGraph ? itemGraph.edges : [];
    let userGraphList = this.getters['userGraph/userGraphList'];
    let itemUserGraph = _.find(userGraphList, x => x.id === graphsId);
    let addUserGraph = {
      id: graphsId,
      transform: {
        scale: itemUserGraph ? itemUserGraph.transform.scale : 1,
        x: itemUserGraph ? itemUserGraph.transform.x : 0,
        y: itemUserGraph ? itemUserGraph.transform.y : 0,
        z: itemUserGraph ? itemUserGraph.transform.z : 0
      }
    };
    _.vueRemove(userGraphList, x => x.id === graphsId);
    userGraphList.push(addUserGraph);
    if (itemGraph) {
      Object.assign(itemGraph, payload.data);
    } else {
      project.graphs.push(payload.data);
    }
  } else if (currentRouterName === 'projectGraph') {
    let userSelectedGraph = this.getters['userGraph/userSelectedGraph'];
    if (userSelectedGraph === graphsId) {
      let index = project.graphs.findIndex(x => x.id === graphsId);
      let nextGraphsId =
        index === 0
          ? project.graphs[index + 1].id
          : project.graphs[index - 1].id;
      _.vueRemove(project.graphs, x => x.id === graphsId);
      let payloadSelectedGraph = {
        projectId: projectId,
        selectedGraph: nextGraphsId
      };
      this.dispatch(
        'userGraph/refreshSelectedGraphId',
        payloadSelectedGraph
      ).then(res => {
        this.dispatch('userGraph/refreshUserGraphList', projectId);
      });
      window
        .getVM()
        .$router.replace(`/projects/${projectId}/graphs/${nextGraphsId}`);
    } else {
      _.vueRemove(project.graphs, x => x.id === graphsId);
    }
  }
}
function SOCKET_updateGraphCursor(state, payload) {
  let colorArry = [
    '#F44336',
    '#E91E63',
    '#9C27B0',
    '#673AB7',
    '#3F51B5',
    '#2196F3',
    '#03A9F4',
    '#00BCD4',
    '#009688',
    '#4CAF50',
    '#8BC34A',
    '#FF9800',
    '#FF5722',
    '#795548',
    '#607D8B',
    '#212121',
    '#263238',
    '#3E2723',
    '#33691E',
    '#1B5E20',
    '#004D40',
    '#006064',
    '#01579B',
    '#0D47A1',
    '#1A237E',
    '#311B92',
    '#4A148C',
    '#880E4F',
    '#B71C1C'
  ];
  let color = colorArry[Math.floor(Math.random() * colorArry.length)];
  let userId = this.getters['session/user'].id;
  let projectId = payload.projectId;
  let graphsId = payload.graphsId;
  let currentRouterName = window.getVM().$router.history.current.name;
  let selectedProjectId = this.getters['projectDetail/selectedProjectId'];
  let userSelectedGraph = this.getters['userGraph/userSelectedGraph'];
  let payloadUserId = payload.operator.id;
  if (
    currentRouterName === 'projectGraph' &&
    projectId === selectedProjectId &&
    userSelectedGraph === graphsId
  ) {
    if (userId !== payloadUserId) {
      let selectedGraphNodes = this.getters['projectDetail/selectedGraphNodes'];
      if (selectedGraphNodes.length > 0) {
        // console.log(payload.mouseData);
        payload.color = color;
        let cursorArry = state.updateGraphCursorList;
        let userGraph = this.getters['userGraph/userGraphList'];
        let itemGraph = _.find(userGraph, x => x.id === graphsId);
        payload.mouseData.x = payload.mouseData.x;
        payload.mouseData.y = payload.mouseData.y;
        // console.log(payload.mouseData)
        let oldPayload = _.find(
          cursorArry,
          x => x.operator.id === payload.operator.id
        );
        if (!oldPayload) {
          state.updateGraphCursorList.push(payload);
        } else {
          let OldColor = oldPayload.color;
          payload.color = OldColor;
          Object.assign(oldPayload, payload);
        }
      }
    }
  }
}
export default {
  refreshGraphList,
  resetGraphsOrderIndex,
  refreshGraphById,
  deleteGraphById,
  dragExperimentNode,
  addNode,
  updateGraphBasicInfo,
  userExpandGraph,
  dragInItemNode,
  dragOutItemNode,
  activeNodeEdegZindex,
  shrinkExperimentNode,
  SOCKET_updateProjectGraphs,
  SOCKET_updateGraphCursor
};
