import NProgress from 'nprogress';
import api from 'Api';

const states = {
  sharesList: [],
  sharedSharesList: []
};

// getters
const getters = {
  sharesListGetters(states) {
    return states.sharesList;
  },
  getsharedSharesListGetters(states) {
    return states.sharedSharesList;
  }
};

const actions = {
  getShares(context, payload) {
    NProgress.start();
    return new Promise((resolve, rejiect) => {
      api
        .get(`/shares?category=${payload.category}&types=${payload.types}`)
        .then(response => {
          context.commit('updateSharesList', response.data);
          resolve(response);
        })
        .catch(error => {
          // console.log(error);
          rejiect(error);
        })
        .finally(() => NProgress.done());
    });
  },
  getsharedSharesList(context, payload) {
    NProgress.start();
    return new Promise((resolve, rejiect) => {
      api
        .get(
          `/shares?category=${payload.category}&types=${payload.types}&projectId=${payload.project}`
        )
        .then(response => {
          context.commit('updatesharedSharesList', response.data);
          resolve(response);
        })
        .catch(error => {
          // console.log(error);
          rejiect(error);
        })
        .finally(() => NProgress.done());
    });
  },
  deleteSharedShare(context, payload) {
    NProgress.start();
    return new Promise((resolve, rejiect) => {
      api
        .delete(`/shares/${payload.shareId}?category=${payload.category}`)
        .then(response => {
          context.commit('deleteSharedShareMutations', payload);
          resolve(response);
        })
        .catch(error => {
          // console.log(error);
          rejiect(error);
        })
        .finally(() => NProgress.done());
    });
  }
};

const mutations = {
  updateSharesList(states, data) {
    states.sharesList = data;
  },
  updatesharedSharesList(states, data) {
    states.sharedSharesList = data;
  },
  deleteSharedShareMutations(states, payload) {
    payload.category === 'favorite'
      ? _.vueRemove(states.sharesList, x => x.id === payload.shareId)
      : _.vueRemove(states.sharedSharesList, x => x.id === payload.shareId);
  }
};

export default {
  namespaced: true,
  state: states,
  getters,
  actions,
  mutations
};
