import actions from './actions';
import mutations from './mutations';

const state = {
  projectList: [],
  descDialogDisplayShow: false,
  descDialogDisplayShowToId: '',
  autoSaveMessages: '',
  myDeleteProject: {}
};

const getters = {
  projectList: state => state.projectList,
  /**
   * Find peoject from project list
   */
  project: state => projectId =>
    state.projectList.find(x => x.id === projectId),
  descDialogDisplayShowGetters(state) {
    return state.descDialogDisplayShow;
  },
  descDialogDisplayShowToIdGetters(state) {
    return state.descDialogDisplayShowToId;
  },
  autoSaveMessagesGetters(state) {
    return state.autoSaveMessages;
  },
  myDeleteProject(state) {
    return state.myDeleteProject;
  }
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};
