import api from 'Api';
import socketAndHttp from '../../../services/socketAndHttp';
/**
 * Get a list of project ideas and refresh local data
 */
async function refreshIdeaList(context, projectId) {
  await this.dispatch('projectCollection/getProjectById', projectId);
  let response = await api.get(`/projects/${projectId}/ideas`);
  let data = {
    projectId,
    ideaList: response.data
  };
  this.commit('projectDetail/refreshIdeaList', data);
  return data;
}

/**
 * Get the idea information specified by ideaId in the project and cache it
 */
async function refreshIdea(context, payload) {
  let response = await api.get(
    `/projects/${payload.projectId}/ideas/${payload.ideaId}`
  );
  let data = {
    projectId: payload.projectId,
    idea: response.data
  };
  this.commit('projectDetail/refreshIdea', data);
  return data;
}

/**
 * Create new ideas
 */
async function createIdea(context, payload) {
  let response = await socketAndHttp(
    `/projects/${payload.projectId}/ideas`,
    'post',
    payload.idea
  );
  let data = {
    projectId: payload.projectId,
    idea: response.data
  };
  this.commit('projectDetail/refreshIdea', data);
  return data;
}

/**
 * Update idea
 */
async function updateIdea(context, payload) {
  let response = await socketAndHttp(
    `/projects/${payload.projectId}/ideas/${payload.idea.id}`,
    'put',
    payload.idea
  );
  let data = {
    projectId: payload.projectId,
    idea: response.data
  };
  this.commit('projectDetail/refreshIdea', data);
  return data;
}

/**
 * Delete idea
 */
async function deleteIdea(context, payload) {
  await socketAndHttp(
    `/projects/${payload.projectId}/ideas/${payload.ideaId}`,
    'delete'
  );
  this.commit('projectDetail/deleteIdea', payload);
  return payload;
}

/**
 * Get the project ideas for the specified id, try to get the local cached project, and get it from the server if it does not exist locally
 */
async function getProjectIdeasById(context, projectId) {
  let project = this.getters['projectCollection/project'](projectId);
  return new Promise((resolve, reject) => {
    if (project) {
      if (project.graphs.length > 0) {
        let payload = {
          projectId: projectId,
          ideaList: project.ideas
        };
        resolve(payload);
      } else {
        this.dispatch('projectDetail/refreshIdeaList', projectId)
          .then(p => resolve(p))
          .catch(e => reject(e));
      }
    } else {
      this.dispatch('projectDetail/refreshIdeaList', projectId)
        .then(p => resolve(p))
        .catch(e => reject(e));
    }
  });
}

export default {
  refreshIdeaList,
  createIdea,
  refreshIdea,
  updateIdea,
  deleteIdea,
  getProjectIdeasById
};
