<template>
  <v-menu
    offset-y
    origin="right top"
    left
    content-class="user-info-card"
    transition="slide-y-transition"
    min-width="18rem"
  >
    <v-btn slot="activator" icon large class="ma-0">
      <user-avata-circle
        :user-name="userName"
        :user-avata="userAvatar"
        style="height:2rem;width: 2rem"
      ></user-avata-circle>
    </v-btn>
    <div class="dropdown-content pa-2 text-center">
      <user-avata-circle
        :user-name="userName"
        :user-avata="userAvatar"
        class="ma-3"
      ></user-avata-circle>
      <div class="nick-name font-lg ma-2">
        <span v-text="userName"></span>
      </div>
      <div class="email grey--text ma-2">
        <span v-text="userEmail.email"></span>
      </div>
      <div class="dropdown-list grey--text ma-3">
        <div class="edit-profile" @click="openRouter('/user/settings')">
          <i class="mdi mdi-account-edit dash-icon"></i>
          <span class="font-sm">{{ trans.Edit }}</span>
        </div>
        <div class="notices" @click="openRouter('/user/message')">
          <i class="mdi mdi-bell-outline dash-icon"></i>
          <span class="font-sm">{{ trans.Notice }}</span>
        </div>
        <div class="logout" @click="logout">
          <i class="mdi mdi-power dash-icon"></i>
          <span class="font-sm">{{ trans.Logout }}</span>
        </div>
      </div>
    </div>
  </v-menu>
</template>

<script>
  import { mapGetters } from 'vuex';
  import userAvataCircle from 'Components/userAvataCircle';

  export default {
    name: 'UserInfoCard',
    components: {
      userAvataCircle
    },
    data() {
      return {
        trans: {
          Edit: this.$i18n.t('UserInfoCard.Edit'),
          Notice: this.$i18n.t('UserInfoCard.Notice'),
          Logout: this.$i18n.t('UserInfoCard.Logout')
        }
      };
    },
    computed: {
      ...mapGetters({
        userAvatar: 'session/userAvatar',
        userName: 'session/userName',
        userEmail: 'session/userEmail'
      })
    },
    methods: {
      openRouter(to) {
        this.$router.push(to);
      },
      logout() {
        this.$store.dispatch('session/logout');
      }
    }
  };
</script>
