import api from 'Api';
import socketAndHttp from '../../../services/socketAndHttp';
/**
 * Save the association edge after creating the Association
 */
async function saveEdge(context, payload) {
  let response = await socketAndHttp(
    `/projects/${payload.projectId}/graphs/${payload.graphId}/edges`,
    'post',
    payload.edge
  );
  this.commit('projectDetail/addEdageConfirm', response.data);
  return response.data;
}

/**
 * Delete the associated edge
 */
async function deleteEdge(context, payload) {
  let response = await socketAndHttp(
    `/projects/${payload.projectId}/graphs/${payload.graphId}/edges/${payload.edge.id}`,
    'delete'
  );
  let edge = {
    startNode: payload.startNode,
    endNode: payload.endNode
  };
  this.commit('projectDetail/removeEdage', edge);
  return response.data;
}

/**
 * Modify the associated edge type
 */
async function updateEdgeType(context, payload) {
  let response = await socketAndHttp(
    `/projects/${payload.projectId}/graphs/${payload.graphId}/edges/${payload.edge.id}`,
    'patch',
    payload.edge
  );

  this.commit('projectDetail/updateEdgeType', payload.edge);
  return response.data;
}

/**
 * Modify the position of associated edge control points
 */
async function updateControlPoint(context, payload) {
  let response = await api.patch(
    `/projects/${payload.projectId}/graphs/${payload.graphId}/edges/${payload.edge.id}`,
    payload.edge
  );
  this.commit('projectDetail/dragControlPoint', payload);
  return response.data;
}

export default {
  saveEdge,
  deleteEdge,
  updateEdgeType,
  updateControlPoint
};
