<template>
  <div class="">
    <v-dialog
      v-model="descDialogDisplay"
      transition="dialog-bottom-transition"
      persistent
      width="50%"
      :content-class="
        'desc-dialog-display stack-edit-dialog desc-dialog-display-top stackEdit-plus ' +
          stackEditContainerClasses
      "
    >
      <v-card v-if="descDialogDisplay">
        <v-container class="container-reset pr-0 pl-0">
          <div class="desc-tab-content">
            <v-btn
              slot="activator"
              icon
              class="ma-0 desc-close-btn"
              @click.stop="close"
            >
              <i class="mdi mdi-close theme--light grey--text"></i>
            </v-btn>
            <v-tabs
              v-model="tabActive"
              centered
              color="tab-orange"
            >
              <v-tabs-slider color="orange"></v-tabs-slider>
              <template v-for="item in projectTitle">
                <v-tab
                  :key="item.id"
                  :ripple="false"
                  @click="getMarkdownContent"
                  :href="'#' + item.id"
                >
                  <span v-text="item.title"></span>
                </v-tab>
              </template>

              <v-tabs-items class="pt-4 pl-4 pr-4 stack-edit-box-overflow">
                <!--Project details-->
                <v-tab-item value="abstract">
                  <v-card flat>
                    <div class="project-tab-item pa-2 pt-4">
                      <div
                        class="tab-item-content"
                        style="flex-direction: column;"
                      >
                        <v-flex class="public-input-box" style="flex: unset; margin-bottom: 0;">
                          <v-text-field
                            ref="projectNameContent"
                            v-model="projectInfo.title"
                            :label="trans.FNProjectName"
                            outline
                            height="40px"
                            :rules="rules.projectNameRequired"
                            validate-on-blur
                            :placeholder="trans.OTEnterProjectName"
                            color="normal"
                          ></v-text-field>
                        </v-flex>
                        <v-flex>
                          <div class="project-info">
                            <stack-edit-editor class="project-info-desc new-privew-desc"></stack-edit-editor>
                          </div>
                        </v-flex>
                      </div>
                    </div>
                  </v-card>
                </v-tab-item>
                 <!--Question Definition-->
                 <!-- <v-tab-item value="problemDefinition">
                  <v-card flat>
                    <div class="project-tab-item pa-2 pt-0">
                      <div class="tab-question-box">
                        <div class="tab-item-content stack-puls-dom">
                          <div
                            class="flex public-input-box stack-edit-box stack-edit-box-height"
                          >
                            <stack-edit-editor
                              v-if="stackEditQuestionShow"
                            ></stack-edit-editor>
                          </div>
                        </div>
                      </div>
                    </div>
                  </v-card>
                </v-tab-item> -->
              </v-tabs-items>
            </v-tabs>
            <div class="project-tab-footer"></div>
          </div>
        </v-container>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
  import projectInfoTabIdeas from '../../components/projectInfoTabIdeas';
  import ideasListContent from '../../components/ideasListContent';
  import StackEditEditor from '../../components/StackEdit';
  import { mapGetters, mapState, mapActions } from 'vuex';
  import editorSvc from '../../components/StackEdit/services/editorSvc';

  export default {
    name: 'DescriptionDialog',
    components: {
      projectInfoTabIdeas,
      ideasListContent,
      StackEditEditor
    },
    props: {
      projectInfoObj: {
        type: Object
      }
    },
    data() {
      return {
        lockTimer: null,
        timer: null,
        uniqeId: 'descriptionDialog' + Date.now(),
        tabActive: 'abstract',
        trans: {
          OTNoSaveTips: this.$i18n.t('ProjectPage.OTNoSaveTips'),
          OTSaveSuccessful: this.$i18n.t('ProjectPage.OTSaveSuccessful'),
          OTAutoSaveSuccessful: this.$i18n.t(
            'ProjectPage.OTAutoSaveSuccessful'
          ),
          OTConfirmProjectSave: this.$i18n.t(
            'ProjectPage.OTConfirmProjectSave'
          ),
          OTConfirmNoSave: this.$i18n.t('ProjectPage.OTConfirmNoSave'),
          OTConfirmSave: this.$i18n.t('ProjectPage.OTConfirmSave'),

          ProjectDescription: this.$i18n.t('ProjectPage.ProjectDescription'),
          ProblemDefinitionTitle: this.$i18n.t(
            'ProjectPage.ProblemDefinitionTitle'
          ),
          EvaluationCriteriaTitle: this.$i18n.t(
            'ProjectPage.EvaluationCriteriaTitle'
          ),
          DataTitle: this.$i18n.t('ProjectPage.DataTitle'),
          FNProjectName: this.$i18n.t('ProjectManage.FNProjectName'),
          FNProjectAbstract: this.$i18n.t('ProjectManage.FNProjectAbstract'),
          DataInputPlaceholder: this.$i18n.t(
            'ProjectPage.DescriptionDialog.DataInputPlaceholder'
          ),
          QuestionContent: this.$i18n.t(
            'ProjectPage.DescriptionDialog.QuestionContent'
          ),
          EvaluationContent: this.$i18n.t(
            'ProjectPage.DescriptionDialog.EvaluationContent'
          ),
          Save: this.$i18n.t('ProjectPage.CreateExperimentDialog.OTSave'),
          ideasEmpty: this.$i18n.t('ProjectPage.ProjectGraph.ideasEmpty'),
          OTEnterProjectName: this.$i18n.t(
            'ProjectPage.DescriptionDialog.OTEnterProjectName'
          ),
          OTEnterProjectAbstract: this.$i18n.t(
            'ProjectPage.DescriptionDialog.OTEnterProjectAbstract'
          ),
          OTEnterData: this.$i18n.t(
            'ProjectPage.DescriptionDialog.OTEnterData'
          ),
          OTProjectNameSame: this.$i18n.t(
            'ProjectPage.DescriptionDialog.OTProjectNameSame'
          ),
          OTNoContentChanges: this.$i18n.t(
            'StackEdit.Layout.OTNoContentChanges'
          ),
          OTSaveing: this.$i18n.t('StackEdit.Layout.OTSaveing'),
          OTAllChangesSaved: this.$i18n.t('StackEdit.Layout.OTAllChangesSaved')
        },
        projectTitle: [
          {
            id: 'abstract',
            title: this.$i18n.t('ProjectPage.DescriptionDialog.Project')
          }
        ],
        rules: {
          projectNameRequired: [
            value => !!value || this.trans.OTEnterProjectName,
            value =>
              !_.some(
                this.projectList,
                item => item.id !== this.projectInfo.id && item.title === value && item.creator === this.user.id
              ) || this.trans.OTProjectNameSame
          ]
        },

        stackEditQuestionShow: false,
        stackEditEvaluationShow: false,
        stackEditDataShow: false,
        ideasContentObj: {
          action: 'create',
          content: {
            title: '',
            content: ''
          }
        }
      };
    },
    computed: {
      ...mapGetters({
        projectList: 'projectCollection/projectList',
        descDialogDisplay: 'projectCollection/descDialogDisplayShowGetters',
        autoSaveMessages: 'projectCollection/autoSaveMessagesGetters',
        user: 'session/user',
      }),
      ...mapGetters('stackEdit/layout', ['styles']),

      projectInfo() {
        return this.projectInfoObj;
      },

      stackEditContainerClasses() {
        let classes = [];
        if (this.styles.expanded) {
          classes.push('stack-edit-plus-active');
        }
        return classes;
      }
    },
    mounted() {

    },
    watch: {
      // Monitor project edit box data changes and save automatically
      descDialogDisplay: function(newValue, oldValue) {
        if (newValue) {
          // Click refresh, modify routing address, close window, prompt whether to reload
          window.onbeforeunload = function(e) {
            e = e || window.event;
            // Compatible with previous versions of IE8 and Firefox 4
            if (e) {
              e.returnValue = '';
            }
            // Chrome, Safari, Firefox 4+, Opera 12+ , IE 9+
            return '';
          };
          this.timer = setInterval(() => {
            this.autoSave();
          }, 5000);
          this.lockTimer = setInterval(() => {
            this.$store.dispatch('projectCollection/addLock', {
              target: `/projects/${this.projectInfo.id}`
            });
          }, 60000);
          this.updateProjectInfoAbstract();
        } else {
          // Cancel the prompt pop-up window and timer when the project edit box closes
          window.onbeforeunload = null;
          clearInterval(this.timer);
          clearInterval(this.lockTimer);
          this.timer = null;
          this.lockTimer = null;

          this.$store.dispatch('projectCollection/unLock', {
            target: `/projects/${this.projectInfo.id}`
          });
        }
      }
    },
    // Cancel Timer before Component Destruction
    beforeDestroy() {
      window.onbeforeunload = null;
      clearInterval(this.timer);
      clearInterval(this.lockTimer);
      this.timer = null;
      this.lockTimer = null;
    },
    methods: {
      ...mapActions('stackEdit/data', ['toggleExpanded']),
      updateProjectInfoAbstract() {
        let abstract = this.projectInfo.abstract
        let value = {
          fileId: "abstract",
          text: abstract.markdown
        };
        this.$store.commit('stackEdit/patchMarkdown', value);
      },
      getMarkdownContent() {
        let field = this.projectInfo["abstract"];
        field.markdown = this.$store.state.stackEdit.markdown.text;
        field.editorHtml = editorSvc.editorElt.innerHTML;
        field.previewHtml = editorSvc.previewElt.innerHTML;
      },
      closeLayout() {
        this.toggleExpanded(false);
        this.$store.commit('projectCollection/descDialogDisplayShowFun', false);
        this.$store.commit(
          'projectCollection/descDialogDisplayShowToIdFun',
          ''
        );
        this.tabActive = 'abstract';
        this.stackEditQuestionShow = false;
        this.stackEditEvaluationShow = false;
        this.stackEditDataShow = false;
      },
      async close() {
        // Close the project editing rectification, check whether the data has changed, if it has changed, display the inquiry box, and save it.
        let result = await this.autoSave();
        if (result) {
          this.closeLayout();
        }
      },
      // Automatic Preservation Method
      autoSave() {
        if (!this.$refs.projectNameContent.validate(true)) {
          this.tabActive = 'abstract';
          return false;
        }
        let self = this;
        self.getMarkdownContent();
        let payload = {
          id: self.projectInfo.id,
          title: self.projectInfo.title,
          abstract: self.projectInfo.abstract,
        };
        let dataChange = '';
        let projectId = payload.id;
        let keys = Object.keys(payload);
        let originProject = self.$store.getters['projectCollection/project'](
          projectId
        );
        if (!originProject || _.isEqual(_.pick(originProject, keys), payload)) {
          self.$store.commit(
            'projectCollection/autoSaveMessagesFun',
            this.trans.OTNoContentChanges
          );
        } else {
          self.$store.commit(
            'projectCollection/autoSaveMessagesFun',
            this.trans.OTSaveing
          );
          self.$store
            .dispatch('projectCollection/updateProject', payload)
            .then(res => {
              setTimeout(() => {
                self.$store.commit(
                  'projectCollection/autoSaveMessagesFun',
                  this.trans.OTAllChangesSaved
                );
              }, 1000);
            });
        }
        return true;
      }
    }
  };
</script>
