<template>
  <div class="security-content-box">
    <div class="security-box user-account-box">
      <div class="user-account">
        <div class="account-infoform mt-0">
          <div class="account-field-box">
            <div class="account-field-content">
              <div class="account-value">
                <div class="input-title">{{ trans.OTOldPassword }}</div>
                <input
                  v-model="oldPassword"
                  class="settings-input"
                  type="password"
                  :placeholder="trans.EnterOldPassword"
                />
              </div>
              <div class="account-value">
                <div class="input-title">{{ trans.OTNewPassword }}</div>
                <input
                  v-model="newPassword"
                  class="settings-input"
                  type="password"
                  :placeholder="trans.EnterNewPassword"
                />
              </div>
              <div class="account-value">
                <div class="input-title">{{ trans.OTConfirmPassword }}</div>
                <input
                  v-model="confirmedPassword"
                  class="settings-input"
                  type="password"
                  :placeholder="trans.EnterConfirmPassword"
                />
              </div>
            </div>
          </div>
        </div>
        <div class="setting-error">
          {{ settingsErrorText }}
        </div>
        <v-btn
          round
          color="orange"
          :disabled="disabled"
          class="settings-button mt-3"
          @click="accountSave"
          v-text="trans.AccountSave"
        ></v-btn>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Security',
    data() {
      return {
        oldPassword: '',
        newPassword: '',
        confirmedPassword: '',
        settingsErrorText: '',
        trans: {
          OldPassword: this.$i18n.t('Session.Account.OldPassword'),
          NewPassword: this.$i18n.t('Session.Account.NewPassword'),
          ConfirmPassword: this.$i18n.t('Session.Account.ConfirmPassword'),
          EnterOldPassword: this.$i18n.t('Session.Account.EnterOldPassword'),
          EnterNewPassword: this.$i18n.t('Session.Account.EnterNewPassword'),
          EnterConfirmPassword: this.$i18n.t(
            'Session.Account.EnterConfirmPassword'
          ),
          OTOldPassword: this.$i18n.t('Session.Account.OTOldPassword'),
          OTNewPassword: this.$i18n.t('Session.Account.OTNewPassword'),
          OTConfirmPassword: this.$i18n.t('Session.Account.OTConfirmPassword'),
          AccountSave: this.$i18n.t('Session.Account.AccountSave'),
          newPassword: this.$i18n.t('password'),
          confirmedPassword: this.$i18n.t('confirmedPassword'),
          OTPasswordRules: this.$i18n.t(
            'Session.ResetPassword.OTPasswordRules'
          ),
          OTPasswordRulesTs: this.$i18n.t(
            'Session.ResetPassword.OTPasswordRulesTs'
          ),
          OTOldPasswordError: this.$i18n.t('Session.Account.OTOldPasswordError')
        }
      };
    },
    created() {
      const passwordRules = {
        alpha_dash: true,
        min: 6,
        regex: /^.*(\D\d)|(\d\D).*$/
      };

      let v = this.$validator;

      let newField = v.attach({
        name: 'newPassword',
        alias: this.trans.newPassword,
        getter: () => this.newPassword,
        rules: Object.assign(
          {
            confirmed: 'confirmedPassword'
          },
          passwordRules
        )
      });

      let confirmField = v.attach({
        name: 'confirmedPassword',
        alias: this.trans.confirmedPassword,
        getter: () => this.confirmedPassword,
        rules: passwordRules
      });

      newField.dependencies.push({
        name: 'confirmed',
        field: confirmField
      });
    },
    computed: {
      disabled() {
        let type =
          this.oldPassword === '' ||
          this.newPassword === '' ||
          this.confirmedPassword === '';
        if (type) {
          return true;
        } else {
          return false;
        }
      }
    },
    methods: {
      async accountSave() {
        let v = this.$validator;
        let validateFailed = !(await v.validate());
        let errors = await v.errors.all();
        if (validateFailed) {
          switch (v.errors.items[0].field) {
            case 'newPassword':
              if (v.errors.items[0].rule === 'confirmed') {
                this.settingsErrorText = errors[0];
                break;
              }
              if (v.errors.items[0].rule === 'alpha_dash') {
                this.settingsErrorText = this.trans.OTPasswordRulesTs;
                break;
              }
            case 'confirmedPassword':
              if (v.errors.items[0].rule === 'confirmed') {
                this.settingsErrorText = errors[0];
                break;
              }
            default:
              this.settingsErrorText = this.trans.OTPasswordRules;
              break;
          }
        } else {
          this.settingsErrorText = '';
          if (this.newPassword !== '' && this.settingsErrorText === '') {
            try {
              let passwords = {
                originalPassword: this.oldPassword,
                newPassword: this.newPassword
              };
              await this.$store.dispatch('user/resetPassword', passwords);
            } catch (error) {
              if (error.response.status === 422) {
                this.settingsErrorText = this.trans.OTOldPasswordError;
              }
            }
          }
        }
      }
    }
  };
</script>
