      width: 34rem;
      text-align: center;
      font-size: 14px;
      color: #ababab;
      .account-field-content {
        input {
          color: #333;
        }
      }
    }
  }
  ::-webkit-input-placeholder {
    /* WebKit browsers */
    color: #ccc;
  }

  :-moz-placeholder {
    /* Mozilla Firefox 4 to 18 */
    color: #ccc;
  }

  ::-moz-placeholder {
    /* Mozilla Firefox 19+ */
    color: #ccc;
  }

  :-ms-input-placeholder {
    /* Internet Explorer 10+ */
    color: #ccc;
  }

  input:-webkit-autofill,
  input:-webkit-autofill:hover,
  input:-webkit-autofill:focus {
