import * as graphCfg from '../constants/peojectGraphConfig';

export function parseIntWithZeroDefault(num) {
  return _.toInteger(parseInt(num));
}

export function removeLocationUnit(location) {
  return {
    x: parseIntWithZeroDefault(location.x),
    y: parseIntWithZeroDefault(location.y),
    z: location.z
  };
}

export function addLocationUnitPX(location) {
  let unit = 'px';
  return {
    x: parseIntWithZeroDefault(location.x) + unit,
    y: parseIntWithZeroDefault(location.y) + unit,
    z: location.z
  };
}

/**
 * Linear functions passing through the two points are calculated according to (x1, y1) and (x2, y2)
 * k is undefined:The line is x = C,
 * @param {*} x1
 * @param {*} y1
 * @param {*} x2
 * @param {*} y2
 */
export function getLineFx(x1, y1, x2, y2) {
  x1 = Math.round(x1);
  y1 = Math.round(y1);
  x2 = Math.round(x2);
  y2 = Math.round(y2);
  // console.log(x1+'-'+y1+'-'+x2+'-'+y2)
  if (x1 === x2 && y1 === y2) {
    return 0;
    // throw 'arguments is invalid,there is the same point';
  }

  if (x1 === x2) {
    return {
      k: void 0,
      b: x1
    };
  }

  let k = (y2 - y1) / (x2 - x1);
  let b = y1 - k * x1;
  return {
    k,
    b
  };
}

/**
 * Calculate the normal function of the point (x0, y0) perpendicular to the straight line of slope k and passing through
 * @param {*} k
 * @param {*} x0
 * @param {*} y0
 */
export function getNormalLineFx(k, x0, y0) {
  x0 = Math.round(x0);
  y0 = Math.round(y0);
  if (k === void 0) {
    return {
      k: 0,
      b: y0
    };
  }

  if (k === 0) {
    return {
      k: void 0,
      b: x0
    };
  }

  let k_ = -1 / k;
  let b = y0 - k_ * x0;
  return {
    k: k_,
    b
  };
}

/**
 * Two Points Calculating the Euclidean Distance of Line Upper Distance (x0, y0) as R
 * @param {*} k
 * @param {*} b
 * @param {*} x0
 * @param {*} y0
 * @param {*} r
 */
export function getPointsOnline(k, b, x0, y0, r) {
  x0 = Math.round(x0);
  y0 = Math.round(y0);
  if (k === void 0) {
    return {
      x1: x0,
      y1: y0 + r,
      x2: x0,
      y2: y0 - r
    };
  }

  if (k === 0) {
    return {
      x1: x0 + r,
      y1: y0,
      x2: x0 - r,
      y2: y0
    };
  }

  let a = 1 + Math.pow(k, 2);
  let b_ = 2 * (k * (b - y0) - x0);
  let c = Math.pow(x0, 2) + Math.pow(b - y0, 2) - Math.pow(r, 2);

  let m = Math.sqrt(Math.pow(b_, 2) - 4 * a * c);

  let x1 = (-b_ + m) / (2 * a);
  let y1 = k * x1 + b;

  let x2 = (-b_ - m) / (2 * a);
  let y2 = k * x2 + b;

  let rst = {
    x1,
    y1,
    x2,
    y2
  };
  Object.getOwnPropertyNames(rst).forEach(
    x => (rst[x] = Math.round(rst[x] * 1000) / 1000)
  );
  return rst;
}

/**
 * Calculate the Euclidean Distance between (x1, y1) and (x2, y2)
 * @param {*} x1
 * @param {*} y1
 * @param {*} x2
 * @param {*} y2
 */
export function getTheDistanceFrom2Point(x1, y1, x2, y2) {
  return (
    Math.round(
      Math.abs(Math.sqrt(Math.pow(x1 - x2, 2) + Math.pow(y1 - y2, 2))) * 1000
    ) / 1000
  );
}

export function computNearestPosition(positionArrayA, positionArrayB) {
  if (!(_.isArray(positionArrayA) && _.isArray(positionArrayB))) {
    throw new Error('arguments must be arraies');
  }

  let min = {};
  for (let i = 0; i < positionArrayA.length; i++) {
    for (let j = 0; j < positionArrayB.length; j++) {
      let pointA = positionArrayA[i];
      let pointB = positionArrayB[j];
      let distance = getTheDistanceFrom2Point(
        pointA.x,
        pointA.y,
        pointB.x,
        pointB.y
      );
      if (_.isEmpty(min) || distance < min.distance) {
        min.distance = distance;
        min.IndexA = i;
        min.IndexB = j;
      }
    }
  }

  return [positionArrayA[min.IndexA], positionArrayB[min.IndexB]];
}

/**
 * Generate edges between two points according to two given nodes
 * @param {*} startNode
 * @param {*} endNode
 */
export function generateDefaultEdge(startNodeId, endNodeId) {
  if (!(startNodeId && endNodeId)) {
    return void 0;
  }

  let edge = {
    edgeType: 'Motivate',
    startNode: startNodeId,
    endNode: endNodeId,
    zIndex: graphCfg.virtualNodeId
  };
  return edge;
}

/**
 * Check whether loops exist in the graph when adding a new edge specifying endNodeId for checking before adding a new edge
 * True: Existence loop
 * False: no loop exists
 */
export function checkLoop(graph, endNodeId) {
  let virtualEdge = graph.edges.find(
    x => x.endNodeVirtualId === graphCfg.virtualNodeId
  );
  let edges = graph.edges
    .filter(x => x.endNodeVirtualId !== graphCfg.virtualNodeId)
    .map(x =>
      Object({
        startNode: x.startNode,
        endNode: x.endNode
      })
    )
    .concat([
      {
        startNode: virtualEdge.startNode,
        endNode: endNodeId
      }
    ]);
  let nodes = graph.nodes
    .filter(x => x.id !== graphCfg.virtualNodeId)
    .map(x =>
      Object({
        id: x.id
      })
    )
    .concat({
      id: endNodeId
    });

  // evey node only have an edge in but can have multi out edges
  if (
    _(edges)
      .map(x => x.endNode)
      .uniq()
      .value().length !== edges.length
  ) {
    return true;
  }

  return checkLoopInUndirectedGraph(nodes, edges);
}

/**
 * Check whether there is a closed loop in undirected graph Graph, which is composed of nodes & edges
 */
export function checkLoopInUndirectedGraph(nodes, edges) {
  if (_.some(edges, x => x.startNode === x.endNode)) {
    return true;
  }

  for (const node of nodes) {
    if (
      !node.visited &&
      checkLoopInUndirectedGraphWithVertex(node, nodes, edges)
    ) {
      return true;
    }
  }

  return false;
}

/**
 * Check whether the graph with vertex has rings
 * @param {*} vertex Unvisited nodes
 * @param {*} nodes Nodes in Undirected Graphs
 * @param {*} edges Edges in Undirected Graphs
 */
export function checkLoopInUndirectedGraphWithVertex(vertex, nodes, edges) {
  if (vertex.visited) {
    return true;
  }

  vertex.visited = true;
  let edgeList = edges.filter(
    x => x.startNode === vertex.id || x.endNode === vertex.id
  );
  for (const edge of edgeList) {
    if (edge.visited) {
      continue;
    }

    edge.visited = true;
    let nextNode = nodes.find(
      x =>
        x.id === (edge.startNode === vertex.id ? edge.endNode : edge.startNode)
    );
    if (
      nextNode &&
      checkLoopInUndirectedGraphWithVertex(nextNode, nodes, edges)
    ) {
      return true;
    }
  }

  return false;
}

/**
 * Backtracking path of node node, node only returns ID in return value, and edge returns object containing startNode and endNode
 * @param {*} graph
 * @param {*} node
 */
export function recallPathOfNode(graph, nodeId) {
  let tempNodeId = nodeId;
  let recallPath = [];
  let edge = null;
  do {
    if (recallPath.includes(tempNodeId)) {
      throw new Error('Have loops in the graph');
    }
    recallPath.push(tempNodeId);
    edge = graph.edges.find(x => x.endNode === tempNodeId);
    if (!edge) {
      break;
    }
    recallPath.push(edge);
    tempNodeId = edge.startNode;
  } while (edge);

  return recallPath;
}
