<template>
  <div class="">
    <v-dialog
      v-model="ideasCreateEditDialogDisplay"
      transition="dialog-bottom-transition"
      persistent
      width="50%"
      :content-class="
        'desc-dialog-display stack-edit-dialog desc-dialog-display-top stackEdit-plus ' +
          stackEditContainerClasses
      "
    >
      <v-card v-if="ideasCreateEditDialogDisplay">
        <v-container class="container-reset pr-0 pl-0">
          <div class="desc-tab-content">
            <v-btn
              slot="activator"
              icon
              class="ma-0 desc-close-btn"
              @click.stop="close(false)"
            >
              <i class="mdi mdi-close theme--light grey--text"></i>
            </v-btn>
            <v-tabs v-model="tabActive" centered color="tab-orange">
              <v-tabs-slider
                style="display:none"
                color="orange"
              ></v-tabs-slider>
              <v-tab
                :ripple="false"
                v-for="item in projectTitle"
                :key="item.id"
                :href="'#des-tab-' + item.id"
              >
                {{ titleText }}
              </v-tab>
              <v-tabs-items class="pt-4 pl-4 pr-4">
                <!--ideas-->
                <v-tab-item value="des-tab-Ideas">
                  <v-card flat>
                    <div class="project-tab-item pa-2 pt-3">
                      <project-info-tab-ideas
                        ref="projectInfoTabIdeasShow"
                        :ideas-content-obj="ideasContentObj"
                        :project-info="projectInfo"
                      ></project-info-tab-ideas>
                    </div>
                  </v-card>
                </v-tab-item>
              </v-tabs-items>
            </v-tabs>
          </div>
        </v-container>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
  import projectInfoTabIdeas from '../../components/projectInfoTabIdeas';
  import ideasListContent from '../../components/ideasListContent';
  import { mapGetters, mapState, mapActions } from 'vuex';
  export default {
    name: 'ideasCreateEditDialog',
    components: {
      projectInfoTabIdeas,
      ideasListContent
    },
    props: {
      projectInfoObj: {
        type: Object
      },
      ideasContentObj: {
        type: Object
      }
    },
    data() {
      return {
        timer: null,
        lockTimer: null,
        uniqeId: 'descriptionDialog' + Date.now(),
        tabActive: null,
        trans: {
          OTIdeasCreatText: this.$i18n.t('StackEdit.Layout.OTIdeasCreatText'),
          OTNoContentChanges: this.$i18n.t(
            'StackEdit.Layout.OTNoContentChanges'
          )
        },
        projectTitle: [
          {
            id: 'Ideas',
            title: 'Ideas'
          }
        ],
        rules: {
          projectNameRequired: [
            value => !!value || 'title is none',
            value =>
              !_.some(
                this.projectList,
                item => item.id !== this.projectInfo.id && item.title === value
              ) || 'title is same'
          ]
        },
        stackEditQuestionShow: false,
        stackEditEvaluationShow: false,
        stackEditDataShow: false
      };
    },
    computed: {
      ...mapGetters({
        projectList: 'projectCollection/projectList',
        ideasCreateEditDialogDisplay:
          'projectDetail/ideasCreateEditDialogDisplayShowGetters'
      }),
      ...mapGetters('stackEdit/layout', ['styles']),

      projectInfo() {
        return this.projectInfoObj;
      },

      titleText() {
        return this.ideasContentObj.action === 'create'
          ? this.$i18n.t('ProjectPage.CreateIdea')
          : this.$i18n.t('ProjectPage.EditIdea');
      },

      stackEditContainerClasses() {
        let classes = [];
        if (this.styles.expanded) {
          classes.push('stack-edit-plus-active');
        }
        return classes;
      }
    },
    mounted() {},
    watch: {
      // Monitor project edit box data changes and save automatically
      ideasCreateEditDialogDisplay: function(newValue, oldValue) {
        if (newValue && this.ideasContentObj.action === 'create') {
          this.$store.commit(
            'projectCollection/autoSaveMessagesFun',
            this.trans.OTIdeasCreatText
          );
        }
        if (newValue && this.ideasContentObj.action === 'edit') {
          this.$store.commit(
            'projectCollection/autoSaveMessagesFun',
            this.trans.OTNoContentChanges
          );
          // Click refresh, modify routing address, close window, prompt whether to reload
          window.onbeforeunload = function(e) {
            e = e || window.event;
            // Compatible with previous versions of IE8 and Firefox 4
            if (e) {
              e.returnValue = '';
            }
            // Chrome, Safari, Firefox 4+, Opera 12+ , IE 9+
            return '';
          };
          this.timer = setInterval(() => {
            this.$refs.projectInfoTabIdeasShow.autoSave();
          }, 5000);

          this.lockTimer = setInterval(() => {
            this.$store.dispatch('projectCollection/addLock', {
              target: `/projects/${this.projectInfo.id}/ideas/${this.ideasContentObj.content.id}`
            });
          }, 60000);
        } else {
          window.onbeforeunload = null;
          clearInterval(this.timer);
          clearInterval(this.lockTimer);
          this.timer = null;
          this.lockTimer = null;
          this.$store.dispatch('projectCollection/unLock', {
            target: `/projects/${this.projectInfo.id}/ideas/${this.ideasContentObj.content.id}`
          });
        }
      }
    },
    // Cancel Timer before Component Destruction
    beforeDestroy() {
      window.onbeforeunload = null;
      clearInterval(this.timer);
      clearInterval(this.lockTimer);
      this.timer = null;
      this.lockTimer = null;
    },
    methods: {
      ...mapActions('stackEdit/data', ['toggleExpanded']),
      closeLayout(type) {
        this.toggleExpanded(false);
        this.$store.commit(
          'projectDetail/ideasCreateEditDialogDisplayShowFun',
          type
        );
      },
      async close(type) {
        if (this.ideasContentObj.action === 'edit') {
          // Close the idea editing rectification, check whether the data changes, if changes, display the inquiry box, whether to save
          let result = await this.$refs.projectInfoTabIdeasShow.autoSave();
          if (result) {
            this.closeLayout(type);
          }
        } else {
          this.closeLayout(type);
        }
      }
    }
  };
</script>
