import api from 'Api';
import socketAndHttp from '../../../services/socketAndHttp';
/**
 * Quickly create experiment and create experiment node according to the experiment created
 */
async function createExperimentWithNode(context, payload) {
  // 先创建实验experiment
  let responseExperiment = await socketAndHttp(
    `/projects/${payload.projectId}/experiments`,
    'post',
    payload.experiment
  );
  let nodeData = {
    experimentId: responseExperiment.data.id,
    location: payload.node.location
  };
  // 再创建节点node
  let responseNode = await socketAndHttp(
    `/projects/${payload.projectId}/graphs/${payload.graphId}/nodes`,
    'post',
    nodeData
  );
  let data = {
    projectId: payload.projectId,
    experiment: responseExperiment.data,
    node: responseNode.data,
    nodeId: responseNode.data.id
  };
  this.commit('projectDetail/refreshExperiment', data);
  this.commit('projectDetail/refreshNode', data);
  return data;
}

/**
 * Import the experimental node
 */
async function importNode(context, payload) {
  let response = await socketAndHttp(
    `/projects/${payload.projectId}/graphs/${payload.graphId}/nodes`,
    'post',
    payload.node
  );
  let data = {
    projectId: payload.projectId,
    experiment: response.data.experiment,
    node: response.data,
    nodeId: response.data.id
  };
  this.commit('projectDetail/refreshNode', data);
  return data;
}

/**
 * Save the location of the currently dragged experimental node after dragging the node
 */
async function saveNode(context, payload) {
  let response = await socketAndHttp(
    `/projects/${payload.projectId}/graphs/${payload.graphId}/nodes/${payload.nodeId}`,
    'patch',
    payload.location
  );
  let selectedGraph = this.getters['projectDetail/selectedGraph'];
  let itemNode = selectedGraph.nodes.find(x => x.id === payload.nodeId);
  let ItemNodeLocation = Object.assign(itemNode.location, payload.location);
  itemNode.location = ItemNodeLocation;
  let data = {
    nodeId: payload.nodeId,
    node: itemNode
  };
  this.commit('projectDetail/refreshNode', data);
  return data;
}

/**
 * Delete the experimental node but not the experiment
 */
async function deleteNode(context, payload) {
  let response = await socketAndHttp(
    `/projects/${payload.projectId}/graphs/${payload.graphId}/nodes/${payload.nodeId}`,
    'delete'
  );

  let data = {
    nodeId: payload.nodeId
  };
  this.commit('projectDetail/removeNode', data);
  let edges = this.getters['projectDetail/selectedGraph'].edges;
  _.vueRemove(
    edges,
    x => x.startNode === payload.nodeId || x.endNode === payload.nodeId
  );
  return data;
}

/**
 * Delete the node of the experiment node and delete the experiment at the same time
 */
async function deleteNodeAndExperiment(context, payload) {
  let response = await socketAndHttp(
    `/projects/${payload.projectId}/graphs/${payload.graphId}/nodes/${payload.nodeId}`,
    'delete'
  );
  let data = {
    nodeId: payload.nodeId,
    experimentId: payload.experimentId,
    projectId: payload.projectId
  };
  this.commit('projectDetail/removeExperiment', data);
  this.commit('projectDetail/removeNode', data);

  let edges = this.getters['projectDetail/selectedGraph'].edges;
  _.vueRemove(
    edges,
    x => x.startNode === payload.nodeId || x.endNode === payload.nodeId
  );
  return data;
}
export default {
  createExperimentWithNode,
  importNode,
  saveNode,
  deleteNode,
  deleteNodeAndExperiment
};
