import * as d3 from 'd3';
import domtoimage from 'dom-to-image';
import * as graphMath from '../../../helpers/graphMath';
import * as graphCfg from '../../../constants/peojectGraphConfig';
import experimentNodeSvc from '../../../services/experimentNodeSvc';
import dragInScaledDomSvc from '../../../services/dragInScaledDomSvc';
import socketAndHttp from '../../../services/socketAndHttp';
export default {
  throttle(func, wait) {
    let timeout;
    return function() {
      let context = this;
      let args = arguments;
      if (!timeout) {
        timeout = setTimeout(() => {
          timeout = null;
          func.apply(context, args);
        }, wait);
      }
    };
  },
  createExpDialogShow() {
    this.createExperiment();
  },
  GenerateReport(node) {
    this.routeWindowOpen(node.experiment.id);
  },
  updateExperimentNode(node) {
    this.updateExperiment(node.experiment);
  },
  routeWindowOpen(id) {
    let projectId = this.$route.params.projectId;
    let routeUrl = this.$router.resolve(
      '/projects/' + projectId + '/experiments/' + id
    );
    window.open(routeUrl.href, '_blank');
  },
  activeConnectionLine(event, edge) {
    edge.svg.class = _.compact(edge.svg.class.split(' ').concat('active')).join(
      ' '
    );
    $(event.srcElement)
      .parents('.edge-container')
      .addClass('active');
    if (!$(event.srcElement).hasClass('connection-line')) {
      return;
    }
    this.selectedEdgeKey = edge;
  },
  clearSelectedThings(event) {
    if (this.isCreateEdge) {
      this.createEdgeEnd(event);
      return;
    }
    if ($(event.srcElement).hasClass('project-graph-content')) {
      this.nodeFocus = false;
      let edges = this.selectedGraph.edges;
      edges.forEach(x => {
        this.$set(x, 'isActive', false);
      });
      this.selectedExperimentNode = null;
      let nodes = this.selectedGraph.nodes;
      nodes.forEach(x => {
        this.$set(x, 'isActive', false);
      });
      this.clearActiveLine();
      this.$forceUpdate(); // Forced refresh to solve the problem that the page will not be re-rendered
      this.clearSelectedEdge();
    }
  },
  clearActiveLine() {
    $('.edge-container').css('z-index', '-1');
  },
  clearSelectedEdge() {
    this.controlLinesData = [];
    this.selectedEdgeKey = null;
    $(this.$el)
      .find('.edge-container.active')
      .removeClass('active');
    $(this.$el)
      .find('.s-anchor.active')
      .removeClass('active');
    $(this.$el)
      .find('.connection-line.active')
      .removeClass('active');
    $(this.$el)
      .find('.arrow.active')
      .removeClass('active');
  },
  shrinkItem(itemEle, experimentId, nodeId) {
    let experimentArry = this.$store.getters['projectDetail/project']
      .experiments;
    let itemExperiment = experimentArry.find(x => x.id === experimentId);
    if (itemEle === 'parameter') {
      if (itemExperiment.settings.isActive) {
        this.$set(itemExperiment.settings, 'isActive', false);
      } else {
        this.$set(itemExperiment.settings, 'isActive', true);
      }
    } else {
      if (itemExperiment.result.isActive) {
        this.$set(itemExperiment.result, 'isActive', false);
      } else {
        this.$set(itemExperiment.result, 'isActive', true);
      }
    }
    setTimeout(() => {
      let nodeHeight = $(`[data-node-id=${nodeId}] .v-card`).height();
      let payload = {
        nodeId: nodeId,
        height: Math.ceil(nodeHeight)
      };
      this.$store.commit('projectDetail/shrinkExperimentNode', payload);
    }, 1);
  },
  resetD3Interactive4Nodes() {
    let thisVueComponent = this;
    let startimestamp = null;
    let dragExperimentNode = d3
      .drag()
      .container(thisVueComponent.$el)
      .subject(function() {
        let srcElement = d3.event.sourceEvent.srcElement;
        return srcElement.closest('.experiment-node');
      })
      .on('start', function() {
        startimestamp = new Date();
        thisVueComponent.clearActiveLine();
        let nodes = thisVueComponent.selectedGraph.nodes;
        let edges = thisVueComponent.selectedGraph.edges;
        nodes.forEach(x => {
          thisVueComponent.$set(x, 'isActive', false);
        });
        edges.forEach(x => {
          thisVueComponent.$set(x, 'isActive', false);
        });

        thisVueComponent.clearSelectedEdge();
        if (thisVueComponent.isCreateEdge) {
          return;
        }
        $(d3.event.subject).addClass('draging');
        thisVueComponent.dragCursorOriginalLocation = {
          x: d3.event.x,
          y: d3.event.y
        };
        let nodeId = d3.event.subject.dataset.nodeId;
        thisVueComponent.selectedExperimentNode = thisVueComponent.selectedGraphNodes.find(
          x => x.id === nodeId
        );
        thisVueComponent.dragObjectOriginalLocation = graphMath.removeLocationUnit(
          thisVueComponent.selectedExperimentNode.location
        );
        thisVueComponent.mouseDownOrUp('mouse-down', nodeId);
      })
      .on('drag', function() {
        if (50 > new Date() - startimestamp || thisVueComponent.isCreateEdge) {
          return;
        }
        let cursorPositionOfCurrent = {
          x: d3.event.x,
          y: d3.event.y
        };
        let nodeId = $(d3.event.subject).data('nodeId');
        let userGraph = thisVueComponent.selectedUserGraph;
        let node = thisVueComponent.selectedGraph.nodes.find(
          x => x.id === nodeId
        );
        let location = dragInScaledDomSvc(
          userGraph.transform.scale,
          thisVueComponent.dragObjectOriginalLocation,
          thisVueComponent.dragCursorOriginalLocation,
          cursorPositionOfCurrent
        );
        let payload = Object.assign({}, location);
        payload.nodeId = nodeId;
        let nodeZIndex = node.location.z;

        if (thisVueComponent.selectedGraph.nodes.length > 1) {
          let maxZindex = _.maxBy(
            thisVueComponent.selectedGraph.nodes,
            x => x.location.z
          ).location.z;
          payload.z = nodeZIndex < maxZindex ? maxZindex + 0.01 : maxZindex;
        }

        $(d3.event.subject).css('z-index', payload.z);
        thisVueComponent.$store.commit(
          'projectDetail/dragExperimentNode',
          payload
        );
      })
      .on('end', function() {
        if (thisVueComponent.isCreateEdge) {
          return;
        }
        let payload = {
          projectId: thisVueComponent.projectInfo.id,
          graphId: thisVueComponent.selectedGraph.id,
          nodeId: $(d3.event.subject).data('nodeId'),
          location: {
            x: parseInt($(d3.event.subject).css('left')),
            y: parseInt($(d3.event.subject).css('top')),
            z: parseInt($(d3.event.subject).css('z-index')) + 1
          }
        };
        if (
          thisVueComponent.dragObjectOriginalLocation.x !==
            payload.location.x &&
          thisVueComponent.dragObjectOriginalLocation.y !== payload.location.y
        ) {
          if (thisVueComponent.updateShow) {
            thisVueComponent.$store.dispatch('projectDetail/saveNode', payload);
          }
        }
        $(d3.event.subject).removeClass('draging');
        thisVueComponent.mouseDownOrUp(
          'mouse-up',
          $(d3.event.subject).data('nodeId')
        );
      });
    d3.select(thisVueComponent.$el).call(dragExperimentNode);
  },
  showNodeMenu(e, node) {
    this.clearSelectedEdge();
    this.clearSelectedThings(e);
    e.preventDefault();
    this.hideAllMenus();
    this.nodeMenu.focusNode = node;
    this.nodeMenu.x = e.clientX;
    this.nodeMenu.y = e.clientY;
    this.nodeMenu.items.forEach(menu => {
      this.$set(menu, 'btnShow', true);
    });
    if (!this.updateShow) {
      this.nodeMenu.items.forEach(menu => {
        if (menu.id !== 'view') {
          this.$set(menu, 'btnShow', false);
        }
      });
    }
    this.$nextTick(() => {
      this.nodeMenu.isOpen = true;
    });
  },
  showEdgeMenu(e, edge) {
    this.activeConnectionLine(e, edge);
    this.clearSelectedThings(e);
    e.preventDefault();
    this.hideAllMenus();
    this.selectedEdgeKey = edge;
    this.edgeMenu.x = e.clientX;
    this.edgeMenu.y = e.clientY;
    this.$nextTick(() => (this.edgeMenu.isOpen = true));
  },
  showGraphSpaceMenu(e) {
    this.clearSelectedThings(e);
    e.preventDefault();
    if (!$(e.srcElement).hasClass('project-graph-content')) {
      return;
    }
    this.hideAllMenus();
    this.graphSpaceMenu.x = e.clientX;
    this.graphSpaceMenu.y = e.clientY;
    this.$nextTick(() => (this.graphSpaceMenu.isOpen = true));
  },
  hideAllMenus() {
    let menus = ['nodeMenu', 'edgeMenu', 'graphSpaceMenu'];
    for (const menu of menus) {
      this[menu].isOpen = false;
    }
  },
  createEdgeBegin(startNode, mouseEvent) {
    this.nodeFocus = false;
    let offset = $(this.$el)
      .find('.project-graph')
      .offset();
    let scale = this.selectedUserGraph.transform.scale;
    let payload = {
      startNodeId: startNode.id,
      cursorLocation: {
        x: (mouseEvent.pageX - offset.left) / scale,
        y: (mouseEvent.pageY - offset.top) / scale,
        z: 1
      }
    };
    this.$store.commit('projectDetail/addEdageBegin', payload);
    this.dragCursorOriginalLocation = {
      x: mouseEvent.pageX,
      y: mouseEvent.pageY
    };
    this.selectedExperimentNode = this.selectedGraph.nodes.find(
      x => x.id === graphCfg.virtualNodeId
    );
    this.dragObjectOriginalLocation = graphMath.removeLocationUnit(
      this.selectedExperimentNode.location
    );
    this.isCreateEdge = true;
  },
  /**
   * When the mouse clicks on the object in the graph, it determines whether edge needs to be created or not.
   */
  createEdgeEnd(mouseEvent) {
    this.clearActiveLine();
    this.nodeFocus = false;
    let nodes = this.selectedGraph.nodes;
    let edges = this.selectedGraph.edges;
    nodes.forEach(x => {
      this.$set(x, 'isActive', false);
    });
    edges.forEach(x => {
      this.$set(x, 'isActive', false);
    });
    _.forEach(this.isActive, (value, key) => {
      this.isActive[key] = false;
    });
    this.isCreateEdge = false;
    let needCancel = true;
    let nodeEl = $(mouseEvent.srcElement).closest('.experiment-node');
    if (nodeEl.length > 0) {
      let endNodeId = $(nodeEl).data('nodeId');
      needCancel = graphMath.checkLoop(this.selectedGraph, endNodeId);
      if (needCancel) {
        // Consider giving a hint that there is a loop.
        this.$store.commit('settings/showGlobalSnackbar', {
          timeout: 5000,
          text: this.$i18n.t('ProjectPage.ProjectGraph.ExperimentNoloop')
        });
      }
      if (!needCancel) {
        let tempEdge = this.selectedGraph.edges.find(
          x => x.endNodeVirtualId === graphCfg.virtualNodeId
        );
        let startNode = this.selectedGraph.nodes.find(
          x => x.id === tempEdge.startNode
        );
        let endNode = this.selectedGraph.nodes.find(x => x.id === endNodeId);
        let newEdge = graphMath.generateDefaultEdge(
          tempEdge.startNode,
          endNodeId
        );
        newEdge.endNodeVirtualId = graphCfg.virtualNodeId;
        let payload = {
          projectId: this.projectInfo.id,
          graphId: this.selectedGraph.id,
          edge: newEdge
        };
        this.$store.dispatch('projectDetail/saveEdge', payload);
      }
    }
    if (needCancel) {
      this.$store.commit('projectDetail/addEdageCancel');
    }
  },
  dragVirtualNodeDrawEdge(mouseEvent) {
    if (!this.isCreateEdge) {
      return;
    }
    let cursorPositionOfCurrent = {
      x: mouseEvent.pageX,
      y: mouseEvent.pageY
    };
    let userGraph = this.selectedUserGraph;
    let location = dragInScaledDomSvc(
      userGraph.transform.scale,
      this.dragObjectOriginalLocation,
      this.dragCursorOriginalLocation,
      cursorPositionOfCurrent
    );
    let payload = {
      nodeId: graphCfg.virtualNodeId,
      ...location
    };

    let hasNode = null;
    _(mouseEvent.path).forEach(function(value) {
      let classArry = typeof value.className === 'string';
      if (classArry) {
        if (value.className.indexOf('experiment-node') >= 0) {
          hasNode = true;
          return;
        }
      }
    });
    if (hasNode) {
      let itemNodeId = $(mouseEvent.srcElement)
        .closest('.experiment-node')
        .data('nodeId');
      let itemNode = this.selectedGraphNodes.find(x => x.id === itemNodeId);
      payload.x = itemNode.location.x;
      payload.y = itemNode.location.y;
      let itemNodeHeight = $(mouseEvent.srcElement)
        .closest('.experiment-node')
        .find('.v-card')
        .height();
      let nodePayload = {
        itemNodeHeight: itemNodeHeight,
        itemNodeId: itemNodeId
      };
      this.$store.commit('projectDetail/dragInItemNode', nodePayload);
    } else {
      this.$store.commit('projectDetail/dragOutItemNode');
    }

    this.$store.commit('projectDetail/dragExperimentNode', payload);
  },
  /**
   * Drag-and-Drop Experiments in Import
   */
  expermentDrop(event) {
    let expermentId = event.dataTransfer.getData('item');
    let project = this.$store.getters['projectDetail/project'];
    let selectedGraph = this.$store.getters['projectDetail/selectedGraph'];
    let payload = {
      projectId: project.id,
      graphId: selectedGraph.id,
      node: {
        experimentId: expermentId,
        location: experimentNodeSvc.calculateNodeInitialLocation(
          event.x,
          event.y
        )
      }
    };
    let experimentNodesIdArry = [];
    this.experimentNodes.forEach(e => {
      experimentNodesIdArry.push(e.experimentId);
    });
    if (experimentNodesIdArry.includes(expermentId)) {
      this.$store.commit('settings/showGlobalSnackbar', {
        text: this.$i18n.t(
          'ProjectPage.CreateExperimentDialog.ExperimentsExist'
        )
      });
      this.expermentMoveToCenter(expermentId);
      this.nodeFocus = true;
      this.selectedGraph.nodes.forEach(n => {
        this.$set(n, 'isActive', false);
        if (n.id === expermentId) {
          this.$set(n, 'isActive', true);
        }
      });
    } else {
      this.$store.dispatch('projectDetail/importNode', payload);
    }
  },
  activeNode(event, node) {
    this.nodeFocus = true;
    this.clearSelectedEdge();
    this.selectedExperimentNode = node;
    if (!$(event.srcElement).closest('.experiment-node').length) {
      return;
    }
    let nodes = this.selectedGraph.nodes;
    let edges = this.selectedGraph.edges;
    let setZindex = null;
    if (nodes.length > 1) {
      setZindex =
        _.maxBy(this.selectedGraph.nodes, x => x.location.z).location.z +
        nodes.length +
        edges.length;
    }
    let payload = {
      itemNodeArr: [],
      itemEdgeArr: [],
      setZindex
    };
    nodes.forEach(x => {
      this.$set(x, 'isActive', false);
    });
    edges.forEach(x => {
      this.$set(x, 'isActive', false);
    });
    let callPath = graphMath.recallPathOfNode(this.selectedGraph, node.id);
    for (const path of callPath) {
      if (_.isObject(path)) {
        let edge = edges.find(e =>
          _.isEqualWith(
            path,
            e,
            (x, y) => x.startNode === y.startNode && x.endNode === y.endNode
          )
        );
        payload.itemEdgeArr.push(edge);
        this.$set(edge, 'isActive', true);
      } else {
        let node = nodes.find(x => x.id === path);
        if (node) {
          payload.itemNodeArr.push(node);
          this.$set(node, 'isActive', true);
        }
      }
    }
    this.$store.commit('projectDetail/activeNodeEdegZindex', payload);
    this.$forceUpdate(); // Forced refresh to solve the problem that the page will not be re-rendered
    // Label A is included in the details of the experiment, jumping to the new window
    let elt = event.target;
    while (elt !== this.$el) {
      if (
        elt.href &&
        elt.href.match(/^((https|http|ftp|rtsp|mms)?:\/\/)[^\s]+/) &&
        (!elt.hash || elt.href.slice(0, appUri.length) !== appUri)
      ) {
        event.preventDefault();
        const wnd = window.open(elt.href, '_blank');
        wnd.focus();
        return;
      }
      elt = elt.parentNode;
    }
  },
  updateEdgeType(edgeType) {
    this.hideAllMenus();
    let payload = {
      projectId: this.projectInfo.id,
      graphId: this.selectedGraph.id,
      edge: this.selectedEdge.edge
    };
    payload.edge.edgeType = edgeType;
    payload.edge.ctlPoint1.x = graphMath.parseIntWithZeroDefault(
      payload.edge.ctlPoint1.x
    );
    payload.edge.ctlPoint1.y = graphMath.parseIntWithZeroDefault(
      payload.edge.ctlPoint1.y
    );
    payload.edge.ctlPoint2.x = graphMath.parseIntWithZeroDefault(
      payload.edge.ctlPoint2.x
    );
    payload.edge.ctlPoint2.y = graphMath.parseIntWithZeroDefault(
      payload.edge.ctlPoint2.y
    );
    this.$store.dispatch('projectDetail/updateEdgeType', payload);
  },
  deleteEdge() {
    // 实现DeleteEdge提示框
    let payload = {
      projectId: this.projectInfo.id,
      graphId: this.selectedGraph.id,
      edge: this.selectedEdge.edge,
      startNode: this.selectedEdge.edge.startNode,
      endNode: this.selectedEdge.edge.endNode
    };
    this.$store.dispatch('projectDetail/deleteEdge', payload);
    this.selectedEdgeKey = null;
  },
  importNodeOpen() {
    if (_.isEmpty(this.projectInfo.experiments)) {
      this.$store.commit('settings/showGlobalSnackbar', {
        text: this.$i18n.t('ProjectPage.OTNoneExperiment')
      });
    } else {
      this.nodeInfo.action = 'allExperment';
      this.nodeDialogDisplay = true;
      $('.project-content').addClass('padding-right-24');
    }
  },
  deleteNode(node) {
    this.primaryTitleOpen = true;
    this.deleteNodeExperimentInfo = {
      action: 'deleteNode',
      title: this.$i18n.t('ProjectPage.ProjectGraph.RemoveNode'),
      content: this.$i18n.t('ProjectPage.ProjectGraph.deleteNodeContent', [
        node.experiment.title
      ]),
      tips: this.$i18n.t('ProjectPage.PromptInformationRomve'),
      nodeInfoList: node
    };
    this.publicPromptDialogShow = true;
  },
  createExperiment() {
    experimentNodeSvc.createNodeWithNewExpriment();
  },
  verLock(experiment) {
    this.$store
      .dispatch('projectCollection/addLock', {
        target: `/projects/${this.projectInfo.id}/experiments/${experiment.id}`
      })
      .then(response => {
        $('body').addClass('edit-experment-class-1024');
        this.nodeDialogDisplay = true;
        let markdownText = experiment.content.markdown;
        let value = {
          fileId: `edit-node-markdown_${experiment.id}`,
          text: markdownText
        };
        this.$store.commit('stackEdit/patchMarkdown', value);
        this.nodeInfo = {
          action: 'edit',
          id: experiment.id,
          title: experiment.title,
          status: experiment.status
        };
      })
      .catch(error => {
        $('body').removeClass('edit-experment-class-1024');
        if (error.response.status === 423) {
          this.$store.commit('settings/showGlobalSnackbar', {
            text: this.$i18n.t(
              'ProjectPage.CreateExperimentDialog.OTExperimentLock'
            )
          });
        }
      });
  },
  async updateExperiment(experiment) {
    this.stackEditOpen = true;
    //判断是否被锁定
    this.verLock(experiment);
  },
  expermentListEdit(experiment) {
    this.updateExperiment(experiment);
  },
  async deleteNodeOrExperiment() {
    // Delete the experimental node but not the experiment
    if (
      this.nodeInfo.action === 'edit' &&
      this.deleteNodeExperimentInfo.nodeInfoList.id === this.nodeInfo.id
    ) {
      this.nodeDialogDisplay = false;
    }
    if (this.deleteNodeExperimentInfo.action === 'deleteNode') {
      let payload = {
        projectId: this.$route.params.projectId,
        graphId: this.$route.params.selectedGraphId,
        nodeId: this.deleteNodeExperimentInfo.nodeInfoList.id
      };
      this.$store.dispatch('projectDetail/deleteNode', payload).then(res => {
        if (this.selectedGraphNodes.length < 1) {
          let data = {
            projectId: this.$route.params.projectId,
            graphId: this.$route.params.selectedGraphId,
            transform: {
              scale: 1,
              x: 0,
              y: 0,
              z: 0
            }
          };
          this.$store.dispatch('projectDetail/userExpandGraph', data);
        }
      });
      this.primaryTitleOpen = false;
      this.publicPromptDialogShow = false;
    }
  },
  exportGraphToPNG() {
    if (_.isEmpty(this.experimentNodes)) {
      return;
    } else {
      let _html = $('.project-graph').html();
      $('.dom-images .v-container').html(_html);
      let containerEl = this.$el;
      let containerRect = containerEl
        .querySelector('.dom-images .v-container')
        .getBoundingClientRect();
      let dropMenu = containerEl.querySelectorAll('.dom-images .v-menu');
      _.map(dropMenu, x => {
        x.remove();
      });
      let domRectArry = _.map(
        containerEl.querySelectorAll('.dom-images .experiment-node'),
        x => x.getBoundingClientRect()
      );
      let maxRect = {
        top: _.minBy(domRectArry, x => x.top).top,
        right: _.maxBy(domRectArry, x => x.right).right,
        bottom: _.maxBy(domRectArry, x => x.bottom).bottom,
        left: _.minBy(domRectArry, x => x.left).left
      };

      let domWidth = maxRect.right - maxRect.left + 30;
      let domHeight = maxRect.bottom - maxRect.top + 30;
      $('.dom-images').css({
        width: domWidth,
        height: domHeight
      });
      $('.v-container').css(
        'transform',
        `translate(${-(maxRect.left - containerRect.left)}px,${-(
          maxRect.top - containerRect.top
        )}px)`
      );
      domtoimage
        .toPng($(this.$el).find('.dom-images')[0])
        .then(function(dataUrl) {
          var link = document.createElement('a');
          link.download = 'metaML.IO.Graph.png';
          link.href = dataUrl;
          link.click();
          $('.dom-images .v-container').empty();
          $('.dom-images').css({
            width: 0,
            height: 0
          });
          $('.v-container').css('transform', `translate(0px,0px)`);
        });
    }
  },
  async allExpermentList() {
    if (_.isEmpty(this.projectInfo.experiments)) {
      this.$store.commit('settings/showGlobalSnackbar', {
        // color: '#2196F3',
        // timeout: 3000,
        text: this.$i18n.t('ProjectPage.OTNoneExperiment')
      });
    } else {
      $('body').addClass('edit-experment-class-1024');
      let payload = {
        category: 'shared',
        types: ['Experiment'],
        project: this.projectInfo.id
      };
      this.$store.dispatch('shares/getsharedSharesList', payload);
      if (this.nodeInfo.action === 'allExperment') {
        $('.project-content').removeClass('padding-right-24');
        this.nodeDialogDisplay = false;
        setTimeout(() => {
          this.nodeInfo.action = 'default';
        }, 500);
      } else {
        // If the experiment edit box exists, verify the experiment and save it automatically
        let createNodeShow = this.$refs.createExpDialogDom.createNodeShow;
        if (createNodeShow) {
          let result = await this.$refs.createExpDialogDom.autoSave();
          if (result) {
            this.nodeInfo.action = 'allExperment';
            this.nodeDialogDisplay = true;
            $('.project-content').addClass('padding-right-24');
            let dialogWidth = '';
            this.$store.commit(
              'projectDetail/resizeDialogWidthFun',
              dialogWidth
            );
          }
        } else {
          this.nodeInfo.action = 'allExperment';
          this.nodeDialogDisplay = true;
          $('.project-content').addClass('padding-right-24');
          let dialogWidth = '';
          this.$store.commit('projectDetail/resizeDialogWidthFun', dialogWidth);
        }
      }
    }
  },
  expermentMoveToCenter(experimentId) {
    let containerEl = this.$el.querySelector(
      '.content-main.project-graph-content'
    );
    let containerRect = containerEl.getBoundingClientRect();
    let experment = {};
    this.experimentNodes.forEach(e => {
      if (e.experimentId === experimentId) {
        experment = e;
      }
    });
    let nodReact = $(
      `*[data-node-id=${experment.id}]`
    )[0].getBoundingClientRect();
    let contentCenter = {
      x: (nodReact.right + nodReact.left) / 2,
      y: (nodReact.top + nodReact.bottom) / 2
    };

    this.moveCenterFun(contentCenter);
  },
  moveToCenter() {
    let containerEl = this.$el.querySelector(
      '.content-main.project-graph-content'
    );
    let domRectArry = _.map(
      containerEl.querySelectorAll('.experiment-node'),
      x => x.getBoundingClientRect()
    );
    let userGraph = this.userGraphList.find(
      x => x.id === this.userSelectedGraph
    );
    let transform = userGraph
      ? userGraph.transform
      : {
          scale: 1,
          x: 0,
          y: 0,
          z: 0
        };
    let payload = {
      projectId: this.projectInfo.id,
      graphId: this.selectedGraph.id,
      transform: {
        scale: transform.scale,
        x: 0,
        y: 0,
        z: 0
      }
    };

    if (domRectArry.length < 1) {
      this.$store.dispatch('projectDetail/userExpandGraph', payload);
      return;
    }

    let maxRect = {
      top: _.minBy(domRectArry, x => x.top).top,
      right: _.maxBy(domRectArry, x => x.right).right,
      bottom: _.maxBy(domRectArry, x => x.bottom).bottom,
      left: _.minBy(domRectArry, x => x.left).left
    };
    // The center point of rectangular frame enclosing all nodes
    let contentCenter = {
      x: (maxRect.right + maxRect.left) / 2,
      y: (maxRect.top + maxRect.bottom) / 2
    };
    this.moveCenterFun(contentCenter);
  },
  moveCenterFun(contentCenter) {
    // graph center point
    this.zoomGraphOper = true;
    this.moveActiveCenter = true;
    let containerRect = this.contentContainer.node().getBoundingClientRect();
    let graphCenter = {
      x: (containerRect.right + containerRect.left) / 2,
      y: (containerRect.top + containerRect.bottom) / 2
    };
    if (this.contentContainer) {
      let transform = d3.zoomTransform(this.contentContainer.node());
      transform.x = transform.x + (graphCenter.x - contentCenter.x);
      transform.y = transform.y + (graphCenter.y - contentCenter.y);
      this.contentContainer.call(this.zoomHandler.transform, transform);
    }
  },
  addZoomBehavior(event) {
    let thisVueComponent = this;
    this.zoomHandler = d3
      .zoom()
      .scaleExtent([0.5, 2]) // zoom limit
      .filter(() => {
        this.sizeVersion++;
        let _event = d3.event.sourceEvent || d3.event;
        if (_event instanceof WheelEvent) {
          if (thisVueComponent.experimentNodes.length > 0) {
            return true;
          } else {
            return false;
          }
        }
        return $(d3.event.path).closest('.experiment-node').length
          ? false
          : true;
      })
      .on('start', function() {
        thisVueComponent.hideAllMenus();
        thisVueComponent.moveActiveCenter = false;
        thisVueComponent.clearSelectedEdge();
        if (
          thisVueComponent.isCreateEdge ||
          thisVueComponent.experimentNodes.length < 1
        ) {
          return;
        }
        let _event = d3.event.sourceEvent || d3.event;
        if (_event instanceof WheelEvent) {
          d3.select(this).classed('project-graph-content-draging', false);
        } else {
          d3.select(this).classed('project-graph-content-draging', true);
        }

        thisVueComponent.dragCursorOriginalLocation = {
          x: d3.event.x,
          y: d3.event.y
        };
        let userGraph = thisVueComponent.userGraphList.find(
          x => x.id === thisVueComponent.userSelectedGraph
        );
        thisVueComponent.dragObjectOriginalLocation = _.pick(
          userGraph.transform,
          ['x', 'y']
        );
      })
      .on('zoom', () => {
        thisVueComponent.experimentNodes.length < 1;
        let t = d3.event.transform;
        let div = d3.select(this.$el).select('.project-graph');
        let _this = this;
        setTimeout(function() {
          _this.moveActiveCenter = false;
          _this.zoomGraphOper = false;
        }, 250);
        this.hasStyle = true;
        this.lastScale = t.k;
        div.style('transform', `translate(${t.x}px,${t.y}px) scale(${t.k})`);
      })
      .on('end', function() {
        d3.select(this).classed('project-graph-content-draging', false);
        let t = d3.event.transform;
        let selectedGraphId =
          thisVueComponent.selectedGraph === undefined
            ? thisVueComponent.$route.params.selectedGraphId
            : thisVueComponent.selectedGraph.id;
        let payload = {
          projectId: thisVueComponent.projectInfo.id,
          graphId: selectedGraphId,
          transform: {
            scale: t.k,
            x: parseInt(t.x),
            y: parseInt(t.y),
            z: 0
          }
        };
        if (thisVueComponent.updateShow) {
          thisVueComponent.$store.dispatch(
            'projectDetail/userExpandGraph',
            payload
          );
        } else {
          thisVueComponent.$store.commit(
            'projectDetail/userExpandGraph',
            payload
          );
        }
      });
    this.contentContainer.call(this.zoomHandler).on('dblclick.zoom', null);
  },
  zoomGraph(isShrink) {
    let containerRect = this.contentContainer.node().getBoundingClientRect();
    let graphCenter = {
      x: (containerRect.right + containerRect.left) / 2,
      y: (containerRect.top + containerRect.bottom) / 2
    };
    this.zoomGraphOper = true;
    this.moveActiveCenter = true;
    // Create events
    let event = new WheelEvent('wheel', {
      clientX: graphCenter.x,
      clientY: graphCenter.y,
      pageX: graphCenter.x,
      pageY: graphCenter.y,
      x: graphCenter.x,
      y: graphCenter.y,
      deltaX: 0,
      deltaY:
        ((0.1 * -644.6158776409102) /
          d3.zoomTransform(this.contentContainer.node()).k -
          3.8909739946717536) *
        (isShrink ? -1 : 1)
    });

    // The trigger object can be any element or other event target
    this.$el.querySelector('.content-main').dispatchEvent(event);
  },
  hideDesc() {
    this.nodeDialogDisplay = false;
    this.stackEditOpen = false;
    this.nodeInfo.action = 'default';
    $('.project-content').removeClass('padding-right-24');
  },
  goRouter(to) {
    this.$router.push(to);
  },
  mouseDownOrUp(type, nodeId) {
    let data = {
      type: type,
      nodeId: nodeId
    };
    let projectId = this.$store.getters['projectDetail/project'].id;
    let graphId = this.$store.getters['projectDetail/selectedGraph'].id;
    let target = `/projects/${projectId}/graphs/${graphId}/cursor`;
    socketAndHttp(target, 'put', data, true);
  },
  goExperment(node) {
    this.routeWindowOpen(node.experiment.id);
    let projectId = this.$store.getters['projectDetail/project'].id;
    setTimeout(() => {
      this.$store.dispatch('projectDetail/refreshExperimentList', projectId);
    }, 3000);
  }
};
