import * as d3 from 'd3';
import socketAndHttp from '../../../services/socketAndHttp';

export default function mounted() {
  $(window).on('resize', () => this.sizeVersion++);
  this.$forceUpdate();
  // Mouse roller or touchpad zooming
  this.contentContainer = d3.select(this.$el).select('.content-main');
  this.addZoomBehavior();
  let self = this;
  this.$el.querySelector('.content-main').onmousemove = this.throttle(function(
    e
  ) {
    let projectId = self.$store.getters['projectDetail/project'].id;
    let graphId = self.$store.getters['projectDetail/selectedGraph'].id;
    let target = `/projects/${projectId}/graphs/${graphId}/cursor`;

    let graphRect = this.querySelector('.project-graph').getClientRects()[0];
    let graphRectHas = graphRect ? graphRect : { x: 0, y: 0 };
    let userGraph = self.$store.getters['userGraph/userGraphList'];
    let itemGraph = _.find(
      userGraph,
      x => x.id === self.$store.getters['userGraph/userSelectedGraph']
    );
    if (itemGraph) {
      let data = {
        type: 'mouse-move',
        x: parseInt((e.x - graphRectHas.x) / itemGraph.transform.scale),
        y: parseInt((e.y - graphRectHas.y) / itemGraph.transform.scale),
        z: 1
      };
      if (self.updateShow) {
        socketAndHttp(target, 'put', data, true);
      }
    }
  },
  1000 / 15);
  self.timerCursor = setInterval(function() {
    let updateGraphCursorListGetters = self.updateGraphCursorListGetters;
    if (
      typeof self.$store.getters['projectDetail/selectedGraph'] !== 'undefined'
    ) {
      let graphId = self.$store.getters['projectDetail/selectedGraph'].id;
      for (let i = 0; i < updateGraphCursorListGetters.length; i++) {
        if (
          new Date().getTime() - updateGraphCursorListGetters[i].timestamp <
            3000 &&
          graphId === updateGraphCursorListGetters[i].graphsId
        ) {
          self.$set(updateGraphCursorListGetters[i], 'isShow', true);
        } else {
          self.$set(updateGraphCursorListGetters[i], 'isShow', false);
        }
      }
    }
  }, 1000);
}
