import Vue from 'vue';
import api from 'Api';
import socketAndHttp from '../../../services/socketAndHttp';
/**
 * Get the experiment information specified by experimentId in the project and cache it
 */
async function refreshExperiment(context, payload) {
  let response = await api.get(
    `/projects/${payload.projectId}/experiments/${payload.experimentId}`
  );
  let data = {
    projectId: payload.projectId,
    experiment: response.data
  };
  this.commit('projectDetail/refreshExperiment', data);
  return data;
}

/**
 * Delete experiment
 */
async function deleteExperiment(context, payload) {
  let response = await socketAndHttp(
    `/projects/${payload.projectId}/experiments/${payload.experimentId}`,
    'delete'
  );
  this.commit('projectDetail/removeExperiment', payload);
  return payload;
}

/**
 * Get a list of project experiments and refresh local data
 */
async function refreshExperimentList(context, projectId) {
  await this.dispatch('projectCollection/getProjectById', projectId);
  let response = await api.get(`/projects/${projectId}/experiments`);

  response.data.forEach(async function(e) {
    let comments = await api.get(`/users/comments/status?targetId=${e.id}`);
    let readStatusList = comments.data;
    let aboutmeUnread = 0;
    let unread = 0;
    readStatusList.forEach(userComment => {
      aboutmeUnread = aboutmeUnread + userComment.aboutmeUnread;
      unread = unread + userComment.unread - userComment.aboutmeUnread;
    });
    Vue.set(e, 'unread', unread);
    Vue.set(e, 'aboutmeUnread', aboutmeUnread);
  });

  let data = {
    projectId,
    experimentList: response.data
  };
  this.commit('projectDetail/refreshExperimentList', data);
  return data;
}

/**
 * Create new experiments
 */
async function createExperiment(context, payload) {
  let response = await api.post(
    `/projects/${payload.projectId}/experiments`,
    payload.experiment
  );
  let data = {
    projectId: payload.projectId,
    experiment: response.data
  };
  this.commit('projectDetail/refreshExperiment', data);
  return data;
}

/**
 * Update the details of the experiment
 * Note that the comment update operation of the experiment should not use this method, but should use the addExperimentComment method.
 */
async function updateExperiment(context, payload) {
  let fields = [
    'id',
    'title',
    'content',
    'goal',
    'settings',
    'result',
    'status'
  ];
  let experiment = _.pick(payload.experiment, fields);
  let experimentId = experiment.id;

  let response = await socketAndHttp(
    `/projects/${payload.projectId}/experiments/${experimentId}`,
    'patch',
    experiment
  );
  let data = {
    projectId: payload.projectId,
    experiment: response.data
  };
  let project = this.getters['projectCollection/project'](payload.projectId);
  let findExperiment = _.find(project.experiments, x => x.id === experimentId);
  if (findExperiment.editor) {
    data.experiment.editor = findExperiment.editor;
  }
  this.commit('projectDetail/refreshExperiment', data);
  return data;
}

/**
 * Add an experimental comment
 */
async function addExperimentComment(context, payload) {
  throw new Error('HaveNotImplement');
  this.commit('projectDetail/addExperimentComment', payload);
}

/**
 * Delete an experimental comment
 */
async function removeExperimentCommentById(context, payload) {
  throw new Error('HaveNotImplement');
  this.commit('projectDetail/removeExperimentCommentById', payload);
}

/**
 * Get the item graphs with the specified id, try to get the local cached item, and get it from the server if it does not exist locally
 */
async function getProjectExperimentsById(context, projectId) {
  let project = this.getters['projectCollection/project'](projectId);
  return new Promise((resolve, reject) => {
    if (project) {
      if (project.graphs.length > 0) {
        let payload = {
          projectId: projectId,
          experimentList: project.experiments
        };
        resolve(payload);
      } else {
        this.dispatch('projectDetail/refreshExperimentList', projectId)
          .then(p => resolve(p))
          .catch(e => reject(e));
      }
    } else {
      this.dispatch('projectDetail/refreshExperimentList', projectId)
        .then(p => resolve(p))
        .catch(e => reject(e));
    }
  });
}

export default {
  refreshExperimentList,
  createExperiment,
  refreshExperiment,
  updateExperiment,
  deleteExperiment,
  addExperimentComment,
  removeExperimentCommentById,
  getProjectExperimentsById
};
