import api from 'Api';

/**
 * Get all the graph panels of the current user and add project experiments and ideas to the target project
 */
async function refreshUserGraphList(context, projectId) {
  let response = null;
  response = await api.get(`/users/projects/${projectId}/graphs`);
  await this.dispatch('projectDetail/refreshGraphList', projectId);
  await this.dispatch('projectDetail/refreshExperimentList', projectId);
  await this.dispatch('projectDetail/refreshIdeaList', projectId);

  let payload = {
    projectId: projectId,
    data: response.data
  };
  this.commit('userGraph/refreshUserGraphList', payload);
  return payload.data;
}

async function getUserSelectedGraph(context, projectId) {
  let response = await api.get(`/users/projects/${projectId}`);
  this.commit('userGraph/refreshUserSelectedGraph', response.data);
  return response.data;
}

/*
 * Get all the graph panels of the current user as routing jumps
 */
async function getUserGraphList(context, projectId) {
  let response = await api.get(`/users/projects/${projectId}/graphs`);
  this.commit('userGraph/refreshUserGraph', response.data);
  return response.data;
}

async function refreshSelectedGraphId(context, payload) {
  this.commit('userGraph/refreshSelectedGraphId', payload);
  await api.patch(
    `/users/projects/${payload.projectId}?selectedGraphId=${payload.selectedGraph}`
  );
}

/**
 * Change the ID and sort of the selected graph of the current user
 */
async function refreshSelectedGraphOrderIndex(context, payload) {
  this.commit('userGraph/refreshSelectedGraphOrderIndex', payload);
  let query = payload.sortedGraphIdList.join('&orderIndex=');
  await api.patch(
    `/users/projects/${payload.projectId}/graphs?orderIndex=${query}`
  );
}

export default {
  refreshUserGraphList,
  getUserSelectedGraph,
  getUserGraphList,
  refreshSelectedGraphId,
  refreshSelectedGraphOrderIndex
};
