import Vue from 'vue';
import * as graphMath from '../../../helpers/graphMath';
/**
 * Update local project pilot node
 */
function refreshNode(state, payload) {
  let selectedGraph = this.getters['projectDetail/selectedGraph'];
  if (!selectedGraph) {
    return;
  }
  let oldNodeArray = _.vueRemove(
    selectedGraph.nodes,
    x => x.id === payload.nodeId
  );
  let node = payload.node;
  node.location = graphMath.addLocationUnitPX(node.location);
  node.isActive =
    (oldNodeArray.length > 0 && oldNodeArray[0].isActive) || false;
  node.mouseDown = (oldNodeArray.length > 0 && oldNodeArray[0].mouseDown) || [];

  //解决导入实验bug start 必须触发一下实验更新
  let experiments = this.getters['projectDetail/project'].experiments;
  let itemExperiment = experiments.find(x => x.id === node.experimentId);
  let data = {
    projectId: node.projectId,
    experiment: itemExperiment
  };
  this.commit('projectDetail/refreshExperiment', data);
  //解决导入实验bug end

  selectedGraph.nodes.push(node);
}

/**
 * Delete a local project lab node
 */
function removeNode(state, payload) {
  let selectedGraph = this.getters['projectDetail/selectedGraph'];
  if (!selectedGraph) {
    return;
  }
  selectedGraph.nodes = selectedGraph.nodes.filter(
    x => x.id !== payload.nodeId
  );
  // _.vueRemove(selectedGraph.nodes, x => x.id === payload.nodeId);
}

/**
 * socket
 */
function SOCKET_updateProjectNodes(state, payload) {
  let projectId = payload.projectId;
  let graphsId = payload.graphsId;
  let nodesId = payload.nodesId;
  let project = this.getters['projectCollection/project'](projectId);
  let currentRouterName = window.getVM().$router.history.current.name;
  let selectedProjectId = this.getters['projectDetail/selectedProjectId'];
  if (currentRouterName === 'projectGraph' && projectId === selectedProjectId) {
    let itemGraphs = _.find(project.graphs, function(x) {
      return x.id === graphsId;
    });
    let userId = this.getters['session/user'].id;
    let itemNode = _.find(itemGraphs.nodes, x => x.id === nodesId);
    if (!itemNode) {
      if (payload.action !== 'delete') {
        payload.data.location = graphMath.addLocationUnitPX(
          payload.data.location
        );

        let experiments = project.experiments;
        let itemExperiment = experiments.find(
          x => x.id === payload.data.experimentId
        );
        let data = {
          projectId: payload.data.projectId,
          experiment: itemExperiment
        };
        this.commit('projectDetail/refreshExperiment', data);

        itemGraphs.nodes.push(payload.data);
      }
    } else {
      let hasMouseDown = itemNode.mouseDown ? itemNode.mouseDown : [];
      let hasOwn = _.find(itemNode.mouseDown, x => x.id === userId);
      let currentUser = this.getters['session/user'];
      if ((hasMouseDown.length > 0 && !hasOwn) || hasMouseDown.length === 0) {
        if (currentUser.id !== payload.metadata.operator.id) {
          itemGraphs.nodes = itemGraphs.nodes.filter(x => x.id !== nodesId);

          // _.vueRemove(itemGraphs.nodes, x => x.id === nodesId);

          if (payload.action !== 'delete') {
            payload.data.location = graphMath.addLocationUnitPX(
              payload.data.location
            );
            payload.data.isActive = itemNode.isActive;
            payload.data.mouseDown = itemNode.mouseDown;
            itemGraphs.nodes.push(payload.data);
          }
        }
      }
    }
  }
}

function SOCKET_mouseDownOrUpNodes(state, payload) {
  let currentRouterName = window.getVM().$router.history.current.name;
  if (currentRouterName === 'projectGraph') {
    let selectedGraph = this.getters['projectDetail/selectedGraph'];
    let node = _.find(selectedGraph.nodes, x => x.id === payload.data.nodeId);
    if (node) {
      if (!node.mouseDown) {
        Vue.set(node, 'mouseDown', []);
      } else {
        payload.data.type === 'mouse-down'
          ? node.mouseDown.push({
              id: payload.operator.id
            })
          : _.vueRemove(node.mouseDown, x => x.id === payload.operator.id);
      }
    }
  }
}

export default {
  refreshNode,
  removeNode,
  SOCKET_updateProjectNodes,
  SOCKET_mouseDownOrUpNodes
};
