<template>
  <div class="ideas-list-content-innertext">
    <div class="innertext" v-html="ideasContentInnerTextFun"></div>
  </div>
</template>

<script>
  export default {
    name: 'IdeasListContent',
    props: {
      ideasContentInnerText: {
        type: String
      }
    },
    mounted() {},
    computed: {
      ideasContentInnerTextFun() {
        let reg = /<(?!a|\/?a|em|\/?em|strong|\/?strong).*?>/g;
        let str = this.ideasContentInnerText.replace(reg, '');
        let removeIdeaCode = /class=\"ml-project-idea-hash-code\"/g;
        return str.replace(removeIdeaCode, '');
      }
    }
  };
</script>

<style lang="scss" scoped>
  .innertext {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
  }
</style>
