<template>
  <div class="session-wrapper forgot-password">
    <div class="session-header">
      <logo-box></logo-box>
    </div>

    <div v-if="validateFailed" class="session-main">
      <div class="session-content">
        <p v-once class="function-hint" v-text="trans.OTFunctionHint1"></p>
        <error-box ref="errorDom" class="mb-4"></error-box>
        <input
          v-model="email"
          class="email"
          type="text"
          :placeholder="trans.email"
          @keyup.enter="send"
        />
        <button
          v-once
          class="submit-button"
          @click="send"
          v-text="trans.OPSendResetPasswdEmail"
        ></button>
      </div>
    </div>

    <div v-else class="session-main">
      <div class="session-content">
        <p
          v-once
          class="function-hint"
          style="font-weight:normal;text-align: center;"
          v-text="trans.OTFunctionHint2"
        ></p>
        <div class="email-adress-text">
          {{ email }}
        </div>
        <div class="email-ico">
          <img src="/img/email-ico.svg" />
        </div>
      </div>
      <div
        class="forgot-password"
        @click="validateFailed = true"
        v-text="trans.ResendRecoveryLink"
      ></div>
    </div>

    <div class="session-bottom">
      <div class="bottom">
        <router-link
          v-once
          class="login"
          to="/login"
          v-text="trans.OPReturnLoginPage"
        ></router-link>
      </div>
    </div>
  </div>
</template>

<script>
  import logoBox from '../../components/Widgets/logo';

  import errorBox from '../../components/Widgets/session/error';

  export default {
    name: 'ForgotPassword',
    created() {
      this.$validator.attach({
        name: 'email',
        alias: this.trans.email,
        getter: () => this.email,
        rules: {
          required: true,
          email: true,
          regex: /^[^\u4e00-\u9fa5]+$/
        }
      });
    },
    components: {
      logoBox,
      errorBox
    },
    data() {
      return {
        email: '',
        validateFailed: true,
        trans: {
          email: this.$i18n.t('email'),
          OTTitle: this.$i18n.t('Session.ForgotPassword.OTTitle'),
          OTFunctionHint1: this.$i18n.t(
            'Session.ForgotPassword.OTFunctionHint1'
          ),
          OTFunctionHint2: this.$i18n.t(
            'Session.ForgotPassword.OTFunctionHint2'
          ),
          OPSendResetPasswdEmail: this.$i18n.t(
            'Session.ForgotPassword.OPSendResetPasswdEmail'
          ),
          OPReturnLoginPage: this.$i18n.t(
            'Session.ForgotPassword.OPReturnLoginPage'
          ),
          OTEmailInvalid: this.$i18n.t('Session.ForgotPassword.OTEmailInvalid'),
          ResendRecoveryLink: this.$i18n.t(
            'Session.ForgotPassword.ResendRecoveryLink'
          )
        }
      };
    },
    methods: {
      async send(event) {
        event.srcElement.blur();
        let v = this.$validator;
        let validateFailed = !(await v.validate());

        let errorDom = this.$refs.errorDom;
        errorDom.open = validateFailed;
        if (validateFailed) {
          errorDom.errorText = this.trans.OTEmailInvalid;
          return;
        }

        try {
          await this.$store.dispatch(
            'emails/sendResetPasswordEmail',
            this.email
          );
          this.validateFailed = false;
        } catch (error) {
          errorDom.errorText = this.trans.OTEmailInvalid;
          errorDom.open = true;
        }
      }
    }
  };
</script>
