<template>
  <v-dialog
    v-model="lazyValue"
    persistent
    content-class="project-invite-teammates-dialog"
  >
    <v-tabs v-model="tabActive" color="tab-orange" v-if="lazyValue">
      <v-tabs-slider color="orange"></v-tabs-slider>
      <v-tab href="#tab-invite" ripple>
        <span class="flex h4">{{ trans.OTTitle }}</span>
      </v-tab>
      <v-tab href="#tab-teammates" ripple v-if="project.creator === user.id">
        <span class="flex h4">{{ trans.TeamMembers }}</span>
      </v-tab>
      <v-tabs-items>
        <v-tab-item value="tab-invite">
          <v-card>
            <v-container>
              <v-layout column>
                <v-flex>
                  <div class="invite-team-email-container">
                    <div class="team-email-text">
                      {{ trans.OTInviteAndPermissions }}
                    </div>
                    <div class="team-email-content">
                      <div class="content-item content-left">
                        <div class="email-text">{{ trans.FNEmail }}</div>
                        <div class="email-input">
                          <input
                            v-model="email"
                            type="text"
                            :placeholder="trans.OTEnterEmail"
                          />
                        </div>
                      </div>
                      <div class="content-item content-right">
                        <div class="email-text">{{ trans.FNPermission }}</div>
                        <div class="email-input">
                          <v-flex xs12 sm12 d-flex>
                            <v-select
                              v-model="permissionText"
                              :items="permissionItems"
                              class="permission-email"
                              label="Solo field"
                              solo
                            ></v-select>
                          </v-flex>
                        </div>
                      </div>
                    </div>
                    <div class="team-email-error" v-text="emailErrorText"></div>
                    <div class="team-email-fottor">
                      <v-btn flat color="#9FA2A8" @click.stop="close">{{
                        trans.OPCancel
                      }}</v-btn>
                      <v-btn
                        depressed
                        color="metaml-orange"
                        class="mr-0"
                        @click.stop="sendEmail"
                        >{{ trans.OPSendEmail }}</v-btn
                      >
                    </div>
                  </div>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card>
        </v-tab-item>
        <v-tab-item value="tab-teammates">
          <div
            class="teammates-list"
            style="height: 300px;background-color: #fff"
          >
            <ul>
              <li v-for="item in contributors" :key="item.id">
                <div class="list-avatar">
                  <user-avata-circle
                    :user-avata="item.avatar"
                    :user-name="item.username"
                  >
                  </user-avata-circle>
                </div>
                <div class="list-email">
                  <span>{{ item.email }}</span>
                </div>
                <div class="list-permission">
                  <div
                    style="padding-left: 13px;text-transform: capitalize;"
                    v-if="item.id === project.creator"
                  >
                    {{ trans.OTCreator }}
                  </div>
                  <teammates-list-select
                    v-else
                    :contributorsPermission="item"
                  ></teammates-list-select>
                </div>
                <div class="delete">
                  <v-btn
                    v-if="item.id !== project.creator"
                    icon
                    ripple
                    @click="contributorsDelete(item.id)"
                  >
                    <span class="mdi mdi-trash-can-outline"></span>
                  </v-btn>
                </div>
              </li>
            </ul>
          </div>
          <div class="team-email-fottor teammates-list-fish">
            <v-btn flat color="#9FA2A8" @click.stop="close">{{
              trans.OPCancel
            }}</v-btn>
            <v-btn
              depressed
              color="metaml-orange"
              class="mr-0"
              @click.stop="saveEdit"
              >{{ trans.OPSaveEdit }}</v-btn
            >
          </div>
        </v-tab-item>
      </v-tabs-items>
    </v-tabs>
  </v-dialog>
</template>

<script>
import { mapGetters } from 'vuex';
import userAvataCircle from '../../components/userAvataCircle';
import teammatesListSelect from '../../components/teammatesListSelect';
export default {
  name: 'InviteTeammates',
  created() {
    let v = this.$validator;

    v.attach({
      name: 'email',
      alias: this.trans.email,
      getter: () => this.email,
      rules: {
        required: true,
        email: true,
        regex: /^[^\u4e00-\u9fa5]+$/
      }
    });
  },
  components: {
    userAvataCircle,
    teammatesListSelect
  },
  props: {
    value: {
      type: Boolean,
      required: true
    }
  },
  computed: {
    ...mapGetters({
      project: 'projectDetail/project',
      user: 'session/user'
    }),
    contributors() {
      let contributorsList = _.cloneDeep(this.project.contributors);
      return _.orderBy(contributorsList, ['joinedAt']);
    },
    permissionType() {
      return this.permissionText === this.trans.OTCanEdit ? 'Update' : 'Read';
    }
  },
  data() {
    return {
      lazyValue: this.value,
      permissionText: this.$i18n.t(
        'ProjectManage.InviteTeammatesDialog.OTCanEdit'
      ),
      permissionItems: [
        this.$i18n.t('ProjectManage.InviteTeammatesDialog.OTCanEdit'),
        this.$i18n.t('ProjectManage.InviteTeammatesDialog.OTCanView')
      ],
      email: '',
      emailErrorText: '',
      trans: {
        OTTitle: this.$i18n.t('ProjectManage.InviteTeammatesDialog.OTTitle'),
        email: this.$i18n.t('email'),
        TeamMembers: this.$i18n.t(
          'ProjectManage.InviteTeammatesDialog.TeamMembers'
        ),
        OTInviteAndPermissions: this.$i18n.t(
          'ProjectManage.InviteTeammatesDialog.OTInviteAndPermissions'
        ),
        FNEmail: this.$i18n.t('ProjectManage.InviteTeammatesDialog.FNEmail'),
        OTEnterEmail: this.$i18n.t(
          'ProjectManage.InviteTeammatesDialog.OTEnterEmail'
        ),
        FNPermission: this.$i18n.t(
          'ProjectManage.InviteTeammatesDialog.FNPermission'
        ),
        OPCancel: this.$i18n.t('ProjectManage.InviteTeammatesDialog.OPCancel'),
        OPSendEmail: this.$i18n.t(
          'ProjectManage.InviteTeammatesDialog.OPSendEmail'
        ),
        OPSaveEdit: this.$i18n.t(
          'ProjectManage.InviteTeammatesDialog.OPSaveEdit'
        ),
        OTInviteSendEmailInfo: this.$i18n.t(
          'ProjectManage.InviteTeammatesDialog.OTInviteSendEmailInfo'
        ),
        OTAlreadyHaveEmail: this.$i18n.t(
          'ProjectManage.InviteTeammatesDialog.OTAlreadyHaveEmail'
        ),
        OTCanNoDeleteOneself: this.$i18n.t(
          'ProjectManage.InviteTeammatesDialog.OTCanNoDeleteOneself'
        ),
        OTCreator: this.$i18n.t(
          'ProjectManage.InviteTeammatesDialog.OTCreator'
        ),
        OTCanEdit: this.$i18n.t(
          'ProjectManage.InviteTeammatesDialog.OTCanEdit'
        ),
        OTCanView: this.$i18n.t('ProjectManage.InviteTeammatesDialog.OTCanView')
      },
      tabActive: 'tab-invite'
    };
  },
  watch: {
    value(val) {
      this.lazyValue = val;
    }
  },
  beforeCreate() {
    this.$_modelEvent =
      (this.$options.model && this.$options.model.event) || 'input';
  },
  methods: {
    close() {
      this.email = '';
      this.emailErrorText = '';
      this.lazyValue = false;
      this.$emit(this.$_modelEvent, this.lazyValue);
      this.$parent.teamIcoDele = false;
    },
    async sendEmail() {
      this.emailErrorText = '';
      let v = this.$validator;
      let validateFailed = !(await v.validate());
      let errors = await v.errors.all();
      if (validateFailed) {
        v.errors.items.forEach(e => {
          this.emailErrorText = e.field === 'email' ? e.msg : '';
        });
        return;
      }
      let emailArry = [];
      emailArry.push(this.email);
      let contributorsEmails = [];
      this.project.contributors.forEach(c => {
        contributorsEmails.push(c.email);
      });

      let inToProject =
        _.difference(emailArry, _.compact(contributorsEmails)).toString() ===
        emailArry.toString();

      if (inToProject) {
        let payload = {
          projectId: this.project.id,
          emails: emailArry,
          ability: this.permissionType
        };
        this.$store
          .dispatch('emails/sendForProjectInviting', payload)
          .then(res => {
            this.email = '';
            this.emailErrorText = '';
            this.close();
            this.$store.commit('settings/showGlobalSnackbar', {
              timeout: 5000,
              text: this.trans.OTInviteSendEmailInfo
            });
          });
      } else {
        this.emailErrorText = this.trans.OTAlreadyHaveEmail;
      }
    },
    contributorsDelete(id) {
      let self = this;
      if (this.user.id === id) {
        this.$store.commit('settings/showGlobalSnackbar', {
          timeout: 5000,
          text: this.trans.OTCanNoDeleteOneself
        });
        return;
      }

      let contributoraArr = this.contributors.filter(function(ele) {
        return ele.id !== id;
      });

      let payload = {
        projectId: this.$route.params.projectId,
        contributorList: contributoraArr,
        deleteContributorId: id
      };

      if (this.project.creator === id) {
        this.$store.commit('settings/showGlobalSnackbar', {
          timeout: 5000,
          text: this.$i18n.t('ProjectPage.OTCantDeleteCreator')
        });
      } else {
        this.$store.dispatch('projectDetail/removeOneContributor', payload);
      }
    },
    saveEdit() {
      let payload = {
        projectId: this.$route.params.projectId,
        contributorList: this.contributors
      };
      this.$store.dispatch('projectDetail/putContributorList', payload);
      this.close();
    }
  }
};
</script>

<style lang="scss" scoped>
.user-avata-circle-size {
  width: 2.25rem;
  height: 2.25rem;
}
</style>
